<template>
  <v-container>
    <v-dialog persistent v-model="dialog" width="500px" scrollable>
      <v-form v-model="isValid">
        <v-card class="rounded-card">
          <v-card-title>
            <v-spacer></v-spacer>
            {{ formTitle }}
            <v-spacer></v-spacer>
            <v-btn icon @click="close()">
              <v-icon> mdi-close </v-icon>
            </v-btn>
          </v-card-title>

          <v-divider></v-divider>

          <v-card-text cols="  text-center ">
            <div>
              <label></label>
              <h3 class="h3-text">Report Title<span class="red--text">*</span></h3>
              <p class="font-italic">Judul laporan</p>
              <v-text-field
                v-model="editedItem.title"
                :rules="[(v) => !!v || 'Required.']"
                outlined
                dense
              ></v-text-field>

              <h3 class="h3-text">Customer<span class="red--text">*</span></h3>
              <p class="font-italic">Pelanggan</p>
              <v-autocomplete
                v-model="editedItem.customer"
                :items="listbyidworkspace.Customer"
                :rules="[(v) => !!v || 'Required.']"
                item-text="name"
                item-value="id_customer"
                outlined
                dense
              ></v-autocomplete>

              <h3 class="h3-text">Period<span class="red--text">*</span></h3>
              <p class="font-italic">Periode</p>

              <v-select
                v-model="editedItem.period"
                :rules="[(v) => !!v || 'Required.']"
                :items="periodlist"
                item-text="name_period"
                item-value="id_period"
                outlined
                dense
              ></v-select>
              <label></label>
              <h3 class="h3-text">SubPeriod<span class="red--text">*</span></h3>
              <p class="font-italic">Subperiode</p>
              <v-select
                v-model="editedItem.subperiod"
                :items="subperiodlist"
                :rules="[(v) => !!v || 'Required.']"
                item-text="name_subperiod"
                item-value="id_subperiod"
                outlined
                dense
              ></v-select>

              <h3 class="h3-text">Type<span class="red--text">*</span></h3>
              <p class="font-italic">Tipe</p>
              <div v-if="!typereport">
                <v-select
                  outlined
                  :rules="[(v) => !!v || 'Required.']"
                  dense
                  :items="typeitem"
                  attach
                  v-model="editedItem.type"
                  @change="selectType()"
                  required
                >
                </v-select>
              </div>
              <div v-else>
                <v-row class="">
                  <v-col class="" cols="10">
                    <v-text-field v-model="editedItem.type" outlined dense>
                    </v-text-field>
                  </v-col>
                  <v-col md="2">
                    <v-btn @click="typereport = false" icon color="red"
                      ><v-icon>mdi-close-circle</v-icon></v-btn
                    >
                  </v-col>
                </v-row>
              </div>

              <label></label>
              <h3 class="h3-text">Descriptions</h3>
              <p class="font-italic">Deskripsi</p>

              <v-textarea v-model="editedItem.description" filled rounded>
              </v-textarea>

              <label>Attachment</label>
              <v-row>
                <v-col
                  v-for="image in editedItem.file_reporting"
                  :key="image.id"
                  class="d-flex child-flex"
                  cols="4"
                >
                  <v-img
                    :src="`/images/fileKeluhan/${image}`"
                    aspect-ratio="1"
                    class="grey lighten-2"
                  >
                    <template v-slot:placeholder>
                      <v-row
                        class="fill-height ma-0"
                        align="center"
                        justify="center"
                      >
                        <v-progress-circular
                          indeterminate
                          color="grey lighten-5"
                        ></v-progress-circular>
                      </v-row>
                    </template>
                  </v-img>
                </v-col>
              </v-row>
              <col />
              <div class="mt-6">
                <v-btn
                  color="primary"
                  class="text-none"
                  fab
                  x-small
                  depressed
                  :loading="isSelecting"
                  @click="onButtonClick"
                >
                  <v-icon center> mdi-plus </v-icon>
                  <!-- {{ buttonText }} -->
                </v-btn>
                <input
                  small-chips
                  ref="uploader"
                  class="d-none"
                  type="file"
                  accept="image/*"
                  @change="onFileChanged"
                />
                <v-spacer />

                <v-row class="mt-4">
                  <v-img
                    width="100"
                    height="100"
                    v-for="(images, i) in selectedImages"
                    :key="i"
                    :src="images"
                  >
                    <template v-slot:default>
                      <v-row class="mt-6" align="center" justify="center">
                        <v-chip
                          close
                          color="white"
                          outlined
                          @click:close="deleteimg(i)"
                        >
                          remove
                        </v-chip>
                      </v-row>
                    </template>
                  </v-img>
                </v-row>
              </div>
            </div>
          </v-card-text>
          <v-card-actions class="d-flex justify-center">
            <v-btn
              width="30%"
              ref="modal-save"
              id="modal-save"
              color="primary"
              rounded
              :loading="loading"
              :disabled="!isValid"
              @click="save"
            >
              Save
            </v-btn>
            <!-- <v-btn color="error" text @click="close"> Cancel </v-btn> -->
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>

    <sukses :alert="dialogsukses" :type="typealert" />
  </v-container>
</template>
<script>
import { mapActions, mapGetters } from "vuex";

import Sukses from "../alert/Sukses.vue";
export default {
  components: { Sukses },
  props: ["type", "propsreport"],
  data: () => ({
    loading: false,
    chip1: true,
    isValid: true,
    selectedFiles: [],
    selectedImages: [],
    typeitem: ["ANALYST", "PROJECT", "Others"],
    isSelecting: false,
    dialogsukses: false,
    typealert: "Congratulations",

    dialog: true,
    menu: false,
    modal: false,
    dialogDelete: false,
    typereport: false,
    editedItem: {
      keterangan: "",
      link: "",
      status: "",
      type: "",
      period: [],
      subperiod: [],
      customer: [],
      file_reporting: [],
      title: "",
      description: "",
    },
  }),

  mounted() {
    if (this.dialog == true) {
      const prm = {
        name: "",
        size: 1000,
        page: 1,
        id_workspace: localStorage.getItem("myworkspace"),
      };
      this.CustomersByidWorkspace(prm);
      this.fetchPeriod();
      this.fetchSubPeriod();

      if (this.type == "Edit Report") {
        this.selectedImages = [];
        this.reportingById({
          id: this.propsreport.id_reporting,
          token: localStorage.getItem("token-user"),
        }).then((response) => {
          this.editedItem.file_reporting = response.data.File_Reporting;
        });

        this.editedItem.keterangan = this.propsreport.keterangan;
        this.editedItem.description = this.propsreport.description;
        this.editedItem.period = this.propsreport.period.id_period;
        this.editedItem.customer = this.propsreport.customer.id_customer;
        this.editedItem.subperiod = this.propsreport.subPeriod.id_subperiod;
        this.editedItem.type = this.propsreport.type;
        this.editedItem.title = this.propsreport.title;
        this.editedItem.status = this.propsreport.status;
      } else {
        this.selectedImages = [];
        this.editedItem.file_reporting = [];
        this.editedItem.customer = "";
        this.editedItem.keterangan = "";
        this.editedItem.title = "";
        this.editedItem.period = "";
        this.editedItem.subperiod = "";
        this.editedItem.description = "";
        this.editedItem.type = "";
      }
    }
  },
  computed: {
    ...mapGetters("customer", ["listbyidworkspace"]),
    ...mapGetters("period", ["periodlist"]),
    ...mapGetters("subperiod", ["subperiodlist"]),
    formTitle() {
      return this.type === "Create Report" ? "Create Report" : "Edit Report";
    },
  },

  methods: {
    ...mapActions("reporting", [
      "addReport",
      "updateReporting",
      "reportingById",
      "fetchreporting",
    ]),
    ...mapActions("customer", ["CustomersByidWorkspace"]),
    ...mapActions("period", ["fetchPeriod"]),
    ...mapActions("subperiod", ["fetchSubPeriod"]),
    close() {
      this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("success", data);
    },
    onButtonClick() {
      this.isSelecting = true;
      window.addEventListener(
        "focus",
        () => {
          this.isSelecting = false;
        },
        { once: true }
      );

      this.$refs.uploader.click();
    },

    onFileChanged(e) {
      // this.selectedFile = e.target.files[0];
      this.selectedFiles.push(e.target.files);
      var files = e.target.files || e.dataTransfer.files;

      for (let index = 0; index < files.length; index++) {
        // const element = this.selectedFiles[index];

        this.selectedImages.push(URL.createObjectURL(files[index]));
      }
    },
    deleteimg(item) {
      this.selectedImages.splice(item, 1);
      this.selectedFiles.splice(item, 1);
    },

    selectType() {
      const type = this.editedItem.type;
      if (type == "Others") {
        this.typereport = true;
      }
    },

    async save() {
      this.loading = true;
      if (this.type == "Create Report") {
        //inisiasi formdata
        let formData = new FormData();

        //images looping
        // for (var i = 0; i < this.selectedFiles.length; i++) {
        // let file = this.selectedFiles;
        // formData.append(`files`, file);
        // }
        for (const i of Object.keys(this.selectedFiles)) {
          formData.append("files", this.selectedFiles[i][0]);
        }

        //keluhanpart diubah menjadi stringify
        let string = JSON.stringify({
          id_customer: { id_customer: this.editedItem.customer },
          id_period: { id_period: this.editedItem.period },
          id_subperiod: { id_subperiod: this.editedItem.subperiod },
          keterangan: this.editedItem.keterangan,
          type: this.editedItem.type,
          title: this.editedItem.title,
          description: this.editedItem.description,
        });
        ///setelah ituu di append
        formData.append("reportingPart", string);

        await this.addReport(formData)

          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
              this.loading = false;
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
        this.loading = false;
      } else {
        this.loading = true;
        //inisiasi formdata
        let formData = new FormData();
        for (const i of Object.keys(this.selectedFiles)) {
          formData.append("files", this.selectedFiles[i][0]);
        }
        //keluhanpart diubah menjadi stringify
        let string = JSON.stringify({
          id_customer: { id_customer: this.editedItem.customer },
          id_period: { id_period: this.editedItem.period },
          id_subperiod: { id_subperiod: this.editedItem.subperiod },
          keterangan: this.propsreport.keterangan,
          description: this.editedItem.description,
          status: this.propsreport.status,
          type: this.editedItem.type,
          title: this.editedItem.title,
        });

        formData.append("reportingPart", string);
        //mengambil id_keluhan
        let id = this.propsreport.id_reporting;
        formData.append("id_reporting", id);

        await this.updateReporting(formData)

          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
              this.loading = false;
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
        this.loading = false;
      }
    },
  },
};
</script>
<style  scoped>
.rounded-card {
  border-radius: 30px;
}
</style>


  