<template>
  <div>
    <v-dialog persistent v-model="dialog" max-width="500px">
      <v-card class="rounded-card">
        <v-card-title class="mx-6 toolbar-form">
          <v-spacer></v-spacer>
          {{ formTitle }}
          <v-spacer></v-spacer>
          <v-btn icon @click="close()">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-card-title>
        <!-- <v-toolbar flat class="mx-6 toolbar-form">
          
          <v-spacer></v-spacer>
          {{ formTitle }}
          <v-spacer></v-spacer>
          <v-btn icon @click="dialog = false">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-toolbar> -->
        <v-divider></v-divider>
        <v-card-text>
          <div>
            <h3 class="h3-text">
              Company / Institution<span class="red--text">*</span>
            </h3>
            <p class="font-italic">Perusahaan / Institusi</p>
            <v-autocomplete
              v-model="editedItem.id_institusi"
              :items="institusilist.Institution"
              item-text="name"
              outlined
              dense
              item-value="id_institusi"
            ></v-autocomplete>

            <h3 class="h3-text">Name</h3>
            <p class="font-italic">Name</p>
            <v-text-field
              v-model="editedItem.name"
              required
              dense
              outlined
            ></v-text-field>

            <h3 class="h3-text">Description</h3>
            <p class="font-italic">Keterangan</p>
            <v-textarea
              v-model="editedItem.keterangan"
              name="input-7-1"
              filled
              outlined
            ></v-textarea>
            <!-- <v-combobox :items="items" label="role" required>
                    </v-combobox> -->
          </div>
        </v-card-text>

        <v-card-actions class="justify-center">
          <v-btn
            rounded
            width="60%"
            ref="modal-save"
            id="modal-save"
            color="primary"
            @click="save"
            :loading="loading"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
  props: ["type", "propssubinstitusi"],
  data: () => ({
    dialogsukses: false,
    loading: false,
    typealert: "Congratulations",
    dialog: true,
    dialogDelete: false,
    // rules: [
    //   (value) => !!value || "Required.",
    //   (value) =>
    //     (value || "").length >= 8 ||
    //     "password should have at least 8 characters",
    // ],
    editedItem: {
      keterangan: "",
      name: "",
      id_institusi: "",
    },
  }),

  mounted() {
    const prm = {
      name: "",
      size: 1000,
      page: 1,
    };
    this.fetchInstitusi(prm);

    // this.editedItem = this.customer;
    if (this.type == "Edit Data") {
     
      this.editedItem.id_institusi = this.propssubinstitusi.institusi.id_institusi;
      this.editedItem.name = this.propssubinstitusi.name;
      this.editedItem.keterangan = this.propssubinstitusi.keterangan;
    } else {
      this.editedItem.id_institusi = "";
      this.editedItem.name = "";
      this.editedItem.keterangan = "";
    }
  },
  computed: {
    ...mapGetters("institusi", ["institusilist"]),

    formTitle() {
      return this.type === "Create Data" ? "Create Data" : "Edit Data";
    },
  },
  methods: {
    ...mapActions("subinstitusi", ["addSubInstitusi", "updateSubInstitusi"]),
    ...mapActions("institusi", ["fetchInstitusi"]),
    close() {
      this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("success", data);
    },

    async save() {
      this.loading = true;
      if (this.type == "Create Data") {
        await this.addSubInstitusi({
          id_institusi: {
            id_institusi: this.editedItem.id_institusi,
          },
          name: this.editedItem.name,
          keterangan: this.editedItem.keterangan,
        })
          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
              this.loading = false;
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
        this.loading = false;
      } else {
        this.loading = true;
        await this.updateSubInstitusi({
          id_institusi: {
            id_institusi: this.editedItem.id_institusi,
          },
          id_subinstitusi: this.propssubinstitusi.id_subinstitusi,
          name: this.editedItem.name,
          keterangan: this.editedItem.keterangan,
        })
          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
              this.loading = false;
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
        this.loading = false;
      }
    },
  },
};
</script>
<style scoped>
.rounded-card {
  border-radius: 30px;
}
</style>
