<template>
  <div :style="`width: 100vw; height: 100vh; background: url(${imglogo})`">
    <v-dialog
      v-model="dialog"
      scrollable
      content-class="style-dialog"
      persistent
    >
      <v-card class="rounded-card">
        <v-toolbar flat class="toolbar">
          <v-spacer></v-spacer>
          Report Confirmation
          <v-spacer></v-spacer>
          <!-- <v-btn icon @click="dialog = false">
              <v-icon> mdi-close </v-icon>
            </v-btn> -->
        </v-toolbar>
        <v-toolbar flat class="toolbar">
          <v-img
            src="@/static/images/iso-9001-01.svg"
             max-height="200"
                max-width="70"
          ></v-img>
          <v-spacer></v-spacer>
          <v-col
            v-model="logoperusahaan"
            class="pt-0 justify-start"
            cols="12"
            md="2"
          >
            <v-img
              :src="'/images/fileKeluhan/' + logoperusahaan"
              max-height="69"
              max-width="126"
            ></v-img>
          </v-col>
        </v-toolbar>
        <v-spacer />
        <v-divider></v-divider>

        <v-subheader> Detail report </v-subheader>

        <v-card-text>
          <v-spacer />
          <v-row class="d.flex">
            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1">Customer Name</h3>
              <p class="font-italic">Nama Pelanggan</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="font-weight-medium subtitle-1">
                {{ item.nama }}
              </h3>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1"> Title </h3>
              <p class="font-italic">Judul </p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="font-weight-medium subtitle-1">
                {{ item.title }}
              </h3>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1">Company / Institution</h3>
              <p class="font-italic">Perusahaan / Institusi</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="font-weight-medium subtitle-1">
                {{ item.perusahaan || "-"}}
              </h3>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1">Period</h3>
              <p class="font-italic">Periode</p>
            </v-col>
            <v-col cols="12" md="6" sm="12" class="justify-center">
              <h3 class="font-weight-medium subtitle-1">
                {{ item.period }}
              </h3>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1">SubPeriod</h3>
              <p class="font-italic">Subperiode</p>
            </v-col>
            <v-col cols="12" md="6" sm="12" class="justify-center">
              <h3 class="font-weight-medium subtitle-1">
                {{ item.subperiod }}
              </h3>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1">Descriptions</h3>
              <p class="font-italic">Deskripsi</p>
            </v-col>
            <v-col cols="12" md="6" sm="12" class="justify-center">
              <h3 class="font-weight-medium subtitle-1">
                {{ item.description }}
              </h3>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1">ATTACHMENT</h3>
              <p class="font-italic">Lampiran</p>
            </v-col>
            <v-col cols="12" md="6" sm="12" class="justify-center">
              <v-row>
                <v-col
                  v-for="image in item.file_customer"
                  :key="image.id"
                  class="d-flex child-flex"
                  cols="4"
                >
                  <v-img
                    :src="`/images/fileKeluhan/${image}`"
                    aspect-ratio="1"
                    class="grey lighten-2"
                  >
                   <v-row class="flex-column" justify="space-between">
                          <div class="align-self-center mt-6">
                            <v-btn
                              :class="{ 'show-btns': hover }"
                              :color="transparent"
                              icon
                              @click="ShowImage(item.file_customer)"
                            >
                              <v-icon :class="{ 'white--text': hover }">
                                mdi-image-search
                              </v-icon>
                            </v-btn>
                          </div>
                        </v-row>
                    <template v-slot:placeholder>
                      <v-row
                        class="fill-height ma-0"
                        align="center"
                        justify="center"
                      >
                        <v-progress-circular
                          indeterminate
                          color="grey lighten-5"
                        ></v-progress-circular>
                      </v-row>
                    </template>
                  </v-img>
                </v-col>
              </v-row>
              <col />
              <div class="mt-6">
                <v-btn
                  color="primary"
                  class="text-none"
                  fab
                  x-small
                  depressed
                  :loading="isSelecting"
                  @click="onButtonClick"
                >
                  <v-icon center> mdi-plus </v-icon>
                  <!-- {{ buttonText }} -->
                </v-btn>
                <input
                  small-chips
                  ref="uploader"
                  class="d-none"
                  type="file"
                  accept="image/*"
                  @change="onFileChanged"
                />
                <v-spacer />

                <v-row class="mt-4">
                  <v-img
                    width="100"
                    height="100"
                    v-for="(images, i) in selectedImages"
                    :key="i"
                    :src="images"
                  >
                    <template v-slot:default>
                      <v-row justify="center" align="center" class="mt-6">
                        <v-chip
                          close
                          color="white"
                          outlined
                          @click:close="deleteimg(i)"
                        >
                          remove
                        </v-chip>
                      </v-row>
                    </template>
                  </v-img>
                </v-row>
              </div>
            </v-col>
          </v-row>

          <v-row> </v-row>
        </v-card-text>

        <v-divider></v-divider>
        <v-card-actions class="justify-center">
          <v-btn
            @click="AcceptReporting(item)"
            depressed
            left
            color="success"
            dark
          >
            Accept</v-btn
          >
          <v-btn @click="decline(item)" depressed right color="error" dark
            >Decline</v-btn
          >
        </v-card-actions>
      </v-card>

      <sukses :alert="dialogsukses" :type="typealert" @close="actionalert" />

      <!-- <PerbaikanKeluhan v-if="showperbaikan" @close="actionperbaikan" /> -->
      <ket-reporting
        v-if="showperbaikan"
        @close="actionketerangan"
        @tutup="showperbaikan = false"
      />
      <rate-comment v-if="showrating" @close="actionrating" />
    </v-dialog>
         <DialogOpenImage
      :dialog="showImage"
      :data="dataImage"
      @close-dialog="showImage = false"
    />
  </div>
</template>


<script>
import Sukses from "../alert/Sukses.vue";
import logo from "@/assets/bg-1png-01.png";
// import PerbaikanKeluhan from "../popup/PerbaikanKeluhan.vue";
import KetReporting from "../popup/KetReporting.vue";
import DialogOpenImage from "../DialogOpenImage.vue";
import RateComment from "../popup/RateComment.vue";
import { mapActions } from "vuex";
export default {
  components: { RateComment, Sukses, KetReporting,DialogOpenImage },
  data() {
    return {
      logoperusahaan: "",
      imglogo: logo,
      selectedFiles: [],
      selectedImages: [],
      isSelecting: false,
      showrating: false,
       showImage: false,
      dataImage: [],
      showperbaikan: false,
      accept: false,
      // decline: false,
      dialog: false,
      dialogsukses: false,
      typealert: "Congratulations",

      dataprm: {
        link: "",
        // token: "",
      },
      item: {
        id: "",
        nama: "",
        namecus: "",
        date: "",
        keluhan: "",
        status: "",
        images: [],
        keterangan: "",

        file_customer: [],
        customer_name: "",
        perusahaan: "",
        period: "",
        subperiod: "",
        type: "",

        id_works: "",
      },
      keterangan: "",
    };
  },

  mounted() {
    this.dataprm = {
      link: this.$route.params.link,
    };
    // this.ReportingActive(this.dataprm);
    this.getReport(this.dataprm);

    // this.dialog = true;
  },
  created() {
    // this.dataprm = {
    //   id: this.$route.params.id,
    // };
  },

  methods: {
    ...mapActions("reporting", [
      "updateKonfirmasi",
      "KeluhanById",
      "updateStatusReporting",
      "ReportingActive",
    ]),
    onButtonClick() {
      this.isSelecting = true;
      window.addEventListener(
        "focus",
        () => {
          this.isSelecting = false;
        },
        { once: true }
      );

      this.$refs.uploader.click();
    },
    onFileChanged(e) {
      this.selectedFiles.push(e.target.files);
      var files = e.target.files || e.dataTransfer.files;

      for (let index = 0; index < files.length; index++) {
        // const element = this.selectedFiles[index];
        console.log("this.selectedFiles ", files[index]);
        this.selectedImages.push(URL.createObjectURL(files[index]));
      }
    },
    deleteimg(item) {
      this.selectedImages.splice(item, 1);
      this.selectedFiles.splice(item, 1);
      if (item == 0) {
        this.selectedFiles.push(item);
      }
    },
        ShowImage(e) {
      this.showImage = true;
      this.dataImage = e;
    },

    getReport(e) {
      this.ReportingActive({
        link: e.link,
      })
        .then((response) => {
          console.log("response reporting active", response);
          this.dialog = true;
          if (response.status == 200) {   
            if (response.data.status=="ACCEPTED") {
              this.$router.push("/Accepted");
            }else if(response.data.status=="expired"){
                this.$router.push("/*");
            }
            this.item = {
              id: response.data.Reporting.id_reporting,
              nama: response.data.Reporting.customer.name,
              perusahaan: response.data.Reporting.customer.perusahaan,
              period: response.data.Reporting.period.name_period,
              subperiod: response.data.Reporting.subPeriod.name_subperiod,
              description: response.data.Reporting.description,
              status: response.data.Reporting.status,
              file_customer: response.data.File_Reporting,
              title: response.data.Reporting.title,
              // id_works:
              //   response.data.Reporting.customer.id_workspace.id_workspace,
            };
            this.logoperusahaan = response.data.Logo;
          }
        })
        .catch((err) => {
          this.dialog = false;
        //   // console.log("masuk ke eror", err);
          // this.$router.push("/*");
        });
    },
    AcceptReporting(item) {
      if (item.status == "NEW") {
        console.log("Log", item.status);
        let formData = new FormData();
        console.log("file apped 1", this.selectedFiles);
        //images looping

        for (const i of Object.keys(this.selectedFiles)) {
          formData.append("files", this.selectedFiles[i][0]);
        }
        formData.append(`id_reporting`, this.item.id);
        formData.append(`status`, "ACCEPTED");
        formData.append(`keterangan`, this.item.keterangan);

        this.updateKonfirmasi({
          // token: this.dataprm.token,
          formData: formData,
        })
          // console.log("data", this.updateKonfirmasi());
          .then((response) => {
            console.log("response ubah", response.status);
            this.dialogsukses = true;
            if (response.status == 200) {
              this.dialog = false;
              let data = {
                type: "create",
                status: true,
              };
              this.dialogsukses = true;
              this.typealert = "Congratulations";
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.dialogsukses = true;
              this.typealert = "Failed";
              this.$emit("success", data);
            }
          })
          .catch((err) => {
            this.dialogsukses = true;
            this.typealert = "Failed";
          });
      } else if (item.status == "") {
        // let formData = new FormData();
        // console.log("file apped 1", this.selectedFiles);
        // for (const i of Object.keys(this.selectedFiles)) {
        //   formData.append("files", this.selectedFiles[i][0]);
        // }
        // formData.append(`id_keluhan`, this.dataprm.id);
        // formData.append(`status`, "CLOSED");
        // formData.append(`perbaikan`, this.item.perbaikan);
        // this.updateKonfirmasi({
        //   token: this.dataprm.token,
        //   formData: formData,
        // })
        // .then((response) => {
        //   if (response.status == 200) {
        //     this.dialog = false;
        //     let data = {
        //       type: "create",
        //       status: true,
        //     };
        //     this.dialogsukses = true;
        //     this.dialogsukses = true;
        //     this.$router.push("/confirm-page");
        //     console.log("sukses", data);
        //     // this.showrating = true;
        //   }
        // })
        // .catch((err) => {});
      }
    },

    decline() {
      this.showperbaikan = true;
    },
    actionalert(e) {
      this.dialogsukses = false;
      if (e == "Congratulations") {
        this.$router.push("/confirm-page");
      }
    },
    actionketerangan(data) {
      console.log("Log", data);
      let formData = new FormData();
      console.log("file apped 1", this.selectedFiles);
      //images looping

      for (const i of Object.keys(this.selectedFiles)) {
        formData.append("files", this.selectedFiles[i][0]);
      }
      formData.append(`id_reporting`, this.item.id);
      formData.append(`status`, "DECLINED");
      formData.append(`keterangan`, data.keterangan);
      if (data.type == "close") {
        this.showperbaikan = false;
      } else {
        this.showperbaikan = false;
        this.dialog = false;
        this.updateKonfirmasi({
          formData: formData,
        })
          .then((response) => {
            if (response.status == 200) {
              this.dialogsukses = true;
              this.$router.push("/confirm-page");
              console.log("sukses", data);
            }
          })
          .catch((err) => {});
      }

      // let formData = new FormData();
      // console.log("file apped 1", this.selectedFiles);
      // for (const i of Object.keys(this.selectedFiles)) {
      //   formData.append("files", this.selectedFiles[i][0]);
      // }
      // formData.append(`id_keluhan`, this.dataprm.id);
      // formData.append(`status`, "CLOSED");
      // formData.append(`perbaikan`, this.item.perbaikan);
      // this.updateKonfirmasi({
      //   token: this.dataprm.token,
      //   formData: formData,
      // })
      // .then((response) => {
      //   if (response.status == 200) {
      //     this.dialog = false;
      //     let data = {
      //       type: "create",
      //       status: true,
      //     };
      //     this.dialogsukses = true;
      //     this.dialogsukses = true;
      //     this.$router.push("/confirm-page");
      //     console.log("sukses", data);
      //     // this.showrating = true;
      //   }
      // })
      // .catch((err) => {});
    },
    actionrating(data) {},
  },
};
</script>
<style  scoped>
.rounded-card {
  border-radius: 30px;
}
/* .fill-height {
  background-image: url("/assets/bg-1png-01.png");
  color: white;
  font-size: 2rem;
  overflow: hidden;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
} */
</style>
