<template>
  <div>
    <v-row class="mx-5 mt-4">
      <!-- <v-breadcrumbs :items="items" divider=">"></v-breadcrumbs> -->
      <v-col cols="12">
        <div>
          <v-row class=" justify-end">
            <v-col cols="12" md="7">
              <h1 style="font-weight-regular">
                {{ titles }}
              </h1>

              <span>{{ kode_fr_reporting }}</span>
            </v-col>

            <v-col class="pt-0 d-flex justify-end" cols="4" md="3"> </v-col>
            <v-col
              v-model="logo"
              class="d-flex justify-space-around "
              cols="9"
              md="2"
              sm="5"
            >
              <v-img
                src="../static/images/iso-9001-01.svg"
                max-height="200"
                max-width="70"
                contain
              ></v-img>
              <v-img
                :src="'/images/fileKeluhan/' + logo"
                max-height="200"
                max-width="120"
                contain
              ></v-img>
            </v-col>
          </v-row>

          <v-row class="justify-end ">
            <v-col class="justify-end" cols="12" md="3">
              <v-text-field
                v-model="search"
                prepend-inner-icon="mdi-magnify"
                label="Search"
                filled
                rounded
                dense
                single-line
                hide-details
                v-on:change="changeType()"
                @click:prepend-inner="changeType()"
              ></v-text-field>
            </v-col>
             <v-col class="d-flex justify-end " cols="12" md="2">
              <v-select
                :items="reportingType"
                item-text="type"
                item-value="type"
                dense
                flat
                solo
                rounded
                filled
                clearable
                label=" Type"
                v-on:change="changeType()"
                v-model="typereport"
              ></v-select>
            </v-col>
            <v-col class="d-flex justify-end " cols="12" md="2">
              <v-select
                :items="stats_item"
                item-text="type"
                item-value="type"
                dense
                flat
                solo
                rounded
                filled
                clearable
                label=" Status"
                v-on:change="changeType()"
                v-model="stats"
              ></v-select>
            </v-col>
            <v-col cols="12" md="2" class=" d-flex justify-end">
              <v-btn
                rounded
                color="primary "
                @click="addreport()"
                depressed
                class="text-capitalize"
              >
                <!-- <v-icon color="white" left> mdi-account-plus </v-icon> -->
                Create Report</v-btn
              >
            </v-col>
          </v-row>
        </div>
      </v-col>

      <v-row>
        <v-col cols="12" class="mx-3 my-2">
          <v-card
            class="shadow rounded-card pr-3"
            :loading="loading"
           
          >
            <v-card-text>
              <v-data-table
                dense
                :headers="headers"
                :page.sync="page"
                :items-per-page="size"
                @page-count="pageCount = $event"
                hide-default-footer
                :items="bytypelist.Reporting"
                class="elevation-0"
                sort-by="customer.name"
              >
                <template v-slot:[`item.status`]="{ item }">
                  <span :style="`color:${getColor(item.status)}; `">
                    {{ item.status }}
                  </span>
                </template>
                <template v-slot:[`item.title`]="{ item }">
                  <div  class="text-truncate kolom"
                    :style="
                      $vuetify.theme.dark
                        ? 'cursor: pointer; color: #0072B6;  font-weight-medium'
                        : 'cursor: pointer; color: blue'
                    "
                    @click="ClickDetail(item)"
                    >{{ item.title }}</div
                  >
                </template>
                <template v-slot:[`item.description`]="{ item }">
                  <div class="text-truncate" style="max-width: 130px">
                    {{ item.description }}
                  </div>
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        :class="`my-2`"
                        depressed
                        fab
                        dark
                        x-small
                        class="mx-1"
                        color="cyan"
                        @click="log(item)"
                        v-bind="attrs"
                        v-on="on"
                      >
                        <v-icon x-small>mdi-file-document</v-icon>
                      </v-btn>
                    </template>
                    <span>Log Activity</span>
                  </v-tooltip>
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        v-bind="attrs"
                        v-on="on"
                        class="mx-1"
                        fab
                        dense
                        dark
                        x-small
                        color="primary"
                        @click="addreport(item)"
                      >
                        <v-icon x-small> mdi-pencil </v-icon>
                      </v-btn>
                    </template>
                    <span>Edit</span>
                  </v-tooltip>
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        fab
                        dark
                        x-small
                        color="red"
                        @click="deleteItem(item)"
                        v-bind="attrs"
                        v-on="on"
                      >
                        <v-icon x-small> mdi-delete </v-icon>
                      </v-btn>
                    </template>
                    <span>Remove </span>
                  </v-tooltip>
                </template>
              </v-data-table>
              <v-row class="justify-end">
                <v-col cols="12" md="4" class="justify-center">
                  <v-pagination
                    v-model="page"
                    class="my-4"
                    v-on:change="changeType()"
                    :length="totaldata"
                    :total-visible="7"
                  >
                  </v-pagination>
                </v-col>
                <v-col cols="12" md="1" class="select">
                  <v-select
                    label="Row per page"
                    :value="size"
                    type="number"
                    :items="row"
                    dense
                    outlined
                    v-on:change="changeType()"
                    @input="size = parseInt($event, 10)"
                  ></v-select>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-row>
    <!-- <form-keluhan
      :type="type"
      v-if="isShowModal"
      :item="keluhan"
      @success="actionreport"
    /> -->

    <form-report
      :type="type"
      v-if="isShowModal"
      :propsreport="reporting"
      @success="actionreport"
    />
    <confirm
      @actionconfirm="deletereport"
      :id_reporting="iddelete"
      v-if="confirmshow"
    />
    <sukses :alert="dialogsukses" :type="typealert" />
    <log-report
      v-if="detailog"
      :propslog="itemLog"
      :dialog="detailog"
      @close="detailog = false"
    />
    <detailreport
      v-if="detailshow"
      :propsdetail="rowClick"
      @close="ActionReporting"
    />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
// import TambahEdit from "../components/form/TambahEdit.vue";
// import LogReport from "../components/popup/LogReport.vue";
// import Detailkeluhan from "./Detailkeluhan.vue";
import Detailreport from "./Detailreporting.vue";
// import FormKeluhan from "../components/form/FormKeluhan.vue";
import FormReport from "../components/form/FormReport.vue";
import Sukses from "./alert/Sukses.vue";
import Confirm from "../components/popup/Confirm.vue";
// import Detailreporting from "./Detailreporting.vue";
import LogReport from "../components/popup/LogReport.vue";
// import TambahEdit from "../components/form/TambahEdit.vue";
export default {
  components: {
    Confirm,
    Sukses,

    FormReport,
    Detailreport,
    LogReport,
  },
  data: () => ({
    stats_item :["ACCEPTED", "DECLINED", "NEW"],
    stats:"all",
    loading: true,
    showlink: false,
    Detailreportingge: 1,
    transparent: "",
    search: "",
    detailshow: false,
    detailog: false,
    dialogsukses: false,
    typealert: "Congratulations",
    isShowModal: false,
    confirmshow: false,
    content: false,
    dialogGen: false,
    card1: 0,
    titles: "Reporting",
    iddelete: "",
    logo: "",
    row: [5, 10, 15, 20, 25],
    page: 1,
    size: 25,
    pageCount: 0,
    totaldata: 0,
    workspace: "",
    items: ["customer"],
    headers: [
      { text: "TITLE", value: "title", sortable: false },
      { text: "CUSTOMER", value: "customer.name", sortable: false },
      // { text: "workspace", value: "customer.id_workspace.id_workspace" },
      // {
      //   text: "name",
      //   value: "name",
      // },
      // { text: "password", value: "password" },

      { text: "PERIOD", value: "period.name_period", sortable: false },
      { text: "SUBPERIOD", value: "subPeriod.name_subperiod", sortable: false },
      { text: "TYPE", value: "type", sortable: false },
      { text: "DESCRIPTION", value: "description", sortable: false },
      { text: "STATUS", value: "status", sortable: false },
      {
        text: "ACTION",
        value: "actions",
        width: "30%",
        fixed: true,
        sortable: false,
      },
    ],
    items: [
      // {
      //   text: "Reporting",
      //   disabled: false,
      //   href: "breadcrumbs_dashboard",
      // },
      // {
      //   text: "Link 1",
      //   disabled: false,
      //   href: "breadcrumbs_link_1",
      // },
    ],
    type: "",
    typereport: "all",
    reporting: {},
    itemLog: null,
    kode_fr_reporting: "",
  }),
  // mounted() {
  //   this.typereport = "PROJECT";
  // },
  computed: {
    ...mapGetters("reporting", ["typereportlist", "bytypelist", "yearlist"]),
    ...mapState("reporting", ["allbytype", "reportingType"]),

    ...mapGetters("survey", ["yearlist"]),

    formTitle() {
      return this.type === "Create Report" ? "Create Report" : "Edit Report";
    },
    // pageCounts() {
    //   return Math.ceil(this.totaldata / this.size);
    // },
  },
  watch: {
    page() {
      this.onfilter();
    },
    search() {
      this.onfilter();
    },
  },

  async created() {
    // await this.fetchreporting();
    await this.onfilter();
    // await this.reportingByType("all");

    await this.fetchType();
    await this.fetchYear();
    this.workspace = localStorage.getItem("myworkspace");
    this.kode_fr_reporting = localStorage.getItem("kode_fr_reporting");
  },
  methods: {
    ...mapActions("reporting", [
      "deleteReportingID",
      "reportingById",
      "reportingByType",
      "fetchreporting",
      "fetchType",
      "fetchYear",
    ]),
    ...mapActions("survey", ["fetchYear"]),
    changeType() {
      this.page = 1;
      this.onfilter();
    },
    async onfilter() {
      this.loading = true;
      if (this.typereport == null) {
        this.typereport = "all";
      }
      if (this.stats == null) {
        this.stats = "all";
      }
      const prm = {
        status:this.stats,
        name: this.search,
        size: this.size,
        page: this.page,
        type: this.typereport,
        id_workspace: localStorage.getItem("myworkspace"),
      };
      await this.reportingByType(prm);
      this.logo = this.allbytype.Logo;
      this.totaldata = this.allbytype.TotalPages;

      // this.logo = this.allbytype[0].workspace.file_workspace;
      this.loading = false;
    },

    ClickDetail(item) {
      this.detailshow = true;
      this.rowClick = item;
    },
    // rowClickss(item, row) {
    //   this.detailshow = true;
    //   this.rowClick = item;
    // },
    getColor(status) {
      if (status == "NEW") return "Primary";
      else if (status == "ACCEPT") return "green";
      else if (status == "DECLINE") return "RED";
    },

    log(item) {
      console.log(item, "item");
      this.itemLog = item;
      this.detailog = true;
    },

    addreport(item) {
      if (item != undefined) {
        this.reporting = item;
        this.type = "Edit Report";
        this.isShowModal = !this.isShowModal;
      } else {
        this.isShowModal = !this.isShowModal;
        this.type = "Create Report";
      }
    },
    deleteItem(item) {
      this.iddelete = item.id_reporting;
      this.confirmshow = true;
    },
    deletereport(data) {
      if (data.id_reporting !== "") {
        this.deleteReportingID(data.id_reporting)
          .then((response) => {
            if (response.status == 200) {
              this.$emit("success");
              this.typealert = "Congratulations";
              this.dialogsukses = true;
              this.confirmshow = false;
              this.onfilter();
            } else {
              this.typealert = "Failed !";
              this.dialogsukses = true;
              this.confirmshow = false;
            }
          })
          .catch((err) => {
            this.typealert = "Failed !";
            this.dialogsukses = true;
          });
      } else {
        this.confirmshow = false;
      }
    },
    actionreport(data) {
      this.isShowModal = false;

      if (data.type == "create") {
        if (data.status == true) {
          this.dialogsukses = true;
          this.typealert = "Congratulations";
          this.onfilter();
        } else {
          this.dialogsukses = true;
          this.typealert = "Failed !";
        }
      } else {
        setTimeout(() => {
          this.onfilter();
        }, 1000);
      }
    },

    ActionReporting(data) {
      // alert("aaaa");
      this.detailshow = false;
      this.rowClick = [];
      if (data.type == "save") {
        if (data.status == true) {
          this.typealert = "Congratulations";
          this.dialogsukses = true;
          this.onfilter();
        } else {
          this.typealert = "Failed !";
          this.dialogsukses = true;
        }
      }
    },
  },
};
</script>
<style>
/* .rounded-card {
  border-radius: 16px !important;
} */
.select {
  padding-top: 30px;
}
.kolom{
  max-width: 130px;
}
</style>
