<template>
  <div>
    <v-dialog
      v-model="dialog"
      persistent
      content-class="detail-dialog"
      scrollable
    >
      <v-card class="rounded-card">
        <v-card-title>
          <v-spacer></v-spacer>
          Detail Report
          <v-spacer></v-spacer>
          <v-btn icon @click="close()">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-card-title>

        <v-spacer />
        <v-divider></v-divider>
        <!-- </v-row> -->
        <v-subheader class="font-weight-bold">DETAIL</v-subheader>

        <v-card-text>
          <div>
            <v-row>
              <v-col cols="12" md="4" sm="12">
                <h3 class="h3-text">Customer Name</h3>
                <p class="font-italic">Nama Pelanggan</p>
              </v-col>
              <v-col cols="12" md="8" sm="12">
                <h3 class="h3-props">
                  {{ propsdetail.customer.name }}
                </h3>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4" sm="12">
                <h3 class="h3-text">Title</h3>
                <p class="font-italic">Judul</p>
              </v-col>
              <v-col cols="12" md="8" sm="12">
                <h3 class="h3-props">
                  {{ propsdetail.title }}
                </h3>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4" sm="12">
                <h3 class="h3-text">Type</h3>
                <p class="font-italic">Tipe</p>
              </v-col>
              <v-col cols="12" md="8" sm="12">
                <h3 class="h3-props">
                  {{ propsdetail.type }}
                </h3>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4" sm="12">
                <h3 class="h3-text">Period</h3>
                <p class="font-italic">Periode</p>
              </v-col>
              <v-col cols="12" md="8" sm="12">
                <h3 class="h3-props">{{ propsdetail.period.name_period }}</h3>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4" sm="12">
                <h3 class="h3-text">Subperiod</h3>
                <p class="font-italic">Subperiode</p>
              </v-col>
              <v-col cols="12" md="8" sm="12">
                <h3 class="h3-props">
                  {{ propsdetail.subPeriod.name_subperiod }}
                </h3>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4" sm="12">
                <h3 class="h3-text">Description</h3>
                <p class="font-italic">Deskripsi</p>
              </v-col>
              <v-col cols="12" md="8" sm="12">
                <h3 class="h3-props">
                  {{ propsdetail.description }}
                </h3>
              </v-col>
            </v-row>
            <v-row v-if="propsdetail.status == 'DECLINED'">
              <v-col cols="12" md="4" sm="12">
                <h3 class="h3-text">Description Declined</h3>
                <p class="font-italic">Keterangan Penolakan</p>
              </v-col>
              <v-col cols="12" md="8" sm="12">
                <h3 class="h3-props">
                  {{ propsdetail.keterangan }}
                </h3>
              </v-col>
            </v-row>

            <v-row>
              <v-col cols="12" md="4" sm="12" class="">
                <h3 class="h3-text">Status</h3>
                <p class="font-italic">Status</p>
              </v-col>
              <v-col cols="12" md="5" sm="12">
                <h3 class="h3-text">
                  <v-select
                    dense
                    class=""
                    v-model="editedItem.status"
                    :items="statusitem"
                    item-text="state"
                    item-value="abbr"
                    persistent-hint
                    return-object
                    outlined
                    rounded
                  ></v-select>
                </h3>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4" sm="12" >
                <h3 class="h3-text">ATTACHMENT</h3>
                <p class="font-italic">Lampiran</p>
              </v-col>

              <v-row  v-if="editedItem.file_report.length > 0"  class="mx-4">
                <v-col
                  cols="3"
                  
                  v-for="(image, i) in editedItem.file_report"
                  :key="i"
                  :style="i < 2 ? '' : 'display:none !important'"
                >
                  <v-hover v-slot="{ hover }">
                    <v-card :elevation="hover ? 12 : 2">
                      <v-img
                        :src="'/images/fileKeluhan/' + image"
                        max-height="200"
                        max-width="200"
                      >
                        <v-row class="flex-column" justify="space-between">
                          <div class="align-self-center mt-6">
                            <v-btn
                              :class="{ 'show-btns': hover }"
                              :color="transparent"
                              icon
                              @click="ShowImage(editedItem.file_report)"
                            >
                              <v-icon :class="{ 'white--text': hover }">
                                mdi-image-search
                              </v-icon>
                            </v-btn>
                          </div>
                        </v-row>
                      </v-img>
                    </v-card>
                  </v-hover>
                </v-col>
                <v-col
                  v-if="editedItem.file_report.length > 2"
                >
                  <v-card max-width="140" elevation="1">
                    <v-img
                      :src="'/na-image/' + editedItem.file_report[2]" 
                      contain    
                      
                    >
                      <v-row class="justify-center">
                        <div class="align-center pt-5 pb-12">
                          <v-btn
                            class="show-img white--text"
                            :color="transparent1"
                            small
                            @click="ShowImage(editedItem.file_report)"
                          >
                            <span>+</span
                            >{{ editedItem.file_report.length - 2 }}
                          </v-btn>
                        </div>
                      </v-row>
                    </v-img>
                  </v-card>
                </v-col>
              </v-row>
            </v-row>
          </div>

          <get-link
            v-if="showlink"
            :propslink="editedItem"
            @close="dialogGen = false"
            :dialog="dialogGen"
          />
        </v-card-text>
        <v-divider></v-divider>
        <div class="pt-4"></div>
        <v-card-actions class="justify-center">
          <v-btn
            ref="modal-save"
            id="modal-save"
            color="primary"
            rounded
            outlined
            @click="generate"
          >
            Generate Link
          </v-btn>

          <v-btn
            class="btn-update"
            ref="modal-save"
            id="modal-save"
            color="primary"
            depressed
            rounded
            @click="ubahstatus"
          >
            Update
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <DialogOpenImage
      :dialog="showImage"
      :data="dataImage"
      @close-dialog="showImage = false"
    />
  </div>
</template>
                
<script>
import { mapActions, mapGetters } from "vuex";
import GetLink from "./popup/GetLink.vue";
import DialogOpenImage from "./DialogOpenImage.vue";

import colors from "vuetify/lib/util/colors";
export default {
  props: ["propsdetail"],
  components: { GetLink, DialogOpenImage },
  data() {
    return {
      transparent:"",
      transparent1:colors.blue.darken2,
      fullWidthImage: true,
      showImage: false,
      dataImage: [],
      showlink: false,
      selectedFiles: [],
      selectedImages: [],
      isSelecting: false,
      statusitem: ["ACCEPTED", "DECLINED", "NEW"],
      marker: true,
      dialog: true,
      resolve: null,
      showlink: true,
      reject: null,
      link: "",
      data: "",
      date: "",
      editedItem: {
        // title: "",
        // status: "",
        // link: "",
        // perbaikan: "",
        // file_report: "",
        // download: "",

        keterangan: "",
        link: "",
        status: "",
        type: "",
        period: [],
        subperiod: [],
        customer: [],
        file_report: "",
        description: "",
      },
      dialogGen: false,
    };
  },

  mounted() {
    console.log( this.propsdetail);
    this.editedItem.customer = this.propsdetail.customer.name;
    this.editedItem.period = this.propsdetail.period.name_period;
    this.editedItem.status = this.propsdetail.status;
    this.editedItem.description = this.propsdetail.description;
  },

  computed: {
    // ...mapGetters("keluhan", ["keluhanlist"]),

    buttonText() {
      return this.selectedFile
        ? this.selectedFile.name
        : this.defaultButtonText;
    },
  },
  async created() {
    // alert("ff");
    // await this.fetchKeluhan();
    await this.reportingById({
      id: this.propsdetail.id_reporting,
      token: localStorage.getItem("token-user"),
    }).then((response) => {
      this.editedItem.file_report = response.data.File_Reporting;
    });
  },
  methods: {
    ...mapActions("reporting", [
      "fetchreporting",
      "reportingById",
      "updateStatusReporting",
      "Generated",
    ]),
    close() {
      // alert("close");
      this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("close", data);
    },

    onButtonClick() {
      this.isSelecting = true;
      window.addEventListener(
        "focus",
        () => {
          this.isSelecting = false;
        },
        { once: true }
      );

      this.$refs.uploader.click();
    },

    onFileChanged(e) {
      // this.selectedFile = e.target.files[0];
      this.selectedFiles = e.target.files;

      for (let index = 0; index < this.selectedFiles.length; index++) {
        const element = this.selectedFiles[index];
        this.selectedImages.push(URL.createObjectURL(element));
      }
    },

    ubahstatus() {
      //inisiasi formdata
      var formData = new FormData();
      formData.append(`id_reporting`, this.propsdetail.id_reporting);
      formData.append(`status`, this.editedItem.status);
      formData.append(`keterangan`, this.editedItem.keterangan);
      formData.append(`files`, this.selectedImages);

      this.updateStatusReporting({
        formData: formData,
      })

        .then((response) => {
          if (response.status == 200) {
            let data = {
              type: "save",
              status: true,
            };
            this.fetchreporting();
            this.$emit("close", data);
          } else {
            let data = {
              type: "save",
              status: false,
            };
            this.$emit("close", data);
          }
        })
        .catch((err) => {
          let data = {
            type: "save",
            status: false,
          };
          this.$emit("close", data);
        });
    },

    async generate() {
      let res = await this.Generated({
        idreporting: this.propsdetail.id_reporting,
      });
      // .then((response) => {

      //   // this.$emit("linkgen", this.editedItem);
      // })
      // .catch((err) => {});

      // this.editedItem.link = `https://ebcrm.blackeye.id/#/konfirmasi/${res.data.Reporting.link}`;
      this.editedItem.link = `https://ebcrm.blackeye.id/#/confirm-report/${res.data.Reporting.link}`;
      this.showlink = true;
      this.dialogGen = true;

      // this.$emit("close", false);
    },
    getColor(propsdetail) {
      if (propsdetail.status == "Done") return "green";
      else if (propsdetail.status == "progress") return "orange";
      else return "red";
    },
    ShowImage(e) {
      this.showImage = true;
      this.dataImage = e;
    },
  },
};
</script>
<style  scoped>
.rounded-card {
  border-radius: 30px;
}
.select {
  background: orange;
}
.full {
  width: 100%;
  height: auto;
}
img {
  width: 150px;
  border-radius: 2px;
  box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.5);
  transition: width 1s;
}
img:hover {
  cursor: pointer;
}
</style>