<script>
import OpsiworkSpace from "@/components/popup/OpsiWorkspace.vue";
// import confirmlogout from "@/components/popup/ConfirmLogout.vue";
import ConfirmLogout from "../popup/ConfirmLogout.vue";
export default {
  components: { OpsiworkSpace, ConfirmLogout },
  data: () => ({
    // drawer: false,
    drawer: null,

    dialogWorkspaces: false,

    confim: false,
    group: null,
    search: "",
    role: "",
    selectedItem: 0,
  }),

  computed: {
    getdata() {
      const data = localStorage.getItem("role");
      return data;
    },
  },
  watch:{
    selectedItem(val){
      console.log(val,"val");
    }
  },
  mounted() {
    this.setDefault();
    const theme = localStorage.getItem("dark_theme");
    if (theme) {
      if (theme === "true") {
        this.$vuetify.theme.dark = true;
      } else {
        this.$vuetify.theme.dark = false;
      }
    } else if (
      window.matchMedia &&
      window.matchMedia("(prefers-color-scheme: dark)").matches
    ) {
      this.$vuetify.theme.dark = true;
      localStorage.setItem("dark_theme", this.$vuetify.theme.dark.toString());
    }
  },
  methods: {
    logout() {
      this.confim = true;
     
    },
    setDefault() {
      const route = this.$route.name;

      switch (route) {
        case "Dashboard":
          this.selectedItem = 0;
          break;
        case "Keluhan":
          this.selectedItem = 1;
          break;
        case "Workorder":
          this.selectedItem = 2;
          break;
        case "Customer":
          this.selectedItem = 3;
          break;
        case "Survey":
          this.selectedItem = 4;
          break;
        case "Reporting":
          this.selectedItem = 5;
          break;
        case "Workspace":
          this.selectedItem = 6;
          break;
        case "UserManagement":
          this.selectedItem = 8;
          break;
        case "Institusi":
          this.selectedItem = 9;
          break;
        case "SubInstitusi":
          this.selectedItem = 10;
          break;

        default:
          break;
      }
    },
    myworkspace() {
      this.dialogWorkspaces = true;
    },
    close() {
      this.dialogWorkspaces = false;
    },
    toggle_dark_mode: function() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      localStorage.setItem("dark_theme", this.$vuetify.theme.dark.toString());
    },
  },
};
</script>
