<template>
  <v-dialog v-model="dialog" persistent width="500">
    <v-card class="rounded-card">
      <v-toolbar flat class="toolbar">
        <v-spacer></v-spacer>
        Decline Report
        <v-spacer></v-spacer>
        <v-btn icon @click="cls()">
          <v-icon> mdi-close </v-icon>
        </v-btn>
      </v-toolbar>
      <v-spacer />
      <v-card-text>
        <v-row>
          <v-col cols="12 text-center" md="12" sm="12">
            <span class="text-center">
              Please explain the problem related problem
            </span></v-col
          >
          <v-textarea
            rounded
            v-model="editedItem.keterangan"
            filled
            name="input-7-4"
          ></v-textarea>
        </v-row>
        <v-divider />
        <v-card-actions class="justify-center">
          <!-- <v-btn @click="cancel()" depressed right color="error" dark
                      >Cancel<v-icon> mdi-close</v-icon></v-btn
                    > -->
          <v-btn text @click="send" depressed left color="#BF0F3E" dark>
            Submit</v-btn
          >
        </v-card-actions>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>


<script>
export default {
  data() {
    return {
      dialog: true,

      editedItem: {
        keterangan: "",
      },
    };
  },
  methods: {
    send() {
      const data = {
        type: "decline",
        keterangan: this.editedItem.keterangan,
      };
      this.$emit("close", data);
    },
    cls() {
      // alert("close");
      this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("tutup", data);
    },
    cancel() {
      const data = {
        type: "close",
      };
      this.$emit("close", data);
    },
  },
};
</script>


<style  scoped>
.rounded-card {
  border-radius: 40px;
}
</style>