import axios from "axios";

const state = {
    keluhan: [],
    respon: {},
    keluhanlistbyidkeluhan: []

}
const getters = {
    keluhanlist: state => state.keluhan,
    keluhanbyidworkspace: state => state.keluhanlistbyidkeluhan
}
const actions = {
    async fetchKeluhan({ commit }) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get("/login-crm/keluhan", {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("setKeluhan", response.data)
    },
    async getKeluhanById({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        console.log(token, "tokenuser");
        const myworkspace = localStorage.getItem("myworkspace");
        const response = await axios.get(
          `/login-crm/KeluhanByIdWorkspace?id_workspace=${data.id_workspace}&name=${data.name}&page=${data.page}&size=${data.size}`,
          {
            headers: {
              Authorization: "Bearer " + token,
              //the token is a variable which holds the token
            },
          }
        );
        commit("keluhanlistbyidcomplaint", response.data);
        console.log("get keluhan by ID dri tabel keluhan", token);
    },
    async KeluhanById({ commit }, data) {
        const token = sessionStorage.getItem('token-user')

        const response = await axios.get(`/login-crm/keluhanById/${data.id}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        })
        return response;
    },
    async KeluhanActive({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = axios.get(`/login-crm//keluhanActive/${data.link}`, {
            // headers: {

            //     Authorization: 'Bearer ' + token 
            // }
        })
        return response
            // commit("updateStatus", id_keluhan, perbaikan, status);
    },
    async addKeluhan({ commit }, keluhan) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.post("/login-crm/addKeluhan", keluhan, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            },

        });

        commit("addNewKeluhan", response)
        return response
    },
    async Generated({ commit }, data) {
        const token = sessionStorage.getItem('token-user');
        // const response = await axios.post(`/api-crm/generate?id_customer=${data.idcus}&id_keluhan=${data.idkeluhan}&id_user=${data.iduser}`, data, {
        //     headers: {
        //         Authorization: 'Bearer ' + token //the token is a variable which holds the token
        //     }
        // });
        // return response
        return new Promise((resolve, reject) => {
            axios.post(`/login-crm/generate?id_customer=${data.idcus}&id_keluhan=${data.idkeluhan}&id_user=${data.iduser}`, data, {
                headers: {
                    Authorization: 'Bearer ' + token //the token is a variable which holds the token
                }
            }).then((res) => {
                resolve(res)
            });
        })
    },

    async deleteKeluhanID({ commit }, id_keluhan) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.delete(`/login-crm/deleteKeluhan/${id_keluhan}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        })
        commit("removeKeluhan", id_keluhan)
        return response
    },
    async updateKeluhan({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.put(`/login-crm/updateKeluhan`, data, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token

            }
        });
        return response;
    },
    async updateStatus({}, data) {
        const token = sessionStorage.getItem('token-user')
            // return axios.put(`/api-crm/updateStatus/${data.id_keluhan}?status=${data.status}&perbaikan=${data.perbaikan}`, data.formData, {
        return axios.put(`/login-crm/updateStatus`, data.formData, {

            headers: {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        })
    },

    async updateKonfirmasi({}, data) {
        const token = sessionStorage.getItem('token-user');
        return axios.put(`/login-crm/updateStatus`, data.formData, {
                headers: {
                    'Content-Type': 'application/json',
                    //the token is a variable which holds the token
                }
            })
            // commit("updateStatus", id_keluhan, perbaikan, status);
    },

};

const mutations = {
    setKeluhan: (state, keluhan) => (state.keluhan = keluhan),
    // workslistbyidworkspace: (state, workslistbyidworkspace) => (state.listbyidworkspace = workslistbyidworkspace),
    keluhanlistbyidcomplaint: (state, keluhanlistbyidkeluhan) => (state.keluhanlistbyidkeluhan = keluhanlistbyidkeluhan),
    addNewKeluhan: (state, keluhan) => state.respon = keluhan,
    removeKeluhan: (state, id_keluhan) => {
        state.keluhan.filter(keluhan => keluhan.id_keluhan == id_keluhan),
            state.keluhan.splice(keluhan => keluhan.id_keluhan, 1)

    },
    updateKeluhan: (state, keluhan) => (state.keluhan = (keluhan => keluhan)),
    updateStatus: (state, keluhan) => (state.keluhan = (keluhan => keluhan))

}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}