<template>
  <div>
    <v-row class="justify-center text-center">
      <v-col cols="7">
        <v-dialog v-model="dialog" persistent max-width="500" scrollable>
          <v-card class="rounded-card">
            <v-card-title class="toolbar-form">
              <v-spacer></v-spacer>
              History
              <v-spacer></v-spacer>

              <v-btn icon @click="closet">
                <v-icon> mdi-close </v-icon>
              </v-btn>
            </v-card-title>

            <!-- <v-toolbar flat dense shaped class="h6 font-weight-black">
              <v-spacer></v-spacer>
              History
              <v-spacer></v-spacer>
              <v-btn icon @click="closet">
                <v-icon> mdi-close </v-icon>
              </v-btn> -->
            <!-- </v-toolbar> -->
            <v-divider></v-divider>
            <v-card-text>
              <v-row class="my-12">
                <v-col>
                  <div class="font-weight-bold ml-8 mb-2">
                    <span>Customer Name :</span> {{ propslog.customer.name }}
                  </div>
                  <v-timeline align-top dense>
                    <v-timeline-item
                      v-for="message in messages"
                      :key="message.data"
                      icon-color="grey lighten-2"
                      small
                    >
                      <v-row>
                        <v-col>
                          <div class="font-weight-normal">Date</div>
                          <h3 class="subtitle-1 pb-2">
                            <strong>{{ message.date }}</strong>
                          </h3>

                          <div
                            class="font-weight-normal"
                            v-if="message.action != 'null '"
                          >
                            Activity
                          </div>
                          <h3 class="subtitle-1 pb-2">
                            <strong>{{ message.action }}</strong>
                          </h3>

                          <div
                            class="font-weight-normal"
                            v-if="message.status_reporting != null"
                          >
                            Latest Status
                          </div>
                          <h3 class="subtitle-1 pb-2">
                            <strong>{{ message.status_reporting }}</strong>
                          </h3>
                          <v-row v-if="message.file_customer.length > 0">
                            <v-col
                              cols="4"
                              v-for="(image, i) in message.file_customer"
                              :key="i"
                              :style="i < 2 ? '' : 'display:none !important'"
                            >
                              <v-hover v-slot="{ hover }">
                                <v-card :elevation="hover ? 12 : 2">
                                  <v-img
                                    :src="'/images/fileKeluhan/' + image"
                                    height="50"
                                    contain
                                  >
                                    <v-row
                                      class="flex-column"
                                      justify="space-between"
                                    >
                                      <div class="align-self-center mt-4">
                                        <v-btn
                                          :class="{ 'show-btns': hover }"
                                          :color="transparent"
                                          icon
                                          @click="
                                            ShowImage(message.file_customer)
                                          "
                                        >
                                          <v-icon
                                            :class="{ 'white--text': hover }"
                                          >
                                            mdi-image-search
                                          </v-icon>
                                        </v-btn>
                                      </div>
                                    </v-row>
                                  </v-img>
                                </v-card>
                              </v-hover>
                            </v-col>

                            <v-col
                              cols="3"
                              class="pl-0 pr-1 col col-2"
                              v-if="message.file_customer.length > 2"
                            >
                              <v-card>
                                <v-img
                                  contain
                                  height="50"
                                  :src="'/na-image/' + message.file_customer[2]"
                                >
                                  <v-row
                                    class="flex-column"
                                    justify="space-between"
                                  >
                                    <div class="align-self-center mt-6">
                                      <v-btn
                                        depressed
                                        class="show-img red--text"
                                        :color="transparent"
                                        small
                                        @click="
                                          ShowImage(message.file_customer)
                                        "
                                      >
                                        <span>+</span
                                        >{{ message.file_customer.length - 2 }}
                                      </v-btn>
                                    </div>
                                  </v-row>
                                </v-img>
                              </v-card>
                            </v-col>
                          </v-row>
                          <!-- <v-col>
                            <v-img
                              v-if="message.file_customer != ''"
                              max-height="96"
                              max-width="79"
                              :src="`/images/fileKeluhan/${message.file_customer}`"
                              aspect-ratio="1"
                              class="grey lighten-2"
                            >
                              <template v-slot:placeholder>
                                <v-row
                                  class="fill-height ma-0"
                                  align="center"
                                  justify="center"
                                >
                                  <v-progress-circular
                                    v-if="message.file_customer != ''"
                                  ></v-progress-circular>
                                </v-row>
                              </template>
                            </v-img>
                          </v-col> -->
                        </v-col>
                      </v-row>
                    </v-timeline-item>
                  </v-timeline>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-col>
    </v-row>
    <DialogOpenImage
      :dialog="showImage"
      :data="dataImage"
      @close-dialog="showImage = false"
    />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import DialogOpenImage from "../DialogOpenImage.vue";
export default {
  props: ["propslog", "dialog"],
  components: { DialogOpenImage },
  data: () => ({
    messages: [],
    customer_name: "",
    images: [],
    showImage: false,
    dataImage: [],
    transparent: "",
  }),

  mounted() {
    console.log("propsdetail detail report", this.propslog);
    this.customer_name = this.propslog.customer_name;
  },

  async created() {
    // alert("ff");
    // await this.fetchKeluhan();
    await this.logByIdReport({
      id: this.propslog.id_reporting,
      token: localStorage.getItem("token-user"),
    }).then((response) => {
      console.log("reportingbyid", response.data);
      this.messages = response.data;
      this.images = response.data.file_customer;
    });
  },
  methods: {
    ...mapActions("reporting", ["logByIdReport"]),
    closet() {
      this.$emit("close", false);
    },
    ShowImage(e) {
      this.showImage = true;
      this.dataImage = e;
    },
  },
};
</script>

<style scoped>
.rounded-card {
  border-radius: 30px;
}

.v-progress-circular {
  margin: 1rem;
}
</style>
