<template>
  <div>
    <v-row class="mx-5 mt-4">
      <!-- <v-breadcrumbs :items="items" divider=">"></v-breadcrumbs> -->
      <v-col cols="12">
        <div>
          <v-row class=" justify-end">
            <v-col cols="12" md="7">
              <h1 style="font-weight-regular" class="Montserrat">
                {{ titles }}
              </h1>
              <span>{{ kode_fr_workorder }}</span>
            </v-col>

            <v-col class="pt-0 d-flex justify-end " cols="4" md="3"> </v-col>
            <v-col
              v-model="logo"
              class="d-flex justify-space-around "
              cols="9"
              md="2"
              sm="5"
            >
              <v-img
                src="../static/images/iso-9001-01.svg"
                max-height="200"
                max-width="70"
                contain
              ></v-img>
              <v-img
                :src="'/images/fileKeluhan/' + logo"
                max-height="200"
                max-width="120"
                contain
              ></v-img>
            </v-col>
          </v-row>

          <v-row class="justify-end ">
            <v-col cols="12" md="2" class="pe-4 justify-end ">
              <v-text-field
                v-model="search"
                prepend-inner-icon="mdi-magnify"
                label="Category"
                filled
                rounded
                v-on:change="changeType()"
                @click:prepend-inner="changeType()"
                dense
                single-line
                hide-details
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="2">
              <v-select
                :items="keluhanlistbyidkeluhan.Keluhan"
                dense
                flat
                item-text="title"
                item-value="id_keluhan"
                solo
                rounded
                filled
                clearable
                label=" Complaint"
                v-on:change="changeType()"
                v-model="filterbyidkeluhan"
              >
              </v-select>
            </v-col>

            <v-col cols="12" md="2" class=" d-flex justify-end">
              <v-btn
                rounded
                color="primary "
                @click="addworkorder()"
                class="text-capitalize"
                depressed
              >
                <!-- <v-icon color="white" left> </v-icon> -->
                Create Workorder</v-btn
              >
            </v-col>
          </v-row>
        </div>
      </v-col>

      <v-row>
        <v-col cols="12" class="mx-3 my-2">
          <v-card class="shadow rounded-card pr-1" :loading="loading">
            <v-card-text>
              <v-data-table
                :headers="headers"
                :items="bytypeidkeluhan.Workorder"
                hide-default-footer
                :page.sync="page"
                :items-per-page="size"
                @page-count="pageCount = $event"
                sort-by="kategori"
                class="elevation-0"
              >
                <template v-slot:[`item.status`]="{ item }">
                  <span :style="`color:${getColor(item.status)}`">
                    {{ item.status }}
                  </span>
                </template>
                <template v-slot:[`item.kategori`]="{ item }">
                  <a
                    :style="
                      $vuetify.theme.dark
                        ? 'cursor: pointer; color: #0072B6'
                        : 'cursor: pointer; color: blue'
                    "
                    class="Subtitle 2 font-weight-medium"
                    @click="ClickDetail(item)"
                    >{{ item.kategori }}</a
                  >
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        fab
                        dark
                        v-bind="attrs"
                        v-on="on"
                        class="mx-1 my-2"
                        x-small
                        color="primary"
                        @click="addworkorder(item)"
                      >
                        <v-icon x-small> mdi-pencil </v-icon>
                      </v-btn>
                    </template>
                    <span>Edit</span>
                  </v-tooltip>
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        fab
                        dark
                        x-small
                        color="red"
                        @click="deleteItem(item)"
                        v-bind="attrs"
                        v-on="on"
                      >
                        <v-icon x-small> mdi-delete </v-icon>
                      </v-btn>
                    </template>
                    <span>Remove </span>
                  </v-tooltip>
                </template>
              </v-data-table>
              <v-row class="justify-end">
                <v-col cols="12" md="4" class="justify-center">
                  <v-pagination
                    v-model="page"
                    class="my-4"
                    v-on:change="changeType()"
                    :length="totaldata"
                    :total-visible="13"
                  >
                  </v-pagination>
                </v-col>
                <v-col cols="12" md="1" class="select">
                  <v-select
                    label="Row per page"
                    :value="size"
                    type="number"
                    :items="row"
                    dense
                    outlined
                    v-on:change="changeType()"
                    @input="size = parseInt($event, 10)"
                  ></v-select>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-row>
    <form-wkorder
      :propsworkorder="workorder"
      :type="type"
      v-if="isShowModal"
      @success="actionfrom"
    />

    <confirm
      @actionconfirm="deletework"
      :id_workorder="iddelete"
      v-if="confirmshow"
    />
    <sukses :alert="dialogsukses" :type="typealert" />
    <Konfirmasiworkorder
      v-if="detailshow"
      :propsWorkorder="rowClick"
      @close="actionWorkorder"
    />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
import Sukses from "../components/alert/Sukses.vue";
import FormWkorder from "../components/form/FormWkorder.vue";
import Confirm from "../components/popup/Confirm.vue";
import Konfirmasiworkorder from "../components/form/Konfirmasiworkorder.vue";

export default {
  components: { Confirm, FormWkorder, Sukses, Konfirmasiworkorder },
  data: () => ({
    search: "",
    detailshow: false,
    dialogsukses: false,
    typealert: "Congratulations",
    isShowModal: false,
    confirmshow: false,
    content: false,
    card1: 0,
    loading: true,
    titles: "Workorder",
    workspace: "",
    iddelete: "",
    logo: "",
    row: [5, 10, 15, 20, 25],
    page: 1,
    size: 5,
    pageCount: 0,
    totaldata: 0,
    kode_fr_workorder: "",
    items: ["workorder"],

    headers: [
      { text: "CATEGORY", value: "kategori" },

      // { text: "POSITION", value: "posisi" },

      { text: "COMPLAINT TITLE ", value: "keluhan.title" },
      { text: "DESCRIPTION", value: "deskripsi" },
      // { text: "CREATED DATE", value: "date" },
      { text: "FINISH DATE", value: "batas_waktu" },
      // { text: "keterangan", value: "keterangan" },
      { text: "STATUS", value: "status" },
      { text: "ACTION", value: "actions", sortable: false, width: "20%" },
    ],
    items: [
      {
        text: "Workorder",
        disabled: false,
        href: "breadcrumbs_dashboard",
      },
      {
        // text: "L",
        disabled: false,
        href: "breadcrumbs_link_1",
      },
    ],
    type: "",
    filterbyidkeluhan: "all",
    workorder: {},
  }),
  mounted() {},
  computed: {
    ...mapState("keluhan", ["keluhanlistbyidkeluhan"]),
    ...mapGetters("workorder", ["workorderlist", "bytypeidkeluhan"]),
    ...mapState("workorder", ["byid_keluhan"]),

    // formTitle() {
    //   return this.type === "Create Work Order"
    //     ? "Create Work Order"
    //     : "Edit  Work Order";
    // },
    // pageCounts() {
    //   return Math.ceil(this.totaldata / this.size);
    // },
  },
  watch: {
    page() {
      this.onfilter();
    },
    search() {
      this.onfilter();
    },
  },

  async created() {
    await this.onfilter();
    await this.fetchWorkorder();

    const prm1 = {
      name: "",
      size: 1000,
      page: 1,
      id_workspace: localStorage.getItem("myworkspace"),
    };
    await this.getKeluhanById(prm1);

    this.loading = false;

    // await this.fetchKeluhan();
    this.workspace = localStorage.getItem("myworkspace");
    this.kode_fr_workorder = localStorage.getItem("kode_fr_workorder");
    // this.listworkspace = this.workspacelist;
  },

  methods: {
    ...mapActions("workorder", [
      "fetchWorkorder",
      "deleteWorkorder",
      "WorderByIdKeluhan",
    ]),
    ...mapActions("keluhan", ["getKeluhanById"]),
    changeType() {
      this.page = 1;
      // this.reportingByType(this.)
      this.onfilter();
    },
    async onfilter() {
      if (this.filterbyidkeluhan == null) {
        this.filterbyidkeluhan = "all";
      }
      const prm = {
        category: this.search,
        size: this.size,
        page: this.page,
        id_keluhan: this.filterbyidkeluhan,
        id_workspace: localStorage.getItem("myworkspace"),
      };

      await this.WorderByIdKeluhan(prm);
      this.logo = this.byid_keluhan.Logo;
      this.totaldata = this.byid_keluhan.TotalPages;
    },

    ClickDetail(item) {
      this.detailshow = true;
      this.rowClick = item;
    },

    // rowClickss(item, row) {

    //   this.detailshow = true;
    //   this.rowClick = item;
    // },
    getColor(status) {
      if (status == "In PROGRESS") return "orange";
      else if (status == "DONE") return "green ";
      else if (status == "created") return "purple ";
      else return "primary";
    },
    addworkorder(item) {
      if (item != undefined) {
        this.workorder = item;
        this.type = "Edit Work Order";
        this.isShowModal = !this.isShowModal;
      } else {
        this.isShowModal = !this.isShowModal;
        this.type = "Create Work Order";
      }
    },
    deleteItem(item) {
      this.iddelete = item.id_workorder;

      this.confirmshow = true;
    },
    deletework(data) {
      if (data.id_workorder !== "") {
        this.loading = true;
        this.deleteWorkorder(data.id_workorder)
          .then((response) => {
            if (response.status == 200) {
              this.$emit("success");
              this.typealert = "Congratulations";
              this.dialogsukses = true;
              this.confirmshow = false;
              this.onfilter();
              this.loading = false;
            } else {
              this.typealert = "Failed !";
              this.dialogsukses = true;
              this.confirmshow = false;
            }
          })
          .catch((err) => {
            this.typealert = "Failed !";
            this.dialogsukses = true;
          });
      } else {
        this.confirmshow = false;
        this.loading = false;
      }
    },
    async actionfrom(data) {
      this.isShowModal = false;
      this.loading = true;
      if (data.type == "create") {
        if (data.status == true) {
          this.dialogsukses = true;
          this.typealert = "Congratulations";
          await this.onfilter();
          this.loading = false;
        } else {
          this.dialogsukses = true;
          this.typealert = "Failed !";
        }
      } else {
        this.loading = false;
        setTimeout(() => {
          this.onfilter();
        }, 1000);
      }
    },
    async actionWorkorder(data) {
      this.detailshow = false;
      this.rowClick = [];
      if (data.type == "save") {
        if (data.status == true) {
          this.dialogsukses = true;
          this.typealert = "Congratulations";
          this.onfilter();
        } else {
          this.dialogsukses = true;
          this.typealert = "Failed !";
        }
      }
    },
  },
};
</script>
<style scoped>
.v-progress-circular {
  margin: 1rem;
}
.select {
  padding-top: 30px;
}
.rounded-card {
  border-radius: 16px !important;
}
.select {
  padding-top: 30px;
}
</style>
