<template>
  <div>
    <v-alert
      v-model="warning"
      text
      prominent
      type="error"
      icon="mdi-alert-circle"
      ><v-row align="center">
        <v-col class="grow">
          {{ text_alert }}
        </v-col>
        <v-col class="shrink">
          <v-btn icon color="red" @click="dismis()">
            <v-icon>mdi-close-circle</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-alert>
    <v-row class="ml-4 my-4 pr-10">
      <v-col cols="12">
        <v-breadcrumbs
          style="padding-left: unset;margin-left: 10px;}"
          divider=">"
          :items="items"
        ></v-breadcrumbs>
        <div>
          <v-row class="align-bottom">
            <v-col cols="12">
              <h1 style="font-weight-medium">
                {{ titles }}
              </h1>
            </v-col>
          </v-row>
          <v-row class="justify-end">
            <v-col class="justify-end" cols="12" md="3">
              <v-text-field
                v-model="search"
                prepend-inner-icon="mdi-magnify"
                label="Search here"
                filled
                rounded
                dense
                single-line
                hide-details
                v-on:change="changeType()"
                @click:prepend-inner="changeType()"
              ></v-text-field>
            </v-col>

            <v-col cols="12" md="2">
              <v-btn rounded color="primary " @click="addworkspace()" depressed class="text-capitalize">
                <!-- <v-icon color="white" left> mdi-account-plus </v-icon> -->
                Create Workspace</v-btn
              >
            </v-col>
          </v-row>
        </div>
      </v-col>

      <v-row>
        <v-col cols="12" class="mx-3 my-2">
          <v-card class="shadow rounded-card" :loading="loading">
            <v-card-text>
              <v-data-table
                style="cursor: pointer"
                :headers="headers"
                :items="workspacelist.Workspace"
                hide-default-footer
                :page.sync="page"
                :items-per-page="size"
                @page-count="pageCount = $event"
                class="elevation-0"
              >
                <template v-slot:[`item.actions`]="{ item }">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        fab
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        dark
                        class="mx-1 my-2"
                        color="primary"
                        @click="addworkspace(item)"
                      >
                        <v-icon x-small> mdi-pencil </v-icon>
                      </v-btn>
                    </template>
                    <span>Edit</span>
                  </v-tooltip>
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        fab
                        x-small
                        dark
                        color="red"
                        @click="deleteItem(item)"
                        v-bind="attrs"
                        v-on="on"
                      >
                        <v-icon x-small> mdi-delete </v-icon>
                      </v-btn>
                    </template>
                    <span>Remove </span>
                  </v-tooltip>
                </template>
              </v-data-table>

              <v-row class="justify-end">
                <v-col cols="12" md="4" class="justify-center">
                  <v-pagination
                    v-model="page"
                    class="my-4"
                    v-on:change="changeType()"
                    :length="totaldata"
                  >
                  </v-pagination>
                </v-col>
                <v-col cols="12" md="1" class="select">
                  <v-select
                    label="Row per page"
                    :value="size"
                    type="number"
                    :items="row"
                    dense
                    outlined
                    v-on:change="changeType()"
                    @input="size = parseInt($event, 10)"
                  ></v-select>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-row>
    <form-workspace
      @success="actionWorkspace"
      :propsworkspace="workspace"
      :type="type"
      v-if="isShowModal"
    />
    <confirm
      @actionconfirm="deleteworkspace"
      :id_workspace="iddelete"
      v-if="confirmshow"
    />
    <sukses
      :alert="dialogsukses"
      :type="typealert"
      @close="dialogsukses = false"
    />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
import Sukses from "../components/alert/Sukses.vue";
import FormWorkspace from "../components/form/FormWorkspace.vue";
// import TambahEdit from "../components/form/TambahEdit.vue";
// import FormWorkspace from "../components/form/FormWorkspace.vue";
import Confirm from "../components/popup/Confirm.vue";
// import TambahEdit from "../components/form/TambahEdit.vue";
export default {
  components: { Confirm, FormWorkspace, Sukses },
  data: () => ({
    text_alert: "",
    warning: false,
    search: "",
    row: [5, 10, 15, 20, 25],
    page: 1,
    size: 5,
    pageCount: 0,
    totaldata: 0,
    dialogsukses: false,
    typealert: "Congratulations",
    isShowModal: false,
    confirmshow: false,
    content: false,
    card1: 0,
    loading: true,
    titles: "Workspace",
    iddelete: "",
    listworkspace: [],

    items: ["workspace"],
    headers: [
      // { text: "id_workspace", value: "id_workspace" },
      { text: "NAME", value: "name" },
      { text: "DESCRIPTION", value: "keterangan" },
      { text: "ACTION", value: "actions", sortable: false },
    ],
    items: [
      // {
      //   text: "Workspace",
      //   disabled: false,
      //   href: "breadcrumbs_dashboard",
      // },
      // {
      //   text: "Link 1",
      //   disabled: false,
      //   href: "breadcrumbs_link_1",
      // },
    ],
    type: "",
    workspace: {},
  }),
  computed: {
    ...mapGetters("workspace", ["workspacelist"]),
    formTitle() {
      return this.type === "Create Workspace"
        ? "Create Workspace"
        : "Edit Workspace";
    },
  },
  watch: {
    page() {
      this.dataworks();
    },
    search() {
      this.dataworks();
    },
  },
  async created() {
    await this.dataworks();
    this.loading = false;
    // this.listworkspace = this.workspacelist;
  },

  methods: {
    ...mapActions("workspace", ["fetchWorkspace", "deleteWorkspace"]),

    addworkspace(item) {
      if (item != undefined) {
        this.workspace = item;
        this.type = "Edit Workspace";
        this.isShowModal = !this.isShowModal;
      } else {
        this.isShowModal = !this.isShowModal;
        this.type = "Create Workspace";
      }
    },
    dismis() {
      this.warning = false;
    },
    async dataworks() {
      this.loading = true;
      const prm = {
        name: this.search,
        size: this.size,
        page: this.page,
      };
      await this.fetchWorkspace(prm);

      this.totaldata = this.workspacelist.TotalPages;
      this.loading = false;
    },
    changeType() {
      this.page = 1;
      this.dataworks();
    },
    deleteItem(item) {
      this.iddelete = item.id_workspace;

      this.confirmshow = true;
    },
    deleteworkspace(data) {
      if (data.id_workspace !== "") {
        this.loading = true;
        this.deleteWorkspace(data.id_workspace)
          .then((response) => {
            if (response.status == 200) {
              this.$emit("success");
              this.typealert = "Congratulations";
              this.dialogsukses = true;
              this.confirmshow = false;
              this.dataworks();
              this.loading = false;
            } else {
              this.typealert = "Failed !";
              this.dialogsukses = true;
              this.confirmshow = false;
              this.loading = false;
            }
          })
          .catch((err) => {
            this.text_alert = err.response.data;
            this.warning = true;
            this.loading = false;

            // this.typealert = "Failed !";
            // this.dialogsukses = true;
          });
        this.confirmshow = false;
      } else {
        this.confirmshow = false;
        this.loading = false;
      }
    },
    async actionWorkspace(data) {
      this.isShowModal = false;
      this.loading = true;

      if (data.type == "create") {
        if (data.status == true) {
          this.dialogsukses = true;
          this.typealert = "Congratulations";
          this.dataworks();
          this.loading = false;
        } else {
          this.dialogsukses = true;
          this.typealert = "Failed !";
          this.loading = false;
        }
      } else {
        this.loading = false;
        setTimeout(() => {
          this.dataworks();
        }, 1000);
      }
    },
  },
};
</script>
<style scoped>
.select {
  padding-top: 30px;
}
</style>
