<template>
  <div>
    <v-dialog
      persistent
      v-model="dialog"
      max-width="500px"
      scrollable
      content-class="form-dialog"
    >
      <v-form v-model="isValid">
        <v-card class="rounded-card">
          <v-card-title class="mx-6 toolbar-form">
            <v-spacer></v-spacer>
            {{ formTitle }}
            <v-spacer></v-spacer>
            <v-btn icon @click="close()">
              <v-icon> mdi-close </v-icon>
            </v-btn>
          </v-card-title>
          <!-- <v-toolbar flat class="mx-6 toolbar-form">
            <v-spacer></v-spacer>
            {{ formTitle }}
            <v-spacer></v-spacer>
            <v-btn icon @click="dialog = false">
              <v-icon> mdi-close </v-icon>
            </v-btn>
          </v-toolbar> -->
          <v-divider></v-divider>
          <v-card-text>
            <div>
              <h3 class="h3-text">Name<span class="red--text">*</span></h3>
              <p class="font-italic">Name</p>
              <v-text-field
                v-model="editedItem.name"
                required
                :rules="[(v) => !!v || 'Required.']"
                outlined
                dense
              ></v-text-field>

              <h3 class="h3-text">Code Form Complaint & Valid Date<span class="red--text">*</span></h3>
              <p class="font-italic">Kode Form Keluhan & Tanggal berlaku</p>
              <v-text-field
                v-model="editedItem.kode_fr_keluhan"
                required
                outlined
                :rules="[(v) => !!v || 'Required.']"
                dense
              ></v-text-field>

              <h3 class="h3-text">Code Form Workorder & Valid Date<span class="red--text">*</span></h3>
              <p class="font-italic">Kode Form Workorder & Tanggal berlaku</p>
              <v-text-field
                v-model="editedItem.kode_fr_workorder"
                required
                outlined
                :rules="[(v) => !!v || 'Required.']"
                dense
              ></v-text-field>
              <h3 class="h3-text">Code Form Customer & Valid Date<span class="red--text">*</span></h3>
              <p class="font-italic">Kode Form Pelanggan & Tanggal berlaku</p>
              <v-text-field
                v-model="editedItem.kode_fr_customer"
                required
                outlined
                :rules="[(v) => !!v || 'Required.']"
                dense
              ></v-text-field>
              <h3 class="h3-text">Code Form Reporting & Valid Date<span class="red--text">*</span></h3>
              <p class="font-italic">Kode Form Laporan & Tanggal berlaku</p>
              <v-text-field
                v-model="editedItem.kode_fr_reporting"
                required
                outlined
                :rules="[(v) => !!v || 'Required.']"
                dense
              ></v-text-field>
              <h3 class="h3-text">Code Form Survey & Valid Date<span class="red--text">*</span></h3>
              <p class="font-italic">Kode Form Survey & Tanggal berlaku</p>
              <v-text-field
                v-model="editedItem.kode_fr_survey"
                required
                :rules="[(v) => !!v || 'Required.']"
                outlined
                dense
              ></v-text-field>

              <h3 class="h3-text">Description</h3>
              <p class="font-italic">Keterangan</p>
              <v-textarea
                v-model="editedItem.keterangan"
                name="input-7-1"
                filled
                outlined
                dense
              ></v-textarea>

              <h3 class="h3-text">Company Logo</h3>
              <p class="font-italic">Logo Perusahaan</p>

              <v-row>
                <v-col
                  v-if="editedItem.file"
                  v-model="editedItem.file"
                  class="d-flex child-flex"
                  cols="4"
                >
                  <v-img
                    :src="`/images/fileKeluhan/${editedItem.file}`"
                    class="grey lighten-2"
                  >
                    <template v-slot:default>
                      <v-row class="mt-6" justify="center">
                        <v-chip
                          close
                          color="black"
                          outlined
                          @click:close="deleteimg()"
                        >
                          remove
                        </v-chip>
                      </v-row>
                    </template>
                  </v-img>
                </v-col>
              </v-row>
              <v-col cols="12" md="6" sm="12">
                <v-btn
                  color="primary"
                  class="text-none"
                  fab
                  :rules="[(v) => !!v || 'Required.']"
                  x-small
                  depressed
                  :loading="isSelecting"
                  @click="onButtonClick"
                >
                  <v-icon center> mdi-plus </v-icon>
                </v-btn>
                <input
                  multiple
                  small-chips
                  ref="uploader"
                  class="d-none"
                  type="file"
                  accept="image/*"
                  @change="onFileChanged"
                />
                <v-spacer />
              </v-col>
              <v-col cols="6" md="2" sm="12">
                <v-row>
                  <v-img
                    width="100"
                    height="100"
                    v-for="(images, i) in selectedImages"
                    :key="i"
                    :src="images"
                  >
                    <template v-slot:default>
                      <v-row class="mt-6" justify="center">
                        <v-chip
                          close
                          color="black"
                          outlined
                          @click:close="deleteimg(i)"
                        >
                          remove
                        </v-chip>
                      </v-row>
                    </template>
                  </v-img>
                </v-row>
              </v-col>
            </div>
          </v-card-text>

          <v-card-actions class="d-flex justify-center">
            <v-btn
              rounded
              width="40%"
              ref="modal-save"
              id="modal-save"
              color="primary"
              @click="save"
              :disabled="!isValid"
              :loading="loading"
            >
              Save
            </v-btn>
            <!-- <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn> -->
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>
    <sukses :alert="dialogsukses" :type="typealert" />
  </div>
</template>
<script>
import { mapActions, mapGetters, mapState } from "vuex";
import Sukses from "../alert/Sukses.vue";
export default {
  components: { Sukses },
  props: ["type", "propsworkspace"],
  data: () => ({
    selectedFiles: [],
    loading: false,
    selectedImages: [],
    isSelecting: false,
    dialogsukses: false,
    typealert: "Congratulations",
    dialog: true,
    dialogDelete: false,
    isValid: true,
    id_user: "",
    editedItem: {
      file: "",
      keterangan: "",
      name: "",
      kode_fr_keluhan: "",
      kode_fr_workorder: "",
      kode_fr_customer: "",
      kode_fr_reporting: "",
      kode_fr_survey: "",
    },
  }),

  mounted() {
    // this.fetchuser();

    // this.editedItem = this.customer;
    if (this.type == "Edit Workspace") {
      this.editedItem.file = this.propsworkspace.file_workspace;
      this.editedItem.name = this.propsworkspace.name;
      this.editedItem.kode_fr_customer = this.propsworkspace.kode_fr_customer;
      this.editedItem.keterangan = this.propsworkspace.keterangan;
      this.editedItem.kode_fr_keluhan = this.propsworkspace.kode_fr_keluhan;
      this.editedItem.kode_fr_customer = this.propsworkspace.kode_fr_customer;
      this.editedItem.kode_fr_reporting = this.propsworkspace.kode_fr_reporting;
      this.editedItem.kode_fr_survey = this.propsworkspace.kode_fr_survey;

      this.editedItem.kode_fr_workorder = this.propsworkspace.kode_fr_workorder;
    } else {
      //   this.id_workspace = "";
      this.editedItem.name = "";
      this.editedItem.keterangan = "";
      this.editedItem.kode_fr_keluhan = "";
      this.editedItem.kode_fr_workorder = "";
      this.editedItem.kode_fr_survey = "";
      this.editedItem.kode_fr_reporting = "";
      this.editedItem.kode_fr_customer = "";
    }
  },
  computed: {
    ...mapGetters("users", ["userslist"]),

    ...mapGetters("workspace", ["workspacelist"]),
    formTitle() {
      return this.type === "Create Workspace"
        ? "Create Workspace"
        : "Edit Workspace";
    },
  },
  methods: {
    ...mapActions("users", ["fetchuser"]),
    ...mapActions("workspace", [
      "addWorkspace",
      "updateWorkspace",
      "fetchWorkspace",
    ]),
    close() {
      this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("success", data);
    },
    onButtonClick() {
      this.isSelecting = true;
      window.addEventListener(
        "focus",
        () => {
          this.isSelecting = false;
        },
        { once: true }
      );

      this.$refs.uploader.click();
    },
    onFileChanged(e) {
      this.selectedFiles.push(e.target.files);
      var files = e.target.files || e.dataTransfer.files;

      for (let index = 0; index < files.length; index++) {
        // const element = this.selectedFiles[index];

        this.selectedImages.push(URL.createObjectURL(files[index]));
      }
    },
    deleteimg(item) {
      this.selectedImages.splice(item, 1);
      this.selectedFiles.splice(item, 1);
      if (item == 0) {
        this.selectedFiles.push(item);
      }
    },
    deleteimg() {
      this.editedItem.file = "";
    },

    async save() {
      this.loading = true;
      if (this.type == "Create Workspace") {
        let formData = new FormData();

        for (const i of Object.keys(this.selectedFiles)) {
          formData.append("file", this.selectedFiles[i][0]);
        }
        let string = JSON.stringify({
          file_workspace: "",

          name: this.editedItem.name,
          kode_fr_keluhan: this.editedItem.kode_fr_keluhan,
          kode_fr_workorder: this.editedItem.kode_fr_workorder,
          keterangan: this.editedItem.keterangan,
          kode_fr_survey: this.editedItem.kode_fr_survey,
          kode_fr_reporting: this.editedItem.kode_fr_reporting,
          kode_fr_customer: this.editedItem.kode_fr_customer,
        });
        formData.append("workspacePart", string);
        await this.addWorkspace(formData)
          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
              this.loading = false;
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
        this.loading = false;
      } else {
        this.loading = true;
        let formData = new FormData();
        for (const i of Object.keys(this.selectedFiles)) {
          formData.append("file", this.selectedFiles[i][0]);
        }

        let id_workspace = this.propsworkspace.id_workspace;
        // let id_workspace = localStorage.getItem("myworkspace");
        formData.append("id_workspace", id_workspace);

        let string = JSON.stringify({
          file_workspace: "",
          kode_fr_keluhan: this.editedItem.kode_fr_keluhan,
          kode_fr_workorder: this.editedItem.kode_fr_workorder,
          kode_fr_survey: this.editedItem.kode_fr_survey,
          kode_fr_reporting: this.editedItem.kode_fr_reporting,
          kode_fr_customer: this.editedItem.kode_fr_customer,
          name: this.editedItem.name,
          keterangan: this.editedItem.keterangan,
        });
        formData.append("workspacePart", string);
        await this.updateWorkspace(formData)
          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
              this.loading = false;
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
        this.loading = false;
      }
    },
  },
};
</script>
<style scoped>
.rounded-card {
  border-radius: 30px;
}
</style>