import axios from "axios";
const token = sessionStorage.getItem('token-user')
const state = {
    survey: [],
    charts: [],
    listbyidsurvey: [],
    year: [],
    allbytype: [],
    surveycustomer: [],
    surveydownload: [],


}
const getters = {
    surveylist: state => state.survey,
    chartsurvey: state => state.charts,
    byidsurvey: state => state.listbyidsurvey,
    yearlist: state => state.year,
    bytypelist: state => state.allbytype,
    bysurveycustomer: state => state.surveycustomer,
    bydownload: state => state.surveydownload,


}
const actions = {

    async fetchSurvey({ commit }) {
        const myinstitusi = sessionStorage.getItem('id_user')
        const response = await axios.get("/login-crm/surveys", {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        // return response
        commit("setSurvey", response.data)

    },
    async surveyDashboardS({ commit }) {
        const myinstitusi = sessionStorage.getItem('id_user')
        const response = await axios.get("/login-crm/surveyDashboard", {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        // return response
        commit("setSurveyDashboard", response.data)
        console.log("surveyDashboardS", response.data);

    },
    async addSurvey({ commit }, survey) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.post("/login-crm/addSurveys  ", survey, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });

        commit("addSurvey", response)
        return response;
    },
    async updateSurvey({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.put(`/login-crm/fillSurvey?id_survey=${data.id_survey}`, data, {
            headers: {
                'Content-Type': 'application/json',
                //the token is a variable which holds the token
            }
        });
        return response;

    },
    async surveyActive({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = axios.get(`/login-crm//surveyActive/${data.link}`, {

        })
        return response
            // commit("updateStatus", id_keluhan, perbaikan, status);
    },


    async SubByIdSurvey({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get(`/login-crm/surveyById/${data.id }`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });

        return response;


    },

    // DeleteReporting
    async deleteSurveyID({ commit }, id_survey) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.delete(`/login-crm/deleteSurvey/${id_survey}`, {
                headers: {
                    Authorization: 'Bearer ' + token //the token is a variable which holds the token
                }
            })
            // commit("removeSurvey", id_survey)
        return response
    },
    async Generated({ commit }, data) {
        const token = sessionStorage.getItem('token-user');
        return new Promise((resolve, reject) => {
            axios.post(`/login-crm/generateLinkSurvey?id_survey=${data.idsurvey}`, data, {
                headers: {
                    Authorization: 'Bearer ' + token //the token is a variable which holds the token
                }
            }).then((res) => {
                resolve(res)
            });
        })
    },
    // allYearsByType
    async fetchYear({ commit }) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get("/login-crm/surveyAllYear", {
          headers: {
            Authorization: "Bearer " + token, //the token is a variable which holds the token
          },
        });
        commit("setAllYear", response.data)
    },


    //getSurveyByType
    // ?year=${year}&type=${type}&subscription=${subscription}
    async SurveyFilter({ commit }, data) {
        const token = sessionStorage.getItem('token-user')

        const response = await axios.get(
          `/login-crm/surveyFilter?id_workspace=${data.id_workspace}&name=${data.name}&page=${data.page}&period=${data.period}&size=${data.size}&status=${data.status}&subscription=${data.subscription}&type=${data.type}&year=${data.year}`,
          {
            headers: {
              Authorization: "Bearer " + token,
            },
          }
        );
        commit("setAllSurvey", response.data)

        return response.data


    },
    async SurveyCustomer({ commit }, data) {
        const token = sessionStorage.getItem('token-user')

        const response = await axios.get(`/login-crm/surveyCustomer?id_customer=${data.id_customer}&name=${data.name}&page=${data.page}&period=${data.period}&size=${data.size}&status=${data.status}&subscription=${data.subscription}&type=${data.type}&year=${data.year}`, {
            headers: {
                Authorization: 'Bearer ' + token
            }
        });
        commit("setAllSurveyCustomer", response.data)
        return response.data


    },
    async SurveyDownload({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get(
          `/login-crm/surveyDownload?id_workspace=${data.id_workspace}&name=${data.name}&page=${data.page}&period=${data.period}&size=${data.size}&status=${data.status}&subscription=${data.subscription}&type=${data.type}&year=${data.year}`,
          {
            headers: {
              Authorization: "Bearer " + token,
            },
          }
        );
        commit("setAllSurveyDownload", response.data)

        return response.data


    },


};

const mutations = {
    setSurvey: (state, survey) => (state.survey = survey),
    setSurveyDashboard: (state, charts) => (state.charts = charts),
    setAllYear: (state, year) => (state.year = year),
    setAllSurvey: (state, all) => (state.allbytype = all),
    setAllSurveyCustomer: (state, all) => (state.surveycustomer = all),
    setAllSurveyDownload: (state, all) => (state.surveydownload = all),
    addSurvey: (state, survey) => state.survey = survey,
    byIdSurvey: (state, listbyid) => (state.listbyidsurvey = listbyid),
    logIdSurvey: (state, logsurvey) => (state.logsurvey = logsurvey)

    // updateWorkspace: (state, workspace) => (state.workspace = (workspace => workspace))
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}