<template>
  <div>
    <v-row class="justify-center text-center">
      <v-col cols="7">
        <v-dialog v-model="dialog" persistent max-width="500px">
          <v-card class="rounded-card">
            <v-card-title class="toolbar-form">
              <v-spacer></v-spacer>
              Get Link
              <v-spacer></v-spacer>

              <v-btn icon @click="closet">
                <v-icon> mdi-close </v-icon>
              </v-btn>
            </v-card-title>
            <!-- <v-toolbar flat dense shaped class="h6 font-weight-black">
              <v-spacer></v-spacer>
              Get Link
              <v-spacer></v-spacer>
              <v-btn icon @click="closet">
                <v-icon> mdi-close </v-icon>
              </v-btn>
            </v-toolbar> -->
            <v-divider></v-divider>

            <v-card-text>
              <v-subheader>Link</v-subheader>
              <v-row>
                <v-col cols="12 text-center" md="12" sm="12">
                  <v-text-field
                    outlined
                    dense
                    append-icon=" mdi-content-copy"
                    v-model="editedItem.link"
                    @click:append="doCopy()"
                  ></v-text-field>
                </v-col>
                <!-- <v-col cols="12 text-center" md="6" sm="12" class="justify-end">
                  <v-btn depressed color="#E6EEF0" @click="doCopy()"
                    ><b color="#0072B6">Copy Link</b></v-btn
                  >
                </v-col> -->
              </v-row>
            </v-card-text>
            <v-card-actions class="justify-center">
              <v-btn width="40%" @click="closet" rounded color="success"
                >Done</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  props: ["propslink", "dialog"],
  data() {
    return {
      editedItem: {
        link: "",
      },
    };
  },
  mounted() {
    console.log("ehe mounted", this.propslink.link);
    this.editedItem.link = this.propslink.link;
  },

  watch: {
    propslink: {
      handler(val) {
        this.editedItem.link = val.link;
        // console.log("ehe watch", val);
        // console.log("dialog watch ", this.dialog);
        // console.log("dialog new watch ", val.dialog);
      },
      deep: true,
    },
    dialog: {
      handler(val) {
        // console.log("ehe dialog ", val);
      },
      deep: true,
    },
  },
  methods: {
    closet() {
      this.$emit("close", false);
    },

    doCopy: function() {
      let link;
      this.$copyText(this.editedItem.link).then(
        function(e) {
          link = e.text;
          console.log("link", link);
          navigator.clipboard.writeText(e.text);
          alert("Copied Success !!");
        },
        function(e) {
          console.log(e);
        }
      );
    },
  },
};
</script>

<style scoped>
.rounded-card {
  border-radius: 30px;
}
</style>
