<template>
  <v-container>
    <v-dialog persistent v-model="dialog" max-width="500px">
      <v-card class="rounded-card">
        <v-toolbar flat class="mx-6 toolbar-form">
          <v-spacer></v-spacer>
          {{ formTitle }}
          <v-spacer></v-spacer>
          <v-btn icon @click="dialog = false">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-toolbar>
        <v-divider></v-divider>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12"> </v-col>
              <v-col cols="12">
                <label>Name</label>
                <v-text-field
                  v-model="editedItem.name"
                  required
                  outlined
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <v-card-actions class="justify-center">
          <v-btn
            rounded
            width="60%"
            ref="modal-save"
            id="modal-save"
            color="primary"
            @click="save"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
  props: ["type", "propsperiod"],
  data: () => ({
    dialogsukses: false,
    typealert: "Congratulations",
    dialog: true,
    dialogDelete: false,

    editedItem: {
      name: "",
    },
  }),

  mounted() {
    // this.fetchPeriod();
    // console.log("type", this.type);
    // // this.editedItem = this.customer;
    if (this.type == "Edit Data") {
      this.editedItem.name = this.propsperiod.name_period;
    } else {
      this.editedItem.name = "";
    }
  },
  computed: {
    ...mapGetters("period", ["periodlist"]),
    formTitle() {
      return this.type === "Create Data" ? "Create Data" : "Edit Data";
    },
  },
  methods: {
    ...mapActions("period", ["addPeriod", "updatePeriod", "fetchPeriod"]),
    close() {
      this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("success", data);
    },

    async save() {
      if (this.type == "Create Data") {
        await this.addPeriod({
          name_period: this.editedItem.name,
        })
          .then((response) => {
            // console.log("res", response);
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
      } else {
        await this.updatePeriod({
          id_period: this.propsperiod.id_period,
          name_period: this.editedItem.name,
        })

          .then((response) => {
            console.log("res", response);
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
      }
    },
  },
};
</script>
<style scoped>
.rounded-card {
  border-radius: 30px;
}
</style>
