<template>
  <div :style="`width: 100vw; height: 100vh; background: url(${imglogo})`">
    <v-dialog
      v-model="dialog"
      scrollable
      content-class="style-dialog"
      persistent
    >
      <v-card class="rounded-card">
        <v-toolbar flat class="toolbar">
          <v-spacer></v-spacer>
          Complaint Confirmation
          <v-spacer></v-spacer>
        </v-toolbar>
        <v-toolbar flat class="toolbar">
          <v-img
            src="@/static/images/iso-9001-01.svg"
             max-height="200"
                max-width="70"
          ></v-img>
          <v-spacer></v-spacer>
          <v-col
            v-model="logoperusahaan"
            class="pt-4 justify-start"
            cols="12"
            md="2"
          >
            <v-img
              :src="'/images/fileKeluhan/' + logoperusahaan"
              max-height="69"
              max-width="126"
            ></v-img>
          </v-col>
        </v-toolbar>
        <v-spacer />
        <v-divider></v-divider>
        <v-subheader>Detail Complaint</v-subheader>
        <v-card-text>
          <v-spacer />
          <v-row class="d.flex">
            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1">Title</h3>
              <p class="font-italic">Judul</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="font-weight-medium subtitle-1">
                {{ item.nama }}
              </h3>
            </v-col>

            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1">Customer Name</h3>
              <p class="font-italic">Nama Pelanggan</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="font-weight-medium subtitle-1">
                {{ item.namecus }}
              </h3>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1">Date</h3>
              <p class="font-italic">Tanggal</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="font-weight-medium subtitle-1">
                {{ item.date }}
              </h3>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1">Detail Complaint</h3>
              <p class="font-italic">Detail keluhan</p>
            </v-col>
            <v-col
              cols="12"
              md="6"
              sm="12"
              class="justify-center font-weight-medium"
            >
              <v-textarea
                :disabled="item.status == 'DONE' || item.status == 'CLOSED'"
                filled
                rounded
                v-model="item.perbaikan"
              ></v-textarea>
            </v-col>
            <v-col cols="12" md="6" sm="12" v-if="item.status == 'DONE'">
              <span v-if="item.status == 'DONE'">
                <h3 class="subtitle-1">Correction</h3></span
              >
            </v-col>
            <v-col cols="12" md="6" sm="12" v-if="item.status == 'DONE'">
              <span v-if="item.status == 'DONE'">
                <h3 class="font-weight-bold subtitle-1">
                  {{ item.keluhan }}
                </h3>
              </span>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1">Status</h3>
              <p class="font-italic">status</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="font-weight-bold subtitle-1">
                <span :style="`color:${getColor(item)}`">{{
                  item.status
                }}</span>
              </h3>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="subtitle-1">File Available*</h3>
              <p class="font-italic">File tersedia*</p>
            </v-col>

            <v-col
              cols="12"
              md="6"
              sm="12"
              v-for="(image, i) in item.images"
              :key="i"
            >
              <v-img
                width="80"
                height="80"
                :src="`/images/fileKeluhan/${image}`"
              >
              </v-img>
            </v-col>
            <!-- <v-col class="border" v-if="item.status == 'NEW'" cols="12" md="6">
              <h3 class="subtitle-1">Attachment</h3>
              <p class="font-italic">Lampiran</p>
            </v-col> -->
            <v-col cols="12" md="12" sm="12" v-if="item.status == 'NEW'">
              <h3 class="subtitle-1">Attachment</h3>
              <p class="font-italic">Lampiran</p>
            </v-col>
            <v-col v-if="item.status == 'NEW'">
              <v-btn
                color="primary"
                class="text-none"
                fab
                x-small
                depressed
                :loading="isSelecting"
                @click="onButtonClick"
              >
                <v-icon center> mdi-plus </v-icon>
              </v-btn>
              <input
                multiple
                small-chips
                ref="uploader"
                class="d-none"
                type="file"
                accept="image/*"
                @change="onFileChanged"
              />
              <v-spacer />
            </v-col>
            <!-- setalah gambar di browse -->
            <v-col>
              <v-row>
                <v-img
                  width="100"
                  height="100"
                  v-for="(images, i) in selectedImages"
                  :key="i"
                  :src="images"
                >
                  <template v-slot:default>
                    <v-row class="mt-6" justify="center">
                      <v-chip
                        close
                        color="white"
                        outlined
                        @click:close="deleteimg(i)"
                      >
                        remove
                      </v-chip>
                    </v-row>
                  </template>
                </v-img>
              </v-row>
            </v-col>
          </v-row>
        </v-card-text>

        <v-divider
          v-show="item.status == 'NEW' && item.status == 'DONE'"
        ></v-divider>

        <v-card-actions
          v-show="item.status !== 'CONFIRMED' && item.status !== 'In PROGRESS'"
          class="justify-center"
        >
          <v-btn
            @click="acceptKeluhan(item)"
            v-show="item.status == 'NEW'"
            depressed
            left
            color="primary"
            dark
          >
            Submit</v-btn
          >
          <v-btn
            v-if="item.status == 'DONE' && 'CLOSE'"
            @click="acceptKeluhan(item)"
            depressed
            left
            color="success"
            dark
          >
            Accept</v-btn
          >
          <v-btn
            v-if="item.status == 'DONE' && 'CLOSE'"
            @click="decline(item)"
            depressed
            right
            color="error"
            dark
            >Decline</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
    <sukses :alert="dialogsukses" :type="typealert" @close="actionalert" />

    <PerbaikanKeluhan
      v-if="showperbaikan"
      @close="actionperbaikan"
      @tutup="showperbaikan = false"
    />
    <rate-comment v-if="showrating" @close="actionrating" />
  </div>
</template>

<script>
import Sukses from "../alert/Sukses.vue";

import logo from "@/assets/bg-1png-01.png";

import PerbaikanKeluhan from "../popup/PerbaikanKeluhan.vue";
import RateComment from "../popup/RateComment.vue";
import { mapActions } from "vuex";
export default {
  components: { PerbaikanKeluhan, RateComment, Sukses },
  data() {
    return {
      logoperusahaan: "",
      imglogo: logo,
      selectedFiles: [],
      selectedImages: [],
      isSelecting: false,
      showrating: false,
      showperbaikan: false,
      accept: false,
      // decline: false,
      dialog: false,
      dialogsukses: false,
      typealert: "Congratulations",

      dataprm: {
        link: "",
        // token: "",
      },
      item: {
        id: "",
        link: "",
        nama: "",
        namecus: "",
        date: "",
        keluhan: "",
        status: "",
        images: [],
        perbaikan: "",
        id_cust: "",
      },
      keterangan: "",
    };
  },

  mounted() {
    this.dataprm = {
      link: this.$route.params.link,
    };

    this.getKeluhan(this.dataprm);
  },
  created() {
    this.dataprm = {
      link: this.$route.params.link,
    };
    console.log(" this.dataprm", this.dataprm.link);
  },

  methods: {
    ...mapActions("keluhan", [
      "updateKonfirmasi",
      "KeluhanById",
      "KeluhanActive",
    ]),
    onButtonClick() {
      this.isSelecting = true;
      window.addEventListener(
        "focus",
        () => {
          this.isSelecting = false;
        },
        { once: true }
      );

      this.$refs.uploader.click();
    },
    onFileChanged(e) {
      this.selectedFiles.push(e.target.files);
      var files = e.target.files || e.dataTransfer.files;

      for (let index = 0; index < files.length; index++) {
        // const element = this.selectedFiles[index];
        console.log("this.selectedFiles ", files[index]);
        this.selectedImages.push(URL.createObjectURL(files[index]));
      }
    },
    deleteimg(item) {
      this.selectedImages.splice(item, 1);
      this.selectedFiles.splice(item, 1);
      if (item == 0) {
        this.selectedFiles.push(item);
      }
    },
    getKeluhan(e) {
      this.KeluhanActive({
        link: e.link,
      })
        .then((response) => {
          console.log("res", response);
          this.dialog = true;
          if (response.status == 200) {
            this.item = {
              id: response.data.Keluhan.id_keluhan,
              nama: response.data.Keluhan.title,
              namecus: response.data.Keluhan.customer.name,
              date: response.data.Keluhan.date,
              keluhan: response.data.Keluhan.keluhan,
              perbaikan: response.data.Keluhan.perbaikan,
              status: response.data.Keluhan.status,
              images: response.data.File_Customer,
              id_cust: response.data.Keluhan.customer.id_workspace.id_workspace,
            };
            this.logoperusahaan = response.data.Logo;
          }
        })
        .catch((err) => {
          console.log("err", err);
          this.dialog = false;
          this.$router.push("/*");
        });
    },
    acceptKeluhan(item) {
      if (item.status == "NEW") {
        let formData = new FormData();
        console.log("file apped 1", this.selectedFiles);
        //images looping

        for (const i of Object.keys(this.selectedFiles)) {
          formData.append("files", this.selectedFiles[i][0]);
        }

        formData.append(`id_keluhan`, this.item.id);
        formData.append(`status`, "CONFIRMED");
        formData.append(`ket_decline`, "");
        formData.append(`perbaikan`, this.item.perbaikan);
        this.updateKonfirmasi({
          formData: formData,
        })
          .then((response) => {
            console.log("response ubah", response);
            if (response.status == 200) {
              this.dialog = false;
              let data = {
                type: "create",
                status: true,
              };
              this.dialogsukses = true;
              this.typealert = "Congratulations";
              // this.$router.push("/confirm-page");
              // console.log("sukses", data);
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.dialogsukses = true;
              this.typealert = "Failed";
              this.$emit("success", data);
            }
          })
          .catch((err) => {
            this.dialogsukses = true;
            this.typealert = "Failed";
          });
      } else if (item.status == "DONE") {
        let formData = new FormData();
        console.log("file apped 1", this.selectedFiles);

        for (const i of Object.keys(this.selectedFiles)) {
          formData.append("files", this.selectedFiles[i][0]);
        }
        formData.append(`id_keluhan`, this.item.id);
        formData.append(`status`, "CLOSED");
        formData.append(`ket_decline`, "");
        formData.append(`perbaikan`, this.item.perbaikan);

        this.updateKonfirmasi({
          formData: formData,
        })
          .then((response) => {
            console.log("response ubah", response);
            if (response.status == 200) {
              this.dialog = false;
              let data = {
                type: "create",
                status: true,
              };

              this.dialogsukses = true;
              this.typealert = "Congratulations";
              // this.$router.push("/confirm-page");
              // console.log("sukses", data);
              // this.showrating = true;
            }
          })
          .catch((err) => {
            this.dialogsukses = true;
            this.typealert = "Failed";
          });
      }
    },
    decline() {
      this.showperbaikan = true;
    },
    actionalert(e) {
      this.dialogsukses = false;
      if (e == "Congratulations") {
        this.$router.push("/confirm-page");
      }
    },

    actionperbaikan(data) {
      let formData = new FormData();
      console.log("file apped 1", data);

      for (const i of Object.keys(this.selectedFiles)) {
        formData.append("files", this.selectedFiles[i][0]);
      }
      formData.append(`id_keluhan`, this.item.id);
      formData.append(`status`, "DECLINE");
      formData.append(`perbaikan`, this.item.perbaikan);
      formData.append(`ket_decline`, data.ket_decline);
      if (data.type == "close") {
        this.showperbaikan = false;
      } else {
        this.showperbaikan = false;
        this.dialog = false;

        this.updateKonfirmasi({
          formData: formData,
        })
          .then((response) => {
            if (response.status == 200) {
              this.dialogsukses = true;
              this.$router.push("/confirm-page");
              console.log("sukses", data);
            }
          })
          .catch((err) => {});
      }
    },
    actionrating(data) {},
    getColor(item) {
      if (item.status == "NEW") return "Primary";
      else if (item.status == "In PROGRESS") return "orange";
      else if (item.status == "DONE") return "green";
      else if (item.status == "CONFIRMED") return "cyan";
      else if (item.status == "CLOSE") return "purple";
      else return "Primary";
    },
  },
};
</script>
<style  scoped>
.rounded-card {
  border-radius: 30px;
}
.fill-height {
  background-image: url("/assets/bg-1png-01.png");
  color: white;
  font-size: 2rem;
  overflow: hidden;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.body {
  /* background-image: url("/assets/bg-1png-01.png"); */
  /* background-color: aqua; */
  background-image: url("/assets/bg-1png-01.png");
}
</style>