<template>
  <div class="bg-login fill-height secondary">
    <v-row class="justify-center align-center bg-login fill-height">
      <!-- <v-col cols="12"  md="7"  sm="8"> -->
      <v-col cols="12 " md="3">
        <v-card elevation="0" class="backlog">
          <v-alert
            v-model="warning"
            text
            dismissible
            type="error"
            icon="mdi-information-outline"
          >
            {{ pesan }}
          </v-alert>
          <v-card-title class=" justify-center">
            <v-img
              max-width="220"
              contain
              :src="$vuetify.theme.dark ? `${imglogodrk}` : `${imglogo}`"
            />
            <!-- <v-img max-width="200" src="@/static/images/crm-logo-drk.png" /> -->
          </v-card-title>

          <v-card-text>
            <v-row no-gutters class="text-center">
              <!-- <v-col class="text-center">
              
            </v-col> -->
              <v-col cols="12" class="align-center">
                <form @submit.prevent="login" ref="form">
                  <label class="">Username</label>
                  <v-text-field
                    prepend-inner-icon="mdi-account-outline"
                    filled
                    single-line
                    rounded
                    v-model="form.username"
                    ref="refusername"
                  ></v-text-field>
                  <label v-if="form.username !== ''">Kode OTP</label>
                  <v-otp-input
                    v-if="form.username !== ''"
                    length="6"
                    type="number"
                    v-model="form.password"
                  ></v-otp-input>
                  <!-- <v-text-field
                    prepend-inner-icon="mdi-lock"
                    filled
                    rounded
                    single-line
                    type="password"
                    v-model="form.password"
                    id="password"
                    label="Password"
                  ></v-text-field> -->

                  <div class="">
                    <v-spacer />
                    <br />
                    <v-btn
                      width="30%"
                      depressed
                      :loading="loading"
                      class=".border-red text-capitalize"
                      id="btn-sign-in"
                      color="primary"
                      type="submit"
                      rounded
                      ref="buttonlogin"
                      style="font-weight: 800; letter-spacing: 0px"
                    >
                      Sign In
                    </v-btn>
                  </div>
                </form>
              </v-col>
            </v-row>
            <!-- </v-container> -->
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <!-- <OpsiworkSpace :dialog="dialogWorkspaces" @status="setworkspace" /> -->
  </div>
  <!-- <v-container class="fill-height" style="border: 1px solid red">
  </v-container> -->
</template>

<script>
// import OpsiworkSpace from "@/components/popup/OpsiWorkspace.vue";
import logodrk from "@/static/images/crm-logo-drk.png";
import logo from "@/static/images/logo.png";
import { mapActions } from "vuex";

export default {
  // components: { OpsiworkSpace },

  data() {
    return {
      imglogodrk: logodrk,
      imglogo: logo,
      loading: false,
      warning: false,
      pesan: "",
      dialogWorkspaces: false,
      visibility: false,
      role: "",
      token: "",
      form: {
        username: "",
        password: "",
      },
    };
  },

  methods: {
    ...mapActions("auth", ["login2", "newLogin"]),
    async login() {
      this.loading = true;

      if (this.form.username == "") {
        this.warning = true;
        this.pesan = "username and password must not be empty";
        this.loading = false;
      } else {
        this.loading = true;
        await this.newLogin(this.form)
          .then((response) => {
            // const rules = window.btoa(response.data.role);
            console.log(response, "response  login");
            if (response.status === true) {
              sessionStorage.setItem("token-user", response.data.access_token);
              // localStorage.setItem("role", rules);
              localStorage.setItem("role",  response.data.role);
              localStorage.setItem("id_user", response.id_user);
              localStorage.setItem(
                "myworkspace",
                response.workspace[0].id_workspace
              );
              localStorage.setItem(
                "kode_fr_keluhan",
                response.workspace[0].kode_fr_keluhan
              );
              localStorage.setItem(
                "kode_fr_customer",
                response.workspace[0].kode_fr_customer
              );
              localStorage.setItem(
                "kode_fr_survey",
                response.workspace[0].kode_fr_survey
              );
              localStorage.setItem(
                "kode_fr_workorder",
                response.workspace[0].kode_fr_workorder
              );
              localStorage.setItem(
                "kode_fr_reporting",
                response.workspace[0].kode_fr_reporting
              );
              //tambahan
              //  localStorage.setItem("pin", response.data.pin);
              //  localStorage.setItem("exp", response.data.exp);
              //  localStorage.setItem("divisi", response.data.divisi);
              //  localStorage.setItem("refresh", response.data.refresh_token);
              //  localStorage.setItem("username_telegram", response.data.username_telegram);
              this.$router.push("/dashboard");
            } else {
              this.warning = true;
              this.pesan = "User Not Found !";
              this.loading = false;

              // console.log("komponen login", response);
              // let curentrouter = this.$route.path;
              // if (curentrouter == "/") {
              //   this.$router.push("/dashboard");
              // } else {
              //   this.$router.push("/dashboard");
              // }
            }
          })
          .catch((err) => {
            this.warning = true;
            this.loading = false;
            this.pesan = "User Unauthorized !";
          });
      }

      // console.log("log response login", response);
      // this.dialogWorkspaces=true
      // });
    },

    setworkspace() {
      this.dialogWorkspaces = false;
    },
  },
};
</script>
<style>
.border-red {
  border: 1px solid red;
}
.bg-login.fill-height {
  background-image: url("../static/images/bg-crm.png") !important;
}
.theme--dark .bg-login.fill-height {
  background-image: url("../static/images/bg-crm-dark.png") !important;
}

.theme--dark .img-logo {
  src: "../static/images/crm-logo-drk.png";
  max-width: 190;
}
.theme--light.v-otp-input .v-input .v-input__control .v-input__slot {
  background: #e8e8e8 !important;
  /* border: none !important; */
}
.otp-input {
  border-radius: 4px;
  border: 1px solid #f5cd79;
}
</style>
