import axios from "axios";
const token = sessionStorage.getItem('token-user')
const state = {
    institusi: [],
    listbyid: [],

}
const getters = {

    institusilist: state => state.institusi,
    byinstitusi: state => state.listbyid
}
const actions = {
    async fetchInstitusi({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const myinstitusi = localStorage.getItem('id_user')
        const response = await axios.get(`/login-crm/AllInstitusi?page=${data.page}&size=${data.size}&name=${data.name}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        // return response
        commit("setInstitusi", response.data)
        console.log("Institusi store", response.data);
    },
    async InstitusiById({ commit }) {
        const token = sessionStorage.getItem('token-user')
        const myworkspace = localStorage.getItem('id_user')
        const response = await axios.get(`/login-crm/InstitusiById/${myworkspace}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("byinstitusi", response.data)
        console.log("get institusi by ID", response.data);
    },
    async addInstitusi({ commit }, institusi) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.post("/login-crm/AddInstitusi", institusi, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });

        // commit("addInstitusi", response)
        return response;
    },
    async deleteInstitusi({ commit }, id_institusi) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.delete(`/login-crm/deleteInstitusi?id_institusi=${id_institusi}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        })

        return response;
    },
    async updateInstitusi({ commit }, data) {
        const response = await axios.put(
          `/login-crm/updateInstitusi?id_institusi=${data.id_institusi}`,
          data,
          {
            headers: {
              Authorization: "Bearer " + token, //the token is a variable which holds the token
            },
          }
        );
        return response;
    }


};

const mutations = {
    setInstitusi: (state, institusi) => (state.institusi = institusi),
    byinstitusi: (state, byinstitusi) => (state.listbyid = byinstitusi),
    // addInstitusi: (state, institusi) => state.respon = institusi,

    // removeInstitusi: (state, id_institusi) => {
    //     state.institusi.filter(institusi => institusi.id_institusi == id_institusi),
    //         state.institusi.splice(institusi => institusi.id_institusi, 1)

    // },
    // updateWorkspace: (state, workspace) => (state.workspace = (workspace => workspace))
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}