<template>
  <div>
    <v-row class="ml-4 my-4 pr-10">
      <v-col cols="12">
        <!-- <v-breadcrumbs divider=">" :items="items"></v-breadcrumbs> -->
      </v-col>
      <v-col cols="12">
        <div>
          <v-row class="align-bottom">
            <v-col cols="12" md="8">
              <h1 class="Montserrat">
                {{ titles }}
              </h1>
            </v-col>
          </v-row>
          <v-row class="justify-end">
      
            <!-- <v-col class="justify-end" cols="12" md="3">
             
            </v-col> -->
            <v-col class="justify-end offset-1" cols="12" md="3">
              <v-text-field
                v-model="search"
                prepend-inner-icon="mdi-magnify"
                label="Search here"
                filled
                rounded
                dense
                single-line
                hide-details
                v-on:change="changeType()"
                @click:prepend-inner="changeType()"
              ></v-text-field>
            </v-col>
            <v-col class="justify-end" cols="12" md="2">
              <v-select
                label="type"
                dense
                flat
                solo
                :items="role"
                rounded
                filled
                v-on:change="changeType()"
                v-model="roles"
                clearable
              >
              </v-select>
            </v-col>

            <!-- <v-col cols="12" md="2" class=" d-flex justify-end"> -->
              <!-- <v-btn rounded color="primary " @click="adduser()" class="text-capitalize" depressed>
            
                Create User</v-btn
              > -->
            <!-- </v-col> -->
          
          </v-row>
        </div>
      </v-col>

      <v-row>
        <v-col cols="12" class="mx-3 my-2">
          <v-card class="shadow rounded-card" :loading="loading">
            <v-card-text>
              <v-data-table
                :headers="headers"
                :items="userslist.User"
                hide-default-footer
                :page.sync="page"
                :items-per-page="size"
                @page-count="pageCount = $event"
                class="elevation-0"
              >
                <template v-slot:[`item.role`]="{ item }">
                  {{ getRole(item.role).toUpperCase() }}
                  <!-- {{ item.role.toUpperCase() }} -->
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        v-bind="attrs"
                        v-on="on"
                        class="mx-1 my-2"
                        fab
                        x-small
                        dark
                        color="primary"
                        @click="adduser(item)"
                      >
                        <v-icon x-small> mdi-pencil </v-icon>
                      </v-btn>
                    </template>
                    <span>Edit</span>
                  </v-tooltip>
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        dark
                        fab
                        x-small
                        color="red"
                        @click="deleteItem(item)"
                        v-bind="attrs"
                        v-on="on"
                        depressed
                      >
                        <v-icon x-small> mdi-delete </v-icon>
                      </v-btn>
                    </template>
                    <span>Remove </span>
                  </v-tooltip>
                </template>
              </v-data-table>

              <v-row class="justify-end">
                <v-col cols="12" md="3" class="justify-center">
                  <v-pagination
                    v-model="page"
                    class="my-4"
                    v-on:change="changeType()"
                    :length="totaldata"
                  >
                  </v-pagination>
                </v-col>
                <v-col cols="12" md="1" class="select">
                  <v-select
                    label="Row per page"
                    :value="size"
                    type="number"
                    :items="row"
                    dense
                    outlined
                    v-on:change="changeType()"
                    @input="size = parseInt($event, 10)"
                  ></v-select>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-row>
    <form-user
      :propsdata="user"
      @success="actionUser"
      :type="type"
      v-if="isShowModal"
    />
    <confirm
      @actionconfirm="deleteuser"
      :id_user="iddelete"
      v-if="confirmshow"
    />
    <sukses
      :alert="dialogsukses"
      :type="typealert"
      @close="dialogsukses = false"
    />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";

import FormUser from "./form/FormUser.vue";
import Sukses from "./alert/Sukses.vue";
// import TambahEdit from "../components/form/TambahEdit.vue";
import Confirm from "./popup/Confirm.vue";

// import TambahEdit from "../components/form/TambahEdit.vue";
export default {
  components: { Confirm, FormUser, Sukses },
  data: () => ({
    loading: true,
    search: "",
    row: [5, 10, 15, 20, 25],
    page: 1,
    size: 5,
    pageCount: 0,
    totaldata: 0,
    dialogsukses: false,
    typealert: "Congratulations",
    isShowModal: false,
    confirmshow: false,
    content: false,
    card1: 0,
    titles: "Administrator",
    iddelete: "",
    id_workspace: "",
    listuser: [],
    marketing: "",
    admin: "",
    analis: "",
    ops: "",
    role: ["MARKETING", "ADMIN", "ANALYST", "OPERATIONAL"],
    roles: "",

    items: ["users"],

    headers: [
      {
        text: "NAME",
        sortable: false,
        value: "name",
      },
      {
        text: "USERNAME",
        sortable: false,
        value: "username_telegram",
      },

      { text: "ROLE", value: "role" },

      { text: "ACTION", value: "actions", sortable: false },
    ],

    type: "",
    user: {},
  }),
  computed: {
    ...mapState("users", ["respon"]),
    ...mapGetters("users", ["userslist"]),
    formTitle() {
      return this.type === "Create User" ? "Create User" : "Edit User";
    },
  },
  watch: {
    page() {
      this.onfilter();
    },
    search() {
      this.onfilter();
    },
  },
  async created() {
    this.onfilter();

    // this.listuser = this.userslist;
  },

  methods: {
    ...mapActions("users", ["fetchuser", "deleteUser"]),

    getRole(role) {
      switch (role) {
        case "QURNSU4":
          return "Admin";
        case "T1BFUkFUSU9OQUw":
          return "Operational";
        case "QU5BTFlTVA":
          return "Analyst";
        case "TUFSS0VUSU5H":
          return "Marketing";

        default:
          return "";
      }
    },

    adduser(item) {
      if (item != undefined) {
        this.user = item;
        this.type = "Edit User";
        this.isShowModal = !this.isShowModal;
      } else {
        this.isShowModal = !this.isShowModal;
        this.type = "Create User";
      }
    },
    changeType() {
      this.page = 1;
      this.onfilter();
      if (this.roles == "QURNSU4") {
        this.roles = "ADMIN";
      }
      if (this.roles == "TUFSS0VUSU5H") {
        this.roles = "MARKETING";
      }
      if (this.roles == "QU5BTFlTVA") {
        this.roles = "ANALYST";
      }
      if (this.roles == "T1BFUkFUSU9OQUw") {
        this.roles = "OPERATIONAL";
      }
    },

    async onfilter() {
      this.loading = true;
      if (this.roles == null) {
        this.roles = "ALL";
      }
      if (this.roles == "ADMIN") {
        this.roles = "QURNSU4";
      }
      if (this.roles == "MARKETING") {
        this.roles = "TUFSS0VUSU5H";
      }
      if (this.roles == "ANALYST") {
        this.roles = "QU5BTFlTVA";
      }
      if (this.roles == "OPERATIONAL") {
        this.roles = "T1BFUkFUSU9OQUw";
      }

      const prm = {
        name: this.search,
        size: this.size,
        page: this.page,
        role: this.roles,
      };
      await this.fetchuser(prm);
      this.totaldata = this.userslist.TotalPages;
      this.loading = false;
      // this.listuser = this.userslist;
    },

    deleteItem(item) {
      this.iddelete = item.id_user;

      this.confirmshow = true;
    },

    deleteuser(data) {
      if (data.id_user !== "") {
        this.loading = true;
        this.deleteUser(data.id_user)
          .then((response) => {
            if (response.status == 200) {
              this.$emit("success");
              this.typealert = "Congratulations";
              this.dialogsukses = true;
              this.confirmshow = false;
              this.onfilter();
              this.loading = false;
            } else {
              this.typealert = "Failed !";
              this.dialogsukses = true;
              this.confirmshow = false;
            }
          })
          .catch((err) => {
           
            this.typealert = "Failed !";
            this.dialogsukses = true;
            this.loading = false;
          });
      } else {
        this.confirmshow = false;
        this.loading = false;
      }
    },
    async actionUser(data) {
      this.isShowModal = false;
      this.loading = true;
      if (data.type == "create") {
        if (data.status == true) {
          this.dialogsukses = true;
          this.typealert = "Congratulations";
          await this.onfilter();
          this.loading = false;
        } else {
          this.dialogsukses = true;
          this.typealert = "Failed !";
        }
      } else {
        this.loading = false;
        setTimeout(() => {
          this.onfilter();
        }, 1000);
      }
    },
    // workspace(data){
    //   this.data =
    // }
  },
};
</script>
<style scoped>
.selected {
  background-color: red;
}
.rounded-card {
  border-radius: 30px;
}
.select {
  padding-top: 30px;
}
</style>
