<template>
  <v-container>
    <v-dialog persistent v-model="dialog" max-width="500px">
      <v-card class="rounded-card">
        <v-card-title class="mx-6 toolbar-form">
          <v-spacer></v-spacer>
          {{ formTitle }}
          <v-spacer></v-spacer>
          <v-btn icon @click="close()">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-card-title>
        <!-- <v-toolbar flat class="mx-6 toolbar-form">
          <v-spacer></v-spacer>
          {{ formTitle }}
          <v-spacer></v-spacer>
          <v-btn icon @click="dialog = false">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-toolbar> -->
        <v-divider></v-divider>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <h3 class="h3-text">Name</h3>
                <p class="font-italic">Nama</p>

                <v-text-field
                  v-model="editedItem.name"
                  required
                  outlined
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <h3 class="h3-text">Description</h3>
                <p class="font-italic">Keterangan</p>

                <v-textarea
                  v-model="editedItem.keterangan"
                  name="input-7-1"
                  filled
                  outlined
                ></v-textarea>
                <!-- <v-combobox :items="items" label="role" required>
                    </v-combobox> -->
              </v-col>
              <!-- <v-col cols="12">
                    <v-col cols="12">
                      <v-select
                        v-model="editedItem.users"
                        :items="userslist"
                        label="Pilih User"
                        item-text="name"
                        item-value="id_user"
                      ></v-select>
                    </v-col>
                  </v-col> -->
            </v-row>
          </v-container>
        </v-card-text>

        <v-card-actions class="justify-center">
          <v-btn
            rounded
            width="30%"
            ref="modal-save"
            id="modal-save"
            color="primary"
            @click="save"
            :loading="loading"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import { mapActions, mapGetters, mapState } from "vuex";
export default {
  props: ["type", "propsinstitusi"],
  data: () => ({
    dialogsukses: false,
    typealert: "Congratulations",
    dialog: true,
    loading: false,
    dialogDelete: false,

    editedItem: {
      keterangan: "",
      name: "",
    },
    defaultItem: {
      keterangan: "",
      name: "",
      users: [],
    },
  }),

  mounted() {
    if (this.type == "Edit Institution") {
      this.editedItem.name = this.propsinstitusi.name;
      this.editedItem.keterangan = this.propsinstitusi.keterangan;
    } else {
      this.editedItem.name = "";
      this.editedItem.keterangan = "";
    }
  },
  computed: {
    formTitle() {
      return this.type === "Create Institution"
        ? "Create Institution"
        : "Edit Institution";
    },
  },
  methods: {
    ...mapActions("institusi", ["addInstitusi", "updateInstitusi"]),
    close() {
      this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("success", data);
    },

    async save() {
      this.loading = true;
      if (this.type == "Create Institution") {
        await this.addInstitusi({
          name: this.editedItem.name,
          keterangan: this.editedItem.keterangan,
        })
          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
              this.loading = false;
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
        this.loading = false;
      } else {
        this.loading = true;
        await this.updateInstitusi({
          id_institusi: this.propsinstitusi.id_institusi,
          name: this.editedItem.name,
          keterangan: this.editedItem.keterangan,
        })
          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
              this.loading = false;
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
        this.loading = false;
      }
    },
  },
};
</script>
<style scoped>
.rounded-card {
  border-radius: 30px;
}
</style>
