<template>
  <div>
    <v-alert
      v-model="warning"
      text
      dismissible
      prominent
      type="error"
      icon="mdi-message-alert"
    >
      {{ text_alert }}
    </v-alert>
    <v-row class="mx-5 mt-4">
      <v-col cols="12">
        <div>
          <v-row class=" justify-end">
            <v-col cols="12" md="7">
              <h1 style="font-weight-regular">
                {{ titles }}
              </h1>
              <span>{{ kode_fr_customer }}</span>
            </v-col>

            <v-col class="pt-0 d-flex justify-end" cols="4" md="3">
            </v-col>
            <v-col
              v-model="logo"
             class="d-flex justify-space-around "
              cols="9"
              md="2"
              sm="5"
            >
              <v-img
                src="../static/images/iso-9001-01.svg"
                max-height="200"
                max-width="70"
                contain
              ></v-img>
              <v-img
                :src="'/images/fileKeluhan/' + logo"
                width="60%"
                contain
              ></v-img>
            </v-col>
          </v-row>

          <v-row class="d-flex justify-end">
          
            <v-col class="justify-end" cols="12 " md="3">
              <v-text-field
                v-model="search"
                prepend-inner-icon="mdi-magnify"
                label="Search Customer"
                filled
                rounded
                dense
                single-line
                hide-details
                v-on:change="changeType()"
                @click:prepend-inner="changeType()"
              ></v-text-field>
            </v-col>

            <v-col cols="12" md="2" class=" d-flex justify-end">
              <v-btn rounded color="primary " @click="addcustomer()" depressed  class="text-capitalize">
                <!-- <v-icon color="white" left> </v-icon> -->
                Create Customer</v-btn
              >
            </v-col>
          </v-row>
        </div>
      </v-col>

      <v-row>
        <v-col cols="12" class="mx-3 my-2">
          <v-card class="shadow rounded-card" :loading="loading">
            <v-card-text>
              <v-data-table
                :headers="headers"
                :items="listbyidworkspace.Customer"
                sort-by="name"
                class="elevation-0"
                hide-default-footer
                :page.sync="page"
                :items-per-page="size"
                @page-count="pageCount = $event"
              >
                <template v-slot:[`item.workspace`]="{ item }">
                  {{ item.workspace }}
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        class="mx-1 my-2"
                        fab
                        v-bind="attrs"
                        v-on="on"
                        x-small
                        dark
                        color="primary"
                        @click="addcustomer(item)"
                      >
                        <v-icon x-small> mdi-pencil </v-icon>
                      </v-btn>
                    </template>
                    <span>Edit</span>
                  </v-tooltip>
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        fab
                        x-small
                        dark
                        color="red"
                        @click="deleteItem(item)"
                        v-bind="attrs"
                        v-on="on"
                      >
                        <v-icon x-small> mdi-delete </v-icon>
                      </v-btn>
                    </template>
                    <span>Remove </span>
                  </v-tooltip>
                </template>
              </v-data-table>

              <v-row class="justify-end">
                <v-col cols="12" md="4" class="justify-center">
                  <v-pagination
                    v-model="page"
                    class="my-4"
                    v-on:change="changeType()"
                    :length="totaldata"
                    :total-visible="7"
                  >
                  </v-pagination>
                </v-col>
                <v-col cols="12" md="1" class="select">
                  <v-select
                    label="Row per page"
                    :value="size"
                    type="number"
                    :items="row"
                    dense
                    outlined
                    v-on:change="changeType()"
                    @input="size = parseInt($event, 10)"
                  ></v-select>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-row>
    <tambah-edit
      :type="type"
      v-if="isShowModal"
      :item="customer"
      @success="actionfrom"
    />
    <confirm
      @actionconfirm="deletecustomer"
      :id_customer="iddelete"
      v-if="confirmshow"
    />
    <sukses :alert="dialogsukses" :type="typealert" />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
import TambahEdit from "../components/form/TambahEdit.vue";
import Sukses from "./alert/Sukses.vue";
import Confirm from "../components/popup/Confirm.vue";
// import TambahEdit from "../components/form/TambahEdit.vue";
export default {
  components: { TambahEdit, Confirm, Sukses },
  data: () => ({
    text_alert: "",
    row: [5, 10, 15, 20, 25],
    page: 1,
    size: 5,
    pageCount: 0,
    totaldata: 0,
    logo: "",
    warning: false,
    search: "",
    dialogsukses: false,
    typealert: "Congratulations",
    isShowModal: false,
    confirmshow: false,
    content: false,
    card1: 0,
    loading: true,
    titles: "Customer",
    iddelete: "",
    id_workspace: "",
    listcustomer: [],

    items: ["customer"],
    headers: [
      // { text: "id_customer", value: "id_customer" },
      { text: "WORKSPACE", value: "workspace.name" },
      {
        text: "NAME",
        value: "name",
      },
      // { text: "password", value: "password" },
      { text: "COMPANY", value: "dataInstitusi.institusi.name" },
      { text: "CONTACT PERSON", value: "contactPerson" },
      { text: "DESCRIPTION", value: "keterangan" },
      { text: "ACTION", value: "actions", sortable: false, width: "20%" },
    ],
    items: [
      {
        text: "Customer",
        disabled: false,
        href: "breadcrumbs_dashboard",
      },
      {
        text: "Link 1",
        disabled: false,
        href: "breadcrumbs_link_1",
      },
    ],
    type: "",
    customer: {},
    kode_fr_customer: "",
  }),
  computed: {
    ...mapGetters("customer", ["listbyidworkspace"]),
    ...mapState("customer", ["customerlistbyidworkspace"]),
    formTitle() {
      return this.type === "Create Customer"
        ? "Create Customer"
        : "Edit Customer";
    },
    // pageCounts() {
    //   return Math.ceil(this.totaldata / this.size);
    // },
  },
  watch: {
    page() {
      this.datacust();
    },
    search() {
      this.datacust();
    },
  },

  async created() {
    await this.datacust();
    this.id_workspace = localStorage.getItem("myworkspace");
    this.kode_fr_customer = localStorage.getItem("kode_fr_customer");
  },
  methods: {
    ...mapActions("customer", [
      "fetchCustomers",
      "deleteCustomerbyID",
      "CustomersByidWorkspace",
    ]),

    addcustomer(item) {
      if (item != undefined) {
        this.customer = item;
        this.type = "Edit Customer";
        this.isShowModal = !this.isShowModal;
      } else {
        this.isShowModal = !this.isShowModal;
        this.type = "Create Customer";
      }
    },
    async datacust() {
      this.loading = true;
      const prm = {
        name: this.search,
        size: this.size,
        page: this.page,
        id_workspace: localStorage.getItem("myworkspace"),
      };
      await this.CustomersByidWorkspace(prm);
      this.loading = false;

      this.logo = this.customerlistbyidworkspace.Logo;
      this.totaldata = this.customerlistbyidworkspace.TotalPages;
      this.loading = false;
    },
    changeType() {
      this.page = 1;
      this.datacust();
      // this.SurveyFilter(this.typesurvey);
    },
    deleteItem(item) {
      this.iddelete = item.id_customer;

      this.confirmshow = true;
    },
    deletecustomer(data) {
      if (data.id !== "") {
        this.loading = true;
        this.deleteCustomerbyID(data.id)
          .then((response) => {
            console.log(response, "response");
            if (response.status == 200) {
              this.$emit("success");
              this.typealert = "Congratulations";
              this.dialogsukses = true;
              this.confirmshow = false;
              this.datacust();
              this.loading = false;
            } else {
              this.typealert = "Failed !";
              this.dialogsukses = true;
              this.confirmshow = false;
            }
          })
          .catch((err) => {
            // this.typealert = "Failed !";
            // this.dialogsukses = true;
            // console.log(err.response, "err");
            this.text_alert = err.response.data;

            this.warning = true;

            this.loading = false;
          });
        this.confirmshow = false;
      } else {
        this.confirmshow = false;
        this.loading = false;
      }
    },
    async actionfrom(data) {
      this.isShowModal = false;
      this.loading = true;
      if (data.type == "create") {
        if (data.status == true) {
          this.dialogsukses = true;
          this.typealert = "Congratulations";
          setTimeout(() => {
            this.datacust();
          }, 1000);
          this.loading = false;
        } else {
          this.dialogsukses = true;
          this.typealert = "Failed !";
          this.loading = false;
        }
      } else {
        this.loading = false;
        setTimeout(() => {
          this.datacust();
        }, 1000);
      }
    },
  },
};
</script>

<style scoped>
.select {
  padding-top: 30px;
}
</style>
