<template>
  <v-container>
    <v-dialog persistent v-model="dialog" width="500px" scrollable>
      <v-form v-model="isValid">
        <v-card class="rounded-card">
          <v-card-title class="mx-6 toolbar-form">
            <v-spacer></v-spacer>
            {{ formTitle }}
            <v-spacer></v-spacer>
            <v-btn icon @click="close()">
              <v-icon> mdi-close </v-icon>
            </v-btn>
          </v-card-title>
          <!-- <v-toolbar flat class="mx-6 toolbar-form">
            <v-spacer></v-spacer>
            {{ formTitle }}
            <v-spacer></v-spacer>
            <v-btn icon @click="dialog = false">
              <v-icon> mdi-close </v-icon>
            </v-btn>
          </v-toolbar> -->
          <v-divider></v-divider>
          <v-card-text>
            <h3 class="subtitle-1">Customer Name<span class="red--text">*</span></h3>
            <p class="font-italic">Nama Pelanggan</p>
            <v-text-field
              :rules="[(v) => !!v || 'Required.']"
              v-model="editedItem.name"
              outlined
              dense
              required
            ></v-text-field>

            <h3 class="h3-text">Company / Institution<span class="red--text">*</span></h3>
            <p class="font-italic">Perusahaan / Institusi</p>

            <v-autocomplete
              outlined
              dense
              :rules="[(v) => !!v || 'Required.']"
              v-model="editedItem.institusi"
              :items="institusilist.Institution"
              item-text="name"
              item-value="id_institusi"
              v-on:change="changeInstitusi()"
            ></v-autocomplete>

            <h3 class="h3-text">SubInstitution<span class="red--text">*</span></h3>
            <p class="font-italic">Subinstitusi</p>
            <v-select
              outlined
              dense
              v-model="editedItem.subinstitusi"
              :items="bysubidinstitusi"
              item-text="name"
              item-value="id_subinstitusi"
            ></v-select>

            <h3 class="subtitle-1">Address</h3>
            <p class="font-italic">Alamat</p>
            <v-text-field
              v-model="editedItem.address"
              outlined
              dense
              required
            ></v-text-field>
            <h3 class="subtitle-1">Email</h3>
            <p class="font-italic">Email</p>
            <v-text-field
              v-model="editedItem.email"
              outlined
              dense
              required
            ></v-text-field>
            <h3 class="subtitle-1">phone Number</h3>
            <p class="font-italic">Nomor Telepon</p>
            <v-text-field
              v-model="editedItem.phoneNumber"
              outlined
              dense
              required
            ></v-text-field>
            <h3 class="subtitle-1">Contact Person<span class="red--text">*</span></h3>
            <p class="font-italic">Narahubung</p>
            <v-text-field
              :rules="[(v) => !!v || 'Required.']"
              v-model="editedItem.contact_person"
              outlined
              dense
              required
            ></v-text-field>

            <h3 class="h3-text">Descriptions</h3>
            <p class="font-italic">Deskripsi</p>

            <v-textarea
              v-model="editedItem.keterangan"
              name="input-7-1"
              outlined
            ></v-textarea>
          </v-card-text>

          <v-card-actions class="justify-center">
            <!-- <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn> -->
            <v-btn
              rounded
              width="60%"
              ref="modal-save"
              id="modal-save"
              color="primary"
              :disabled="!isValid"
              :loading="loading"
              @click="save"
            >
              Save
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>

    <sukses :alert="dialogsukses" :type="typealert" />
  </v-container>
</template>
<script>
import { mapActions, mapGetters, mapState } from "vuex";

import Sukses from "../alert/Sukses.vue";
export default {
  components: { Sukses },
  props: ["type", "item"],
  data: () => ({
    dialogsukses: false,
    typealert: "Congratulations",
    dialog: true,
    loading: false,
    dialogDelete: false,
    isValid: true,

    editedItem: {
      address: "",
      email: "",
      contact_person: "",
      phoneNumber: "",
      keterangan: "",
      perusahaan: "",
      subinstitusi: "",

      name: "",
      password: "",
      workspace: [],
      institusi: [],
    },
  }),

  watch: {
    item(val) {
      if (this.type == "Edit Customer") {
        console.log("val", val);
      }
    },
  },
  mounted() {
    const prm = {
      name: "",
      size: 1000,
      page: 1,
    };

    this.fetchInstitusi(prm);

    // this.editedItem = this.customer;
    if (this.type == "Edit Customer") {
      console.log("propss", this.item);
      this.editedItem.name = this.item.name;
      this.editedItem.address = this.item.alamat;
      this.editedItem.email = this.item.email;
      this.editedItem.phoneNumber = this.item.phoneNumber;
      this.editedItem.contact_person = this.item.contactPerson;
      this.editedItem.keterangan = this.item.keterangan;
      this.editedItem.perusahaan = this.item.perusahaan;
      this.editedItem.workspace = this.item.workspace;
      this.editedItem.subinstitusi = this.item.dataInstitusi.id_subinstitusi;
      this.editedItem.institusi =
        this.item.dataInstitusi.institusi.id_institusi;
      this.SubByIdInstitusi(this.editedItem.institusi);
    } else {
      this.editedItem.name = "";
      this.editedItem.address = "";
      this.editedItem.contact_person = "";
      this.editedItem.email = "";
      this.editedItem.phoneNumber = "";

      this.editedItem.keterangan = "";
      this.editedItem.perusahaan = "";
      this.editedItem.workspace = "";
      this.editedItem.institusi = "";
      this.editedItem.subinstitusi = "";
    }
  },
  computed: {
    ...mapState("customer", ["respon"]),
    ...mapGetters("institusi", ["institusilist"]),
    ...mapGetters("subinstitusi", ["bysubidinstitusi"]),

    formTitle() {
      return this.type === "Create Customer"
        ? "Create Customer"
        : "Edit Customer";
    },
  },
  methods: {
    ...mapActions("customer", [
      "addCustomer",
      "updateCustomer",
      "CustomersByidWorkspace",
    ]),

    ...mapActions("institusi", ["fetchInstitusi"]),
    ...mapActions("subinstitusi", ["SubByIdInstitusi"]),

    changeInstitusi() {
      this.SubByIdInstitusi(this.editedItem.institusi);

      console.log(this.bysubidinstitusi, "hasil");
    },

    close() {
      this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("success", data);
    },

    async save() {
      this.loading = true;
      if (this.type == "Create Customer") {
        try {
          await this.addCustomer({
            keterangan: this.editedItem.keterangan,
            alamat: this.editedItem.address,
            phoneNumber: this.editedItem.phoneNumber,
            email: this.editedItem.email,
            contactPerson: this.editedItem.contact_person,
            perusahaan: "",
            name: this.editedItem.name,
            id_workspace: {
              id_workspace: localStorage.getItem("myworkspace"),
            },
            id_subinstitusi: {
              id_subinstitusi: this.editedItem.subinstitusi,
            },
            role: "CUSTOMER",
          });

          if (this.respon.status == 200) {
            let data = {
              type: "create",
              status: true,
            };
            this.$emit("success", data);
          } else {
            let data = {
              type: "create",
              status: false,
            };
            this.$emit("success", data);
          }
        } catch (error) {
          this.typealert = "Failed !";
          this.dialogsukses = true;
        }
        this.loading = false;
      } else {
        this.loading = true;
        await this.updateCustomer({
          id_customer: this.item.id_customer,
          keterangan: this.editedItem.keterangan,
          alamat: this.editedItem.address,
          phoneNumber: this.editedItem.phoneNumber,
          keterangan: this.editedItem.keterangan,
          contactPerson: this.editedItem.contact_person,
          perusahaan: "",
          name: this.editedItem.name,
          id_workspace: {
            id_workspace: this.item.workspace.id_workspace,
          },
          id_subinstitusi: {
            id_subinstitusi: this.editedItem.subinstitusi,
          },
          email: this.editedItem.email,
          role: "CUSTOMER",
        })

          // this.updateCustomer({
          //   id_customer: this.item.id_customer,
          //   keterangan: this.editedItem.keterangan,
          //   perusahaan: this.editedItem.perusahaan,
          //   name: this.editedItem.name,
          //   id_workspace: {
          //     id_workspace: this.item.workspace.id_workspace,
          //   },
          //   role: "customer",
          // })
          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {
            this.typealert = "Failed !";
            this.dialogsukses = true;
            this.loading = false;
          });
        this.loading = false;
      }
      this.close();
      this.loading = false;
    },
  },
};
</script>
<style  scoped>
.rounded-card {
  border-radius: 30px;
}
</style>