<template>
  <div>
    <v-dialog persistent v-model="dialog" max-width="500">
      <v-card class="rounded-card">
        <v-card-title class="mx-6 toolbar-form">
          <v-spacer></v-spacer>
          {{ formTitle }}
          <v-spacer></v-spacer>
          <v-btn icon @click="close()">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-card-title>
        <!-- <v-toolbar flat class="mx-6 text-h5 font-weight-bold">
          <v-spacer></v-spacer>
          {{ formTitle }}
          <v-spacer></v-spacer>
          <v-btn icon @click="dialog = false">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-toolbar> -->
        <v-divider></v-divider>
        <v-card-text>
          <div>
            <v-subheader></v-subheader>
            <label>Name</label>
            <v-text-field
              v-model="editedItem.name"
              outlined
              disabled
              dense
              required
            ></v-text-field>
            <label>Username</label>
            <v-text-field
              v-model="editedItem.username"
              required
              disabled
              dense
              outlined
            ></v-text-field>
            <label>Password</label>
            <v-text-field
              v-model="editedItem.password"
              required
              outlined
              dense
              type="password"
            ></v-text-field>
            <label>Role</label>
            <v-select
              dense
              v-model="editedItem.role"
              :items="rolesitem"
              outlined
              item-text="state"
              item-value="abbr"
              persistent-hint
              return-object
            ></v-select>
            <label>Worskpace</label>
            <v-select
              dense
              v-model="editedItem.workspace"
              :items="workspacelist.Workspace"
              outlined
              multiple
              item-text="name"
              item-value="id_workspace"
              persistent-hint
            ></v-select>
          </div>
        </v-card-text>

        <v-card-actions class="d-flex justify-center">
          <!-- <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn> -->
          <v-btn
            width="50%"
            rounded
            ref="modal-save"
            id="modal-save"
            color="primary"
            @click="save"
            :loading="loading"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <sukses :alert="dialogsukses" :type="typealert" />
  </div>
</template>
<script>
import { mapActions, mapGetters, mapState } from "vuex";
import Sukses from "../alert/Sukses.vue";
export default {
  props: ["type", "propsdata"],
  components: { Sukses },
  data: () => ({
    dialogsukses: false,
    loading: false,
    typealert: "Congratulations",
    dialog: true,
    rolesitem: ["MARKETING", "ADMIN", "ANALYST", "OPERATIONAL"],
    // workspaceitem: [],
    dialogDelete: false,
    text: "",
    pin:null,
    access_token:"",
    exp:"",
    divisi:"",
    refresh:"",
    last_login:"",
    editedItem: {
      name: "",
      username: "",
      password: "",
      role: "",
      workspace: "",
      username_telegram:""
    },
  }),
  mounted() {
    let text = this.propsdata.role;
    const prm = {
      name: "",
      size: 1000,
      page: 1,
    };
    this.fetchWorkspace(prm);

    if (this.type == "Edit User") {
      console.log(this.propsdata, "edit watch");
      this.editedItem.name = this.propsdata.name;
      this.editedItem.username = this.propsdata.username_telegram; 
      // this.pin=this.propsdata.pin,
      // this.access_token=this.propsdata.access_token,
      // this.exp=this.propsdata.exp,
      // this.divisi=this.propsdata.divisi,
      // this.refresh=this.propsdata.refresh_token,
      // this.last_login=this.propsdata.last_login,
      // this.editedItem.username = this.propsdata.username;
      // this.editedItem.password = this.propsdata.password;
      // this.editedItem.role = this.propsdata.role;
      this.editedItem.role = window.atob(text);
      this.editedItem.workspace = this.propsdata.id_workspace;
    } else {
      this.editedItem.name = "";
      this.editedItem.password = "";
      this.editedItem.role = "";
      this.editedItem.workspace = "";
    }
  },
  computed: {
    ...mapGetters("workspace", ["workspacelist"]),
    formTitle() {
      return this.type === "Create User" ? "Create User" : "Edit User";
    },
    // getdata() {
    //   const data = JSON.parse();
    //   this.workspace = data.id_workspace;

    //   return data;
    // },
  },
  methods: {
    ...mapActions("workspace", ["fetchWorkspace"]),
    ...mapActions("users", ["addUser", "updateUser"]),
    close() {
      this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("success", data);
    },

    async save() {
      this.loading = true;

      if (this.type == "Create User") {
        await this.addUser({
          name: this.editedItem.name,
          username: this.editedItem.username,
          password: this.editedItem.password,
          role: this.editedItem.role,
          // id_workspace: [`this.editedItem.workspace`],
          id_workspace: this.editedItem.workspace,
        })
          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
              this.loading = false;
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
        this.loading = false;
      } else {
        this.loading = true;
        await this.updateUser({
          // workspace: { id_workspace: this.editedItem.workspace[0] },
          // workspace: {
          //   id_workspace: this.editedItem.workspace,
          // },
          id_user: this.propsdata.id_user,
          username_telegram: this.editedItem.username,
          name: this.editedItem.name,
          password: this.editedItem.password,
          role: this.editedItem.role,
          access_token:this.propsdata.access_token,
          divisi: this.propsdata.divisi,
          exp: this.propsdata.exp,
          last_login: this.propsdata.last_login,
          pin: this.propsdata.pin,
          refresh_token:this.propsdata.refresh_token ,
          // token: localStorage.getItem("token-user"),
          id_workspace: this.editedItem.workspace,
        })
          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
              this.loading = false;
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
        this.loading = false;
      }
    },
  },
};
</script>
<style scoped>
.rounded-card {
  border-radius: 30px;
}
</style>