<template>
  <div>
    <v-dialog v-model="dialog" persistent width="700px" scrollable>
      <v-card class="rounded-card">
        <v-card-title>
          <v-spacer></v-spacer>
          Confirm Workorder
          <v-spacer></v-spacer>
          <v-btn icon @click="close()">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-card-title>
        <!-- <v-toolbar flat class="mx-6 toolbar-form">
          <v-spacer></v-spacer>
          Confirm Workorder
          <v-spacer></v-spacer>
          <v-btn icon @click="close()">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-toolbar> -->
        <v-spacer />
        <v-divider></v-divider>
        <v-subheader class="font-weight-bold">DETAIL</v-subheader>
        <v-card-text>
          <div>
            <v-row>
              <v-col cols="12" md="4">
                <h3 class="h3-text">Category</h3>
                <p class="font-italic">Kategori</p>
              </v-col>
              <v-col cols="12" md="8">
                <h3 class="h3-props">{{ propsWorkorder.kategori }}</h3>
              </v-col>
            </v-row>

            <v-row>
              <v-col cols="12" md="4">
                <h3 class="h3-text">Deadline</h3>
                <p class="font-italic">Batas Waktu</p>
              </v-col>
              <v-col cols="12" md="8">
                <h3 class="h3-props">{{ propsWorkorder.batas_waktu }}</h3>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4">
                <h3 class="h3-text">Work Order Description</h3>
                <p class="font-italic">Deskripsi Workorder</p>
              </v-col>
              <v-col cols="12" md="8">
                <h3 class="h3-props">{{ propsWorkorder.deskripsi }}</h3>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4">
                <h3 class="h3-text">Task Description</h3>
                <p class="font-italic">Deskripsi Tugas</p>
              </v-col>
              <v-col cols="12" md="8">
                <v-textarea
                  class="h3-props"
                  filled
                  rounded
                  v-model="editedItem.keterangan"
                ></v-textarea>
                <!-- {{ propsWorkorder.keterangan }} -->
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4">
                <h3 class="h3-text">Status</h3>
                <p class="font-italic">Status</p>
                <v-spacer></v-spacer>
                <h3 class="h3-props"></h3>
              </v-col>
              <v-col cols="12" md="5">
                <h3 class="h3-text">
                  <v-select
                    hide-details
                    v-model="editedItem.status"
                    :items="statusitem"
                    item-text="state"
                    item-value="abbr"
                    persistent-hint
                    return-object
                    outlined
                    rounded
                    dense
                  ></v-select>
                </h3>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="4" class="align-center">
                <h3 class="h3-text">ATTACHMENT</h3>
                <p class="font-italic">Lampiran</p>
              </v-col>
              <v-row v-if="editedItem.file_customer.length > 0">
                <v-col
                  cols="12"
                  md="2"
                  v-for="(image, i) in editedItem.file_customer"
                  :key="i"
                  :style="i < 2 ? '' : 'display:none !important'"
                >
                  <v-hover v-slot="{ hover }">
                    <v-card :elevation="hover ? 12 : 2">
                      <v-img
                        :src="'/images/fileKeluhan/' + image"
                        max-height="200"
                        max-width="200"
                      >
                        <v-row class="flex-column" justify="space-between">
                          <div class="align-self-center mt-6">
                            <v-btn
                              :class="{ 'show-btns': hover }"
                              :color="transparent"
                              icon
                              @click="ShowImage(editedItem.file_customer)"
                            >
                              <v-icon :class="{ 'white--text': hover }">
                                mdi-image-search
                              </v-icon>
                            </v-btn>
                          </div>
                        </v-row>
                      </v-img>
                    </v-card>
                  </v-hover>
                </v-col>

                <v-col
                  cols="4"
                  class="pl-0 pr-1 col col-2"
                  v-if="editedItem.file_customer.length > 2"
                >
                  <v-card>
                    <v-img
                      :src="'/na-image/' + editedItem.file_customer[2]"
                      max-height="200"
                      max-width="200"
                    >
                      <v-row class="flex-column" justify="space-between">
                        <div class="align-self-center mt-6">
                          <v-btn
                            class="show-img red--text"
                            :color="transparent"
                            small
                            @click="ShowImage(editedItem.file_customer)"
                          >
                            <span>+</span
                            >{{ editedItem.file_customer.length - 2 }}
                          </v-btn>
                        </div>
                      </v-row>
                    </v-img>
                  </v-card>
                </v-col>
              </v-row>
            </v-row>
          </div>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-center">
          <v-btn
            width="30%"
            color="primary"
            rounded
            class="btn-update"
            @click="ubahstatus"
          >
            <!-- <v-icon>mdi-pencil<v-icon>  -->
            Update Status</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
    <DialogOpenImage
      :dialog="showImage"
      :data="dataImage"
      @close-dialog="showImage = false"
    />
  </div>
</template>
<script>
import { mapActions, mapGetters, mapState } from "vuex";
import DialogOpenImage from "../DialogOpenImage.vue";
export default {
  props: ["propsWorkorder"],
  components: { DialogOpenImage },
  data() {
    return {
      selectedFiles: [],
      selectedImages: [],
      showImage: false,
      dataImage: [],
      isSelecting: false,

      error: false,
      statusitem: ["In PROGRESS", "DONE", "NEW"],
      accept: false,
      decline: true,
      dialog: true,
      resolve: null,
      reject: null,
      editedItem: {
        kategori: "",
        status: "",
        posisi: "",
        keterangan: "",
        file_customer: "",
      },
    };
  },

  mounted() {
    this.editedItem.status = this.propsWorkorder.status;
    this.editedItem.keterangan = this.propsWorkorder.keterangan;
  },
  computed: {
    // buttonText() {
    //   return this.selectedFile
    //     ? this.selectedFile.name
    //     : this.defaultButtonText;
    // },
    // ...mapActions("keluhan", ["fetchKeluhan", "updateStatus", "Generated"]),
  },
  async created() {
    await this.WorkorderById(this.propsWorkorder.id_workorder).then(
      (response) => {
        this.editedItem.file_customer = response.data.File_Workorder;
        this.editedItem.posisi = response.data.Workorder.posisi;
        this.editedItem.kategori = response.data.Workorder.kategori;
      }
    );
  },
  methods: {
    ...mapActions("workorder", [
      "fetchWorkorder",
      "updateStatusWorkorder",
      "WorkorderById",
    ]),
    close() {
      // alert("close");
      this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("close", data);
    },
    getColor(propsWorkorder) {
      if (propsWorkorder.status == "DONE") return "green";
      else if (propsWorkorder.status == "In PROGRESS") return "orange";
      else return "primary";
    },
    ShowImage(e) {
      this.showImage = true;
      this.dataImage = e;
    },
    onButtonClick() {
      this.isSelecting = true;
      window.addEventListener(
        "focus",
        () => {
          this.isSelecting = false;
        },
        { once: true }
      );

      this.$refs.uploader.click();
    },

    onFileChanged(e) {
      this.selectedFiles.push(e.target.files);
      var files = e.target.files || e.dataTransfer.files;

      for (let index = 0; index < files.length; index++) {
        // const element = this.selectedFiles[index];

        this.selectedImages.push(URL.createObjectURL(files[index]));
      }
    },
    ubahstatus() {
      // let formData = new FormData();

      // for (var i = 0; i < this.selectedFiles.length; i++) {
      //   let file = this.selectedFiles[i];
      //   formData.append(`file`, file);

      // }
      var formData = new FormData();
      formData.append(`id_workorder`, this.propsWorkorder.id_workorder);
      formData.append(`status`, this.editedItem.status);
      formData.append(`keterangan`, this.editedItem.keterangan);
      for (const i of Object.keys(this.selectedFiles)) {
        formData.append("files", this.selectedFiles[i][0]);
      }
      this.updateStatusWorkorder({
        // id_workorder: this.propsWorkorder.id_workorder,
        // status: this.editedItem.status,
        // keterangan: this.editedItem.keterangan,
        formData: formData,
      })

        .then((response) => {
          if (response.status == 200) {
            let data = {
              type: "save",
              status: true,
            };
            this.$emit("close", data);
          } else {
            let data = {
              type: "save",
              status: false,
            };
            this.$emit("close", data);
          }
        })
        .catch((err) => {
          let data = {
            type: "save",
            status: false,
          };
          this.$emit("close", data);
        });
    },
  },
};
</script>
<style  scoped>
.rounded-card {
  border-radius: 30px;
}
</style>