<template>
  <v-container>
    <v-dialog v-model="dialog" persistent width="500">
      <v-card class="rounded-card">
        <v-toolbar flat class="border toolbar-opsiwork">
          <v-spacer class="spacer"></v-spacer>
          <v-toolbar-title class="toolbar-opsiwork"
            >Pilih Workspace</v-toolbar-title
          >

          <v-spacer></v-spacer>
          <v-btn icon color="primary" @click="close">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-toolbar>
        <v-divider />
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-list dense>
                  <v-list-item-group v-model="selectedItem" color="primary">
                    <v-list-item
                      v-for="(item, i) in workspacebyid"
                      :key="i"
                      @click="selectedmyworkspace(item)"
                    >
                      <v-list-item-content
                        v-if="item.id_workspace == mywork"
                        style="background: #0D47A1; color: #fff; padding: 10px"
                      >
                        <v-list-item-title
                          v-text="item.name"
                        ></v-list-item-title>
                      </v-list-item-content>
                      <v-list-item-content v-else>
                        <v-list-item-title
                          v-text="item.name"
                        ></v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>
                  </v-list-item-group>
                </v-list>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  props: ["data", "dialog"],
  data() {
    return {
      selectedItem: "",
      seen: true,
      mywork: "",
      id: "",
      vshow: false,
    };
  },
  watch: {
    dialog(val) {
      if (this.dialog == true) {
        this.mywork = localStorage.getItem("myworkspace");
        this.id = localStorage.getItem("token-user");

        this.WorkspaceByIdUser(localStorage.getItem("id_user"));
      }
    },
  },
  mounted() {
    // this.fetchWorkspace();
    // this.WorkspaceByIdUser();
    // this.workspaceitem = this.workspacelist;
  },
  computed: {
    ...mapGetters("workspace", ["workspacelist", "workspacebyid"]),
  },

  methods: {
    ...mapActions("workspace", ["fetchWorkspace", "WorkspaceByIdUser"]),
    selectedmyworkspace(item) {
      // const data = CryptoJS.AES.encrypt(item).toString();
      // console.log("e", item.id_workspace);
      this.$emit("status", false);
      localStorage.setItem("myworkspace", item.id_workspace);
      localStorage.setItem("kode_fr_keluhan", item.kode_fr_keluhan);
      localStorage.setItem("kode_fr_workorder", item.kode_fr_workorder);
      localStorage.setItem("kode_fr_survey", item.kode_fr_survey);
      localStorage.setItem("kode_fr_reporting", item.kode_fr_reporting);
      localStorage.setItem("kode_fr_customer", item.kode_fr_customer);
      localStorage.setItem("dark_theme", this.$vuetify.theme.dark.toString());
      // console.log("return this.$route.name;", this.$route.path);
      let curentrouter = this.$route.path;
      if (curentrouter == "/") {
        this.$router.push("/dashboard");
      } else {
      this.$route.path;
      location.reload();
        // this.$emit("selectrouter");
      }
    },
    close() {
      // alert("close");
      this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("close", data);
    },
  },
};
</script>

<style scoped>
.rounded-card {
  border-radius: 30px;
}
</style>
