<template>
  <div>
    <v-dialog
      v-model="dialog"
      persistent
      transition="dialog-bottom-transition"
      max-width="800"
    >
      <v-carousel>
        <!-- <v-col class="d-flex flex-row-reverse"> -->
        <v-btn
          class="grey lighten-1"
          color="white"
          icon
          fab
          dark
          x-small
          @click="close()"
          style="position: absolute; z-index: 1; top: 15px; right: 20px"
          ><v-icon>mdi-close</v-icon></v-btn
        >
        <!-- </v-col> -->
        <v-carousel-item
          contain
          v-for="(item, i) in data"
          :key="i"
          :src="'/images/fileKeluhan/' + item"
        ></v-carousel-item>
        <v-sheet height="100%" tile>
          <v-row class="fill-height" align="center" justify="center">
            <div class="text-h2">
              <!-- Slide {{ i + 1 }} -->
            </div>
          </v-row>
        </v-sheet>
      </v-carousel>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "image-dialog",
  props: ["dialog", "data"],
  methods: {
    close() {
      this.$emit("close-dialog");
    },
  },
};
</script>
