  <template>
  <v-container>
    <v-dialog persistent v-model="dialog" max-width="500px">
      <v-card class="rounded-card">
        <v-toolbar flat class="mx-6 toolbar-form">
          <v-spacer></v-spacer>
          {{ formTitle }}
          <v-spacer></v-spacer>
          <v-btn icon @click="dialog = false">
            <v-icon> mdi-close </v-icon>
          </v-btn>
        </v-toolbar>
        <v-divider></v-divider>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12"> </v-col>
              <v-col cols="12">
                <label>Name</label>
                <v-text-field
                  v-model="editedItem.name"
                  required
                  outlined
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <v-card-actions class="justify-center">
          <v-btn
            rounded
            width="60%"
            ref="modal-save"
            id="modal-save"
            color="primary"
            @click="save"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import { mapActions, mapGetters, mapState } from "vuex";
export default {
  props: ["type", "propssubperiod"],
  data: () => ({
    dialogsukses: false,
    typealert: "Congratulations",
    dialog: true,
    dialogDelete: false,
    // rules: [
    //   (value) => !!value || "Required.",
    //   (value) =>
    //     (value || "").length >= 8 ||
    //     "password should have at least 8 characters",
    // ],
    editedItem: {
      name: "",
    },
  }),

  mounted() {
    console.log("type", this.type);

    if (this.type == "Edit Data") {
      console.log(this.propssubperiod, "propssub");
      this.editedItem.name = this.propssubperiod.name_subperiod;
    } else {
      console.log("fillkosong workspace");
      this.editedItem.name = "";
    }
  },
  computed: {
    ...mapGetters("subperiod", ["subperiodlist"]),
    formTitle() {
      return this.type === "Create Data" ? "Create Data" : "Edit Data";
    },
  },
  methods: {
    ...mapActions("subperiod", [
      "addSubPeriod",
      "updateSubPeriod",
      "fetchSubPeriod",
    ]),

    close() {
      this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("success", data);
    },

    async save() {
      if (this.type == "Create Data") {
        await this.addSubPeriod({
          name_subperiod: this.editedItem.name,
        })
          .then((response) => {
            console.log("res", response);
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
      } else {
        await this.updateSubPeriod({
          id_subperiod: this.propssubperiod.id_subperiod,
          name_subperiod: this.editedItem.name,
        })
          .then((response) => {
            console.log("res", response.status);
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
      }
    },
  },
};
</script>
<style  scoped>
.rounded-card {
  border-radius: 30px;
}
</style>