<template>
  <v-container>
    <v-dialog persistent v-model="dialog" width="500px" scrollable>
      <v-form v-model="isValid">
        <v-card class="rounded-card">
          <v-card-title class="mx-6 toolbar-form">
            <v-spacer></v-spacer>
            {{ formTitle }}
            <v-spacer></v-spacer>
            <v-btn icon @click="close()">
              <v-icon> mdi-close </v-icon>
            </v-btn>
          </v-card-title>

          <v-divider></v-divider>

          <v-card-text cols="  text-center ">
            <!-- <div> -->
            <label></label>
            <h3 class="subtitle-1">Title<span class="red--text">*</span></h3>
            <p class="font-italic">Judul</p>
            <v-text-field
              required
              v-model="editedItem.title"
              :rules="[(v) => !!v || 'Required.']"
              outlined
              dense
            ></v-text-field>
            <!-- <label>Position</label>
            <v-text-field
              v-model="editedItem.posisi"
              outlined
              dense
              required
            ></v-text-field> -->
            <label></label>
            <h3 class="subtitle-1">Complaint Detail</h3>
            <p class="font-italic">Detail Keluhan</p>
            <v-text-field
              v-model="editedItem.perbaikan"
              required
              outlined
            ></v-text-field>
            <h3 class="subtitle-1">Problem Analysis</h3>
            <p class="font-italic">Analisa Masalah</p>
            <v-text-field
              v-model="editedItem.analisa_masalah"
              required
              outlined
            ></v-text-field>

            <h3 class="subtitle-1">Correction</h3>
            <p class="font-italic">Perbaikan</p>
            <v-text-field
              v-model="editedItem.keluhan"
              required
              outlined
            ></v-text-field>

            <h3 class="subtitle-1">Root Cause</h3>
            <p class="font-italic">Akar Masalah</p>
            <v-text-field
              v-model="editedItem.root_cause"
              required
              outlined
            ></v-text-field>

            <h3 class="subtitle-1">Corrective Action</h3>
            <p class="font-italic">Tindakan Korektif</p>
            <v-text-field
              v-model="editedItem.corrective_action"
              required
              outlined
            ></v-text-field>

            <h3 class="subtitle-1">Date<span class="red--text">*</span></h3>
            <p class="font-italic">Tanggal</p>
            <v-menu
              v-model="menu"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              dense
              outlined
              min-width="auto"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="date"
                  append-icon="mdi-calendar"
                  :rules="[(v) => !!v || 'Required.']"
                  readonly
                  outlined
                  dense
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                outlined
                dense
                v-model="date"
                @input="menu = false"
              ></v-date-picker>
            </v-menu>
            <h3 class="subtitle-1">Customer Name<span class="red--text">*</span></h3>
            <p class="font-italic">Nama Pelanggan</p>

            <v-autocomplete
              v-model="editedItem.customer"
              :items="listbyidworkspace.Customer"
              :rules="[(v) => !!v || 'Required.']"
              item-text="name"
              item-value="id_customer"
              outlined
              dense
            ></v-autocomplete>
            <h3 class="h3-text">ATTACHMENT</h3>
            <p class="font-italic">Lampiran</p>
            <v-row>
              <v-col
                v-for="image in editedItem.file_customer"
                :key="image.id"
                class="d-flex child-flex"
                cols="4"
              >
                <v-img
                  :src="`/images/fileKeluhan/${image}`"
                  aspect-ratio="1"
                  class="grey lighten-2"
                >
                  <template v-slot:placeholder>
                    <v-row
                      class="fill-height ma-0"
                      align="center"
                      justify="center"
                    >
                      <v-progress-circular
                        indeterminate
                        color="grey lighten-5"
                      ></v-progress-circular>
                    </v-row>
                  </template>
                </v-img>
              </v-col>
            </v-row>
            <col />
            <div class="mt-6">
              <v-btn
                color="primary"
                class="text-none"
                fab
                x-small
                depressed
                :loading="isSelecting"
                @click="onButtonClick"
              >
                <v-icon center> mdi-plus </v-icon>
                <!-- {{ buttonText }} -->
              </v-btn>
              <input
                
                small-chips
                ref="uploader"
                class="d-none"
                type="file"
                accept="image/*"
                @change="onFileChanged"
              />
              <v-spacer />

              <v-row class="mt-4">
                <v-img
                  width="100"
                  height="100"
                  v-for="(images, i) in selectedImages"
                  :key="i"
                  :src="images"
                >
                  <template v-slot:default>
                    <v-row justify="center" class="mt-6">
                      <v-chip
                        close
                        color="white"
                        outlined
                        @click:close="deleteimg(i)"
                      >
                        remove
                      </v-chip>
                    </v-row>
                  </template>
                </v-img>
              </v-row>
            </div>

            <!-- <v-img width="100" height="100" :src="selectedFiles"> </v-img> -->
            <!-- <v-btn fab x-small color="primary">
                <v-icon> mdi-plus</v-icon> -->
            <!-- </v-btn> -->
            <!-- </div> -->

            <!-- </v-btn> -->

            <!-- <v-col cols="6 pl-4" style="border: green solid 3px"> </v-col> -->
          </v-card-text>
          <v-card-actions class="d-flex justify-center">
            <v-btn
              width="20%"
              ref="modal-save"
              id="modal-save"
              color="primary"
              rounded
              @click="save"
              :disabled="!isValid"
              :loading="loading"
            >
              Save
            </v-btn>
            <!-- <v-btn color="error" text @click="close"> Cancel </v-btn> -->
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>
  </v-container>
</template>
<script>
import { mapActions, mapGetters, mapState } from "vuex";

// import Sukses from "../alert/Sukses.vue";
export default {
  props: ["type", "item", "dialog"],
  data: () => ({
    loading: false,
    chip1: true,
    selectedFiles: [],
    selectedImages: [],
    isSelecting: false,
    date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
      .toISOString()
      .substr(0, 10),
    menu: false,
    modal: false,
    dialogDelete: false,
    isValid: true,

    editedItem: {
      analisa_masalah: "",
      posisi: "",
      status: "",
      title: "",
      keluhan: "",
      perbaikan: "",
      root_cause: "",
      corrective_action: "",
      customer: [],
      file_customer: [],
      ket_decline: "",
    },
  }),

  watch: {
    dialog(newval, oldval) {
      if (this.dialog == true) {
        const prm = {
          name: "",
          size: 1000,
          page: 1,
          id_workspace: localStorage.getItem("myworkspace"),
        };
        this.CustomersByidWorkspace(prm);
        if (this.type == "Edit Complaint") {
          this.selectedImages = [];
          this.KeluhanById({
            id: this.item.id_keluhan,
            token: localStorage.getItem("token-user"),
          }).then((response) => {
            // this.editedItem.title = response.data.Keluhan.title;
            this.editedItem.file_customer = response.data.File_Customer;
            // this.editedItem.download = response.data.Download_File_Customer;
          });
          console.log("isifil user");
          this.editedItem.analisa_masalah = this.item.analisa_masalah;
          this.editedItem.posisi = this.item.posisi;
          // this.editedItem.status = this.item.status;
          this.editedItem.title = this.item.title;
          this.date = this.item.date;
          this.editedItem.keluhan = this.item.keluhan;
          this.editedItem.ket_decline = this.item.ket_decline;
          this.editedItem.perbaikan = this.item.perbaikan;
          this.editedItem.customer = this.item.customer.id_customer;
          this.editedItem.root_cause = this.item.root_cause;
          this.editedItem.corrective_action = this.item.corrective_action;
        } else {
          this.selectedImages = [];
          this.editedItem.file_customer = [];
          this.editedItem.analisa_masalah = "";
          // this.editedItem.analisa_masalah = "";
          this.editedItem.posisi = "";
          // this.editedItem.status = "";
          this.editedItem.title = "";
          this.date = "";
          this.editedItem.keluhan = "";
          this.editedItem.corrective_action = "";
          this.editedItem.root_cause = "";
          this.editedItem.perbaikan = "";
          this.editedItem.customer = "";
        }
      }
    },
  },
  computed: {
    ...mapState("customer", ["respon", "customerlistbyidworkspace"]),
    ...mapGetters("customer", ["listbyidworkspace"]),
    formTitle() {
      return this.type === "Create Complaint"
        ? "Create Complaint"
        : "Edit Complaint";
    },
    // buttonText() {
    //   return this.selectedFile
    //     ? this.selectedFile.name
    //     : this.defaultButtonText;
    // },
  },

  methods: {
    ...mapActions("keluhan", ["KeluhanById", "addKeluhan", "updateKeluhan"]),
    ...mapActions("customer", ["CustomersByidWorkspace"]),
    close() {
      let data = {
        type: "close",
      };
      this.$emit("success", data);
    },
    onButtonClick() {
      this.isSelecting = true;
      window.addEventListener(
        "focus",
        () => {
          this.isSelecting = false;
        },
        { once: true }
      );

      this.$refs.uploader.click();
    },

    onFileChanged(e) {
      // this.selectedFile = e.target.files[0];
      this.selectedFiles.push(e.target.files);
      var files = e.target.files || e.dataTransfer.files;

      for (let index = 0; index < files.length; index++) {
        // const element = this.selectedFiles[index];

        this.selectedImages.push(URL.createObjectURL(files[index]));
      }
    },
    deleteimg(item) {
      this.selectedImages.splice(item, 1);
      this.selectedFiles.splice(item, 1);
    },

    async save() {
      this.loading = true;
      if (this.type == "Create Complaint") {
        let d = new Date(this.date),
          month = "" + (d.getMonth() + 1),
          day = "" + d.getDate(),
          year = d.getFullYear();
        if (month.length < 2) month = "0" + month;
        if (day.length < 2) day = "0" + day;

        //inisiasi formdata
        let formData = new FormData();

        //images looping
        // for (var i = 0; i < this.selectedFiles.length; i++) {
        // let file = this.selectedFiles;
        // formData.append(`files`, file);
        // }
        for (const i of Object.keys(this.selectedFiles)) {
          formData.append("files", this.selectedFiles[i][0]);
        }

        //keluhanpart diubah menjadi stringify
        let string = JSON.stringify({
          analisa_masalah: this.editedItem.analisa_masalah,
          posisi: this.editedItem.posisi,
          title: this.editedItem.title,
          date: `${day}-${month}-${year}`,
          keluhan: this.editedItem.keluhan,
          corrective_action: this.editedItem.corrective_action,
          root_cause: this.editedItem.root_cause,
          perbaikan: this.editedItem.perbaikan,
          ket_decline: "",
          id_customer: { id_customer: this.editedItem.customer },
        });
        ///setelah ituu di append
        formData.append("keluhanPart", string);

        await this.addKeluhan(formData)

          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
        this.loading = false;
      } else {
        this.loading = true;
        let d = new Date(this.date),
          month = "" + (d.getMonth() + 1),
          day = "" + d.getDate(),
          year = d.getFullYear();
        if (month.length < 2) month = "0" + month;
        if (day.length < 2) day = "0" + day;

        //inisiasi formdata
        let formData = new FormData();
        for (const i of Object.keys(this.selectedFiles)) {
          formData.append("files", this.selectedFiles[i][0]);
        }
        //keluhanpart diubah menjadi stringify
        let string = JSON.stringify({
          analisa_masalah: this.editedItem.analisa_masalah,
          posisi: this.editedItem.posisi,
          status: this.item.status,
          title: this.editedItem.title,
          date: `${day}-${month}-${year}`,
          keluhan: this.editedItem.keluhan,
          ket_decline: this.item.ket_decline,
          perbaikan: this.editedItem.perbaikan,
          corrective_action: this.editedItem.corrective_action,
          root_cause: this.editedItem.root_cause,
          id_customer: { id_customer: this.editedItem.customer },
        });
        formData.append("keluhanPart", string);
        //mengambil id_keluhan
        let id = this.item.id_keluhan;
        formData.append("id_keluhan", id);

        await this.updateKeluhan(formData)

          .then((response) => {
            if (response.status == 200) {
              let data = {
                type: "create",
                status: true,
              };
              this.$emit("success", data);
            } else {
              let data = {
                type: "create",
                status: false,
              };
              this.$emit("success", data);
            }
          })
          .catch((err) => {});
        this.loading = false;
      }
    },
  },
};
</script>
<style  scoped>
.rounded-card {
  border-radius: 30px;
}
</style>


  