<template>
  <div>
    <v-row class="mx-5 mt-4">
      <v-col cols="12">
        <div>
          <v-row class=" justify-end">
            <v-col cols="12" md="7">
              <h1 class="Montserrat" style="font-weight-regular">
                {{ titles }}
              </h1>
              <span>{{ kode_fr_survey }}</span>
            </v-col>

            <v-col class="pt-0 d-flex justify-end " cols="4" md="3">
            </v-col>
            <v-col
              v-model="logo"
             class="d-flex justify-space-around "
              cols="9"
              md="2"
              sm="5"
            >
               <v-img
                src="../static/images/iso-9001-01.svg"
                max-height="200"
                max-width="70"
                contain
              ></v-img>
              <v-img
                class=""
                :src="'/images/fileKeluhan/' + logo"
               max-height="200"
                max-width="120"
                contain
              ></v-img>
            </v-col>
          </v-row>

          <v-row class="d-flex justify-end  " v-if="isSurvey">
            <v-col cols="12" md="1">
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    icon
                    text
                    color="primary"
                    v-show="arrowback"
                    @click="backtosurvey()"
                    v-bind="attrs"
                    v-on="on"
                  >
                    <v-icon>mdi-arrow-left</v-icon>
                  </v-btn>
                </template>
                <span>Back</span>
              </v-tooltip>
            </v-col>
            <v-col class="justify-end" cols="12" md="3">
              <v-text-field
                v-model="search"
                prepend-inner-icon="mdi-magnify"
                label="Customer"
                filled
                rounded
                dense
                v-on:change="change()"
                @click:prepend-inner="change()"
                single-line
                hide-details
              ></v-text-field>
            </v-col>
            <v-col class="justify-end" cols="12" md="2">
              <v-select
                label="type"
                dense
                flat
                solo
                :items="tipesurveys"
                rounded
                filled
                v-on:change="change()"
                v-model="typesurvey"
                clearable
              >
              </v-select>
            </v-col>
            <v-col class="justify-end" cols="12" md="2">
              <v-select
                label="year"
                dense
                solo
                flat
                rounded
                filled
                v-model="year"
                :items="yearlist"
                v-on:change="change()"
                clearable
              >
              </v-select>
            </v-col>
            <v-col class="justify-end" cols="12" md="2">
              <v-select
                label="subscription"
                dense
                solo
                flat
                :items="langganan"
                rounded
                filled
                v-model="subscription"
                v-on:change="change()"
                clearable
              >
              </v-select>
            </v-col>

            <v-col cols="12" md="2" class="btn-update d-flex justify-end">
              <v-btn rounded color="primary " @click="addsurvey()" depressed   class="text-capitalize">
                Create Survey
              </v-btn>
            </v-col>
          </v-row>
          <v-row class="d-flex justify-end " v-else>
            <v-col class="justify-end " cols="12" md="3">
              <v-text-field
                v-model="search"
                prepend-inner-icon="mdi-magnify"
                label="Customer"
                filled
                rounded
                dense
                v-on:change="changeType()"
                single-line
                @click:prepend-inner="changeType()"
                hide-details
              ></v-text-field>
            </v-col>
            <v-col class="justify-end " cols="12" md="2">
              <v-select
                label="Type"
                dense
                solo
                flat
                :items="tipesurveys"
                rounded
                filled
                v-on:change="changeType()"
                v-model="typesurvey"
                clearable
              >
              </v-select>
            </v-col>
            <v-col class="justify-end" cols="12" md="2">
              <v-select
                label="Year"
                dense
                solo
                flat
                rounded
                filled
                v-model="year"
                :items="yearlist"
                v-on:change="changeType()"
                clearable
              >
              </v-select>
            </v-col>
            <v-col class="justify-end" cols="12" md="2">
              <v-select
                label="Subscription"
                dense
                solo
                :items="langganan"
                rounded
                flat
                filled
                v-model="subscription"
                v-on:change="changeType()"
                clearable
              >
              </v-select>
            </v-col>

            <v-col cols="12" md="2" class="btn-update d-flex justify-end">
              <v-btn rounded color="primary " @click="addsurvey()"   class="text-capitalize" depressed>
                Create Survey
              </v-btn>
            </v-col>
          </v-row>
        </div>
      </v-col>

      <v-row>
        <v-col cols="12" class="mx-3 my-2">
          <v-card class="shadow rounded-card" :loading="loading">
            <v-card-title>
              <v-row class=" justify-end">
                <v-col cols="12" md="3" class=" d-flex justify-end">
                  <v-menu
                    bottom
                    left
                    :close-on-content-click="false"
                    v-model="menu"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        icon
                        color="primary"
                        dark
                        class="mb-2"
                        @click="ExportExcel"
                        title="Download"
                      >
                        <v-icon>mdi-download</v-icon>
                      </v-btn>
                      <v-btn
                        title="Filter"
                        icon
                        v-bind="attrs"
                        v-on="on"
                        v-on:change="changeType()"
                        color="primary"
                      >
                        <v-icon>mdi-dots-vertical</v-icon>
                      </v-btn>
                    </template>
                    <v-card>
                      <v-card-text>
                        <div>Filter By</div>
                      </v-card-text>
                      <v-list>
                        <v-list-item>
                          <v-list-item-action>
                            <v-row>
                              <v-col>
                                <v-radio-group v-model="status" row dense>
                                  <v-radio
                                    label="Filled"
                                    value="FILLED"
                                    dense
                                  ></v-radio>
                                  <v-radio
                                    label="Unfilled"
                                    value="UNFILLED"
                                    dense
                                  ></v-radio>
                                  <v-radio
                                    label="ALL"
                                    value="ALL"
                                    dense
                                  ></v-radio> </v-radio-group
                              ></v-col>
                            </v-row>
                          </v-list-item-action>
                        </v-list-item>
                        <v-list-item>
                          <v-list-item-action v-show="this.status == 'FILLED'">
                            <v-row>
                              <v-col>
                                <v-select
                                  prepend-inner-icon="mdi-calendar-range"
                                  label="Period"
                                  dense
                                  :items="periode"
                                  rounded
                                  filled
                                  v-model="period"
                                  clearable
                                >
                                </v-select>
                              </v-col>
                            </v-row>
                          </v-list-item-action>
                        </v-list-item>
                      </v-list>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="primary" text @click="submit()">
                          Save
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-menu>
                </v-col>
              </v-row>
            </v-card-title>

            <v-card-text>
              <v-data-table
                v-if="isSurvey"
                :headers="header2"
                :single-expand="singleExpand"
                :expanded.sync="expanded"
                :items="bysurveycustomer.Surveys"
                :page.sync="page"
                :items-per-page="size"
                @page-count="pageCount = $event"
                hide-default-footer
                class="elevation-0"
                sort-by="Customer.name"
              >
                <template v-slot:[`item.date_filled`]="{ item }">
                  {{ item.date_filled || "-" }}
                </template>
                <template v-slot:[`item.contract`]="{ item }">
                  {{ item.contract || "-" }}
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        fab
                        x-small
                        dark
                        class="mx-1 my-2"
                        color="primary"
                        v-bind="attrs"
                        v-on="on"
                        @click="ClickDetail(item)"
                      >
                        <v-icon x-small> mdi-magnify-scan </v-icon>
                      </v-btn>
                    </template>
                    <span>Detail</span>
                  </v-tooltip>
                  &nbsp;
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        fab
                        x-small
                        dark
                        color="red"
                        title="Delete"
                        v-bind="attrs"
                        v-on="on"
                        @click="deleteItem(item)"
                      >
                        <v-icon x-small> mdi-delete </v-icon>
                      </v-btn>
                    </template>
                    <span>Remove</span>
                  </v-tooltip>
                </template>
              </v-data-table>

              <v-data-table
                v-else
                @click:row="handleClick"
                :headers="headers"
                style="cursor: pointer"
                :single-expand="singleExpand"
                :expanded.sync="expanded"
                :items="bytypelist.Customers"
                :page.sync="page"
                :items-per-page="size"
                @page-count="pageCount = $event"
                hide-default-footer
                class="elevation-0"
                sort-by="Customer.name"
              >
                <template v-slot:[`item.surveystatus`]="{ item }">
                  <v-chip :color="getColor(item.surveystatus)" dark>
                    {{ item.surveystatus }}
                  </v-chip>
                </template>
              </v-data-table>
              <v-row class="justify-end" v-if="isSurvey">
                <v-col cols="12" md="4" class="justify-center">
                  <v-pagination
                    v-model="page"
                    class="my-4"
                    v-on:change="change()"
                    :total-visible="13"
                    :length="totaldatasurvey"
                  >
                  </v-pagination>
                </v-col>
                <v-col cols="12" md="1" class="select">
                  <v-select
                    label="Survey per page"
                    :value="size"
                    type="number"
                    :items="row"
                    dense
                    outlined
                    v-on:change="change()"
                    @input="size = parseInt($event, 10)"
                  ></v-select>
                </v-col>
              </v-row>
              <v-row class="justify-end" v-else>
                <v-col cols="12" md="4" class="justify-center">
                  <v-pagination
                    :total-visible="7"
                    v-model="page"
                    class="my-4"
                    v-on:change="changeType()"
                    :length="totaldata"
                  >
                  </v-pagination>
                </v-col>
                <v-col cols="12" md="1" class="select">
                  <v-select
                    label="Survey per page"
                    :value="size"
                    type="number"
                    :items="row"
                    dense
                    outlined
                    v-on:change="changeType()"
                    @input="size = parseInt($event, 10)"
                  ></v-select>
                </v-col>
              </v-row>
            </v-card-text>
            <v-data-table
              :headers="headers3"
              :items="data_download"
              v-show="false"
            >
            </v-data-table>
          </v-card>
        </v-col>
      </v-row>
    </v-row>
    <form-survey
      :type="type"
      v-if="isShowModal"
      :propssurvey="survey"
      @success="actionfrom"
    />
    <confirm
      @actionconfirm="deletesurvey"
      :id_survey="iddelete"
      v-if="confirmshow"
    />
    <detailsurvey
      v-if="detailshow"
      :propsdetailsurvey="rowClick"
      @close="actionsurvey"
    />
    <!-- <survey-tab :propsid_customer="id_customer" /> -->
    <!-- <survey-tab /> -->
    <sukses :alert="dialogsukses" :type="typealert" />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
import Detailsurvey from "./Detailsurvey.vue";
import FormSurvey from "../components/form/FormSurvey.vue";
// import SurveyTab from "../components/Survey_tab.vue";
import Sukses from "./alert/Sukses.vue";
import Confirm from "../components/popup/Confirm.vue";

export default {
  components: { Confirm, Sukses, FormSurvey, Detailsurvey },
  data: () => ({
    isSurvey: false,
    logo: "",
    search: "",
    detailshow: false,
    dialogsukses: false,
    typealert: "Congratulations",
    isShowModal: false,
    confirmshow: false,
    row: [5, 10, 15, 20, 25],
    page: 1,
    size: 10,
    menu: false,
    pageCount: 0,
    totaldata: 0,
    totaldatasurvey: 0,
    id_customer: "",
    arrowback: false,
    periode: [
      "DAILY",
      "WEEKLY",
      "MONTHLY",
      "QUARTERLY",
      "SEMESTER",
      "ANNUAL/YEARLY",
    ],
    content: false,
    card1: 0,
    loading: true,
    titles: "Survey",
    tipesurveys: ["SATISFACTION", "EXIT"],
    langganan: ["YES", "NO"],
    year: "ALL",
    subscription: "ALL",
    iddelete: "",
    period: "ALL",
    status: "ALL",
    workspace: "",
    sortBy: "",
    more: false,
    down: "",
    listcustomer: [],
    type: "",
    // itemtype: [],
    items: ["survey"],
    expanded: [],
    headers: [
      { text: "CUSTOMER NAME", value: "name", align: "start" },
      { text: "INSTITUTION", value: "nameinstitusi", align: "start" },
      { text: "ADDRESS", value: "address" },
      {
        text: "EMAIL",
        value: "email",
      },
      {
        text: "STATUS",
        value: "surveystatus",
      },
    ],
    headers3: [
      { text: "No.", value: "No", align: "start" },
      { text: "Contract", value: "Contract", align: "start" },
      { text: "Customer Name", value: "Customer_Name" },
      { text: "Contact Person", value: "Contact_Person" },
      { text: "Alias Name", value: "Alias_Name" },
      { text: "Email", value: "Email" },
      { text: "Phone", value: "Phone" },
      { text: "Institution", value: "Institution" },
      { text: "Client", value: "Client" },
      { text: "Score 1", value: "Score_1" },
      { text: "Score 2", value: "Score_2" },
      { text: "Score 3", value: "Score_3" },
      { text: "Score 4", value: "Score_4" },
      { text: "Suggestion", value: "Suggestion" },
      { text: "Subscription", value: "Subscription" },
      { text: "Date", value: "Date" },

      // { text: "ACTION", value: "actions", sortable: false },
    ],
    header2: [
      { text: "DATE SURVEY", value: "date_filled" },
      { text: "SUBSCRIPTION", value: "subscription" },
      { text: "TYPE ", value: "type" },

      { text: "CONTRACT", value: "contract" },

      { text: "ACTION", value: "actions", sortable: false },
    ],

    type: "",
    survey: {},
    worskpace: "",
    typesurvey: "ALL",
    id_workspace: [],
    singleExpand: false,
    totalfilled: "",
    kode_fr_survey: "",
    data_download: [],
  }),

  computed: {
    ...mapGetters("survey", [
      "surveylist",
      "bytypelist",
      "yearlist",
      "bysurveycustomer",
      "bydownload",
    ]),
    ...mapState("survey", ["allbytype", "surveydownload"]),
    formTitle() {
      return this.type === "Create" ? "Create" : "Edit";
    },

  },
  watch: {
    page() {
      this.onfilter();
    },
    search() {
      this.onfilter();
    },
  },

  async created() {
    await this.fetchYear();
    await this.backtosurvey();
    this.kode_fr_survey = localStorage.getItem("kode_fr_survey");
    this.workspace = localStorage.getItem("myworkspace");
  },
  methods: {
    ...mapActions("survey", [
      "fetchSurvey",
      "fetchYear",
      "updateSurvey",
      "SubByIdSurvey",
      "deleteSurveyID",
      "SurveySearch",
      "SurveyFilter",
      "SurveyCustomer",
      "SurveyDownload",
    ]),

    ClickDetail(item) {
      this.detailshow = true;
      this.rowClick = item;
    },

    handleClick(item) {
      console.log(item,"coba klik");
      this.page = 1;
      this.id_customer = item.id_customer;
      this.onfiltersurvey();
      this.isSurvey = true;
      this.arrowback = true;
    },
    submit() {
      this.menu = false;
      this.onfilter();
    },
    changeType() {
      this.page = 1;
      this.onfilter();
    },
    change() {
      this.page = 1;
      this.onfiltersurvey();
    },
    async onfilter() {
      this.loading = true;
      if (this.typesurvey == null) {
        this.typesurvey = "ALL";
      }
      if (this.year == null) {
        this.year = "ALL";
      }
      if (this.subscription == null) {
        this.subscription = "ALL";
      }

      if (this.period == null) {
        this.period = "ALL";
        this.status = "ALL";
      }
      const prm = {
        name: this.search,
        size: this.size,
        page: this.page,
        type: this.typesurvey,
        year: this.year,
        subscription: this.subscription,
        id_workspace: localStorage.getItem("myworkspace"),
        period: this.period,
        status: this.status,
      };
      await this.SurveyFilter(prm);
      await this.SurveyDownload(prm);

      const data_download = this.surveydownload.Surveys;
      console.log("down1", data_download);
      this.data_download = [];
      for (let x in data_download) {
        this.data_download.push({
          No: this.bydownload.Surveys[x].No,
          Contract: this.bydownload.Surveys[x].Contract,
          Customer_Name: this.bydownload.Surveys[x].Customer_Name,
          Contact_Person: this.bydownload.Surveys[x].Contact_Person,
          Alias_Name: this.bydownload.Surveys[x].Alias_Name,
          Email: this.bydownload.Surveys[x].Email,
          Phone: this.bydownload.Surveys[x].Phone,
          Institution: this.bydownload.Surveys[x].Institution,
          Client: this.bydownload.Surveys[x].Client,
          Score_1: this.bydownload.Surveys[x].Score_1,
          Score_2: this.bydownload.Surveys[x].Score_2,
          Score_3: this.bydownload.Surveys[x].Score_3,
          Score_4: this.bydownload.Surveys[x].Score_4,
          Suggestion: this.bydownload.Surveys[x].Suggestion,
          Subscription: this.bydownload.Surveys[x].Subscription,
          Date: this.bydownload.Surveys[x].Date,
        });
      }

      this.logo = this.allbytype.Utils.Logo;
      this.totaldata = this.allbytype.Utils.TotalPages;
      this.loading = false;
    },
    async onfiltersurvey() {
      this.loading = true;
      if (this.typesurvey == null) {
        this.typesurvey = "ALL";
      }
      if (this.year == null) {
        this.year = "ALL";
      }
      if (this.subscription == null) {
        this.subscription = "ALL";
      }
      if (this.period == null) {
        this.period = "ALL";
        this.status = "ALL";
      }

      const prm = {
        name: this.search,
        size: this.size,
        page: this.page,
        type: this.typesurvey,
        year: this.year,
        subscription: this.subscription,
        id_customer: this.id_customer,
        period: this.period,
        status: this.status,
      };
      await this.SurveyCustomer(prm);
      this.totaldatasurvey = this.bysurveycustomer.Utils.TotalPages;
      this.loading = false;
    },
    backtosurvey() {
      this.onfilter();
      this.isSurvey = false;
      this.arrowback = false;
    },

    addsurvey(item) {
      if (item != undefined) {
        this.survey = item;
        this.type = "Edit";
        this.isShowModal = !this.isShowModal;
        this.rowClick = !item;
      } else {
        this.isShowModal = !this.isShowModal;
        this.type = "Create";
        this.rowClick = !item;
      }
    },
    deleteItem(item) {
      this.iddelete = item.id_survey;
      this.confirmshow = true;
    },
    ExportExcel() {
      console.log(this.data_download, "data");
      const data = XLSX.utils.json_to_sheet(this.data_download);
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, data, "data");
      XLSX.writeFile(wb, "filled_survey.xlsx");
    },
    deletesurvey(data) {
      if (data.id_survey !== "") {
        this.deleteSurveyID(data.id_survey)
          .then((response) => {
            if (response.status == 200) {
              this.$emit("success");
              this.typealert = "Congratulations";
              this.dialogsukses = true;
              this.confirmshow = false;
              this.onfiltersurvey();
              this.onfilter();
            } else {
              this.typealert = "Failed !";
              this.dialogsukses = true;
              this.confirmshow = false;
            }
          })
          .catch((err) => {
            this.typealert = "Failed !";
            this.dialogsukses = true;
          });
      } else {
        this.confirmshow = false;
      }
    },
    async actionfrom(data) {
      this.isShowModal = false;
      this.loading = true;
      if (data.type == "create") {
        if (data.status == true) {
          this.dialogsukses = true;
          this.typealert = "Congratulations";
          this.loading = false;
        } else {
          this.dialogsukses = true;
          this.typealert = "Failed !";
          this.loading = false;
        }
      } else {
        this.loading = false;
        setTimeout(() => {
          this.onfilter();
        }, 1000);
      }
    },
    async actionsurvey(data) {
      this.detailshow = false;
      this.rowClick = [];
      if (data.type == "save") {
        if (data.status == true) {
          this.typealert = "Congratulations";
          this.dialogsukses = true;
          this.onfilter();
        } else {
          this.typealert = "Failed !";
          this.dialogsukses = true;
        }
      }
    },
    getColor(surveystatus) {
      let text = surveystatus;
      const myArray = text.split("/");
      let x = parseInt(myArray[0]);
      let y = parseInt(myArray[1]);

      if (x / y == 1) {
        return "green";
      } else {
        return "red";
      }
    },
    //   if (item === '0/1') {
    //     return 'red'
    //     } else return 'green';
    // },
  },
};
</script>

<style>
#virtual-scroll-table {
  max-height: 400px;
  overflow: auto;
}
.select {
  padding-top: 30px;
}

.v-progress-circular {
  margin: 1rem;
}
</style>
