<template>
  <div>
    <v-row class="ml-4 my-4 pr-10">
      <v-breadcrumbs
        style="padding-left: unset;margin-left: 10px;}"
        divider=">"
      ></v-breadcrumbs>
      <v-col cols="12">
        <div>
          <v-row class="align-bottom">
            <v-col cols="12" class="">
              <h1 class="Montserrat">
                {{ titles }}
              </h1>
            </v-col>
          </v-row>

          <v-row class="justify-end">
            <v-col class="justify-end" cols="12" md="3">
              <v-text-field
               class="d-flex justify-end"
                v-model="search"
                prepend-inner-icon="mdi-magnify"
                label="Search here"
                filled
                rounded
                dense
                single-line
                hide-details
                v-on:change="changeType()"
                @click:prepend-inner="changeType()"
              ></v-text-field>
            </v-col>
            <v-col cols="12 " md="2" class="d-flex justify-end">
              <v-btn
                class="text-capitalize"
                rounded
                depressed
                color="primary "
                @click="addsubinstitusi()"
              >
                Create Data</v-btn
              >
            </v-col>
          </v-row>
        </div>
      </v-col>
      <v-row>
        <v-col cols="12" class="mx-3 my-2">
          <v-card class="shadow rounded-card pr-1" :loading="loading">
            <v-card-text>
              <v-data-table
                style="cursor: pointer"
                :headers="headers"
                :items="subinstitusilist.SubInstitution"
                hide-default-footer
                :page.sync="page"
                :items-per-page="size"
                @page-count="pageCount = $event"
                class="elevation-0"
              >
                <template v-slot:[`item.actions`]="{ item }">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        v-bind="attrs"
                        v-on="on"
                        fab
                        dense
                        dark
                        class="mx-1 my-2"
                        x-small
                        color="primary"
                        @click="addsubinstitusi(item)"
                      >
                        <v-icon x-small> mdi-pencil </v-icon>
                      </v-btn>
                    </template>
                    <span>Edit</span>
                  </v-tooltip>
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        v-bind="attrs"
                        v-on="on"
                        fab
                        dense
                        dark
                        x-small
                        color="red"
                        @click="deleteItem(item)"
                      >
                        <v-icon fab dark x-small> mdi-delete </v-icon>
                      </v-btn>
                    </template>
                    <span>Remove </span>
                  </v-tooltip>
                </template>
              </v-data-table>

              <v-row class="justify-end">
                <v-col cols="12" md="4" class="justify-center">
                  <v-pagination
                    v-model="page"
                    class="my-4"
                    v-on:change="changeType()"
                    :length="totaldata"
                    :total-visible="7"
                  >
                  </v-pagination>
                </v-col>
                <v-col cols="12" md="1" class="select">
                  <v-select
                    label="Row per page"
                    :value="size"
                    type="number"
                    :items="row"
                    dense
                    outlined
                    v-on:change="changeType()"
                    @input="size = parseInt($event, 10)"
                  ></v-select>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-row>
    <form-sub-institusi
      @success="actionSubInstitusi"
      :propssubinstitusi="subinstitusi"
      :type="type"
      v-if="isShowModal"
    />
    <confirm
      @actionconfirm="deletesubinstitut"
      :id_subinstitusi="iddelete"
      v-if="confirmshow"
    />
    <sukses
      :alert="dialogsukses"
      :type="typealert"
      @close="dialogsukses = false"
    />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
import Sukses from "../components/alert/Sukses.vue";
import FormSubInstitusi from "../components/form/FormSubInstitusi.vue";
// import TambahEdit from "../components/form/TambahEdit.vue";
// import FormWorkspace from "../components/form/FormWorkspace.vue";
import Confirm from "../components/popup/Confirm.vue";
// import TambahEdit from "../components/form/TambahEdit.vue";
export default {
  components: { Confirm, FormSubInstitusi, Sukses },
  data: () => ({
    loading: true,
    search: "",
    row: [5, 10, 15, 20, 25],
    page: 1,
    size: 5,
    pageCount: 0,
    totaldata: 0,
    dialogsukses: false,
    typealert: "Congratulations",
    isShowModal: false,
    confirmshow: false,
    content: false,
    card1: 0,
    titles: "SubInstitusi",
    iddelete: "",
    workspace: "",

    items: ["subinstitusi"],
    headers: [
      // { text: "id_workspace", value: "id_workspace" },
      { text: "INSTITUTION", value: "institusi.name" },
      { text: "SUBINSTITUTION", value: "name" },
      { text: "DESCRIPTIONS", value: "keterangan" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    type: "",
    subinstitusi: {},
  }),
  computed: {
    ...mapGetters("subinstitusi", ["subinstitusilist"]),
    formTitle() {
      return this.type === "Create Data" ? "Create Data" : "Edit Data";
    },
    // pageCounts() {
    //   return Math.ceil(this.totaldata / this.size);
    // },
  },
  watch: {
    page() {
      this.datasub();
    },
    search() {
      this.datasub();
    },
  },
  async created() {
    await this.datasub();

    // this.listworkspace = this.workspacelist;
  },

  methods: {
    ...mapActions("subinstitusi", [
      "addSubInstitusi",
      "deleteSubInstitusi",
      "fetchSubInstitusi",
    ]),

    addsubinstitusi(item) {
      if (item != undefined) {
        this.subinstitusi = item;
        this.type = "Edit Data";
        this.isShowModal = !this.isShowModal;
      } else {
        this.isShowModal = !this.isShowModal;
        this.type = "Create Data";
      }
    },
    async datasub() {
      this.loading = true;
      const prm = {
        name: this.search,
        size: this.size,
        page: this.page,
      };
      await this.fetchSubInstitusi(prm);
      this.workspace = localStorage.getItem("myworkspace");

      this.totaldata = this.subinstitusilist.TotalPages;
      this.loading = false;
    },
    changeType() {
      this.page = 1;
      this.datasub();
    },
    deleteItem(item) {
      this.iddelete = item.id_subinstitusi;

      this.confirmshow = true;
    },
    deletesubinstitut(data) {
      if (data.id_subinstitusi !== "") {
        this.deleteSubInstitusi(data.id_subinstitusi).then((response) => {
          if (response.status == 200) {
            this.$emit("success");
            this.typealert = "Congratulations";
            this.dialogsukses = true;
            this.confirmshow = false;
            this.datasub();
            this.loading = false;
          } else {
            // alert("asdasd");
            this.typealert = "Failed !";
            this.dialogsukses = true;
            this.confirmshow = false;
          }
        });
      } else {
        this.confirmshow = false;
      }
    },
    async actionSubInstitusi(data) {
      this.isShowModal = false;
      this.loading = true;
      if (data.type == "create") {
        if (data.status) {
          this.dialogsukses = true;
          this.typealert = "Congratulations";
          setTimeout(async () => {
            await this.datasub();
          }, 1000);
          this.loading = false;
        } else {
          this.dialogsukses = true;
          this.typealert = "Failed !";
        }
      } else {
        this.loading = false;
        setTimeout(() => {
          this.datasub();
        }, 1000);
      }
    },
  },
};
</script>

<style scoped>
.rounded-card {
  border-radius: 30px;
}
.select {
  padding-top: 30px;
}
</style>
