import axios from "axios";
const token = sessionStorage.getItem('token-user')
const state = {
    workspace: [],
    allws: [],
    respon: {},
    listbyidworkspace: []
}
const getters = {
    workspacelist: state => state.workspace,
    workspacess: state => state.allws,
    workspacebyid: state => state.listbyidworkspace
}
const actions = {
    async fetchWorkspace({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const myworkspace = localStorage.getItem('id_user')
        const response = await axios.get(`/login-crm/AllWorkspace?page=${data.page}&size=${data.size}&name=${data.name}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("setWorkspace", response.data)
        console.log("get workspace", response.data);
    },
    async allWS({ commit }) {
        const token = sessionStorage.getItem('token-user')
        const myworkspace = localStorage.getItem('myworkspace')
        const response = await axios.get("/login-crm/AllWorkspace", {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("allWs", response.data)
        console.log("get workspace and form code", response.data);
    },
    async WorkspaceByIdUser({ commit }) {
        const myworkspace = localStorage.getItem('id_user')
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get(
          `/login-crm/WorkspaceByIdUser/${myworkspace}`,
          {
            headers: {
              Authorization: "Bearer " + token, //the token is a variable which holds the token
            },
          }
        );
        commit("workslistbyidworkspace", response.data)
        console.log("get workspace by ID", response.data);
    },
    async addWorkspace({ commit }, workspace) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.post("/login-crm/addData", workspace, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });

        commit("addNewWorkspace", response)
        return response;
    },
    async deleteWorkspace({ commit }, id_workspace) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.delete(`/login-crm/deleteWorkspace?id_workspace=${id_workspace}`, {
                headers: {
                    Authorization: 'Bearer ' + token //the token is a variable which holds the token
                }
            })
            // commit("removeWorkspace", id_workspace)
        console.log("removeWorkspace", response.data);
        return response;
    },
    async updateWorkspace({ commit }, data) {
        const response = await axios.put(`/login-crm/updateWorkspace?id_workspace=${data.id_workspace}`, data, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        return response;
    }


};

const mutations = {
    setWorkspace: (state, workspace) => (state.workspace = workspace),
    allWs: (state, allws) => (state.allws = allws),
    workslistbyidworkspace: (state, workslistbyidworkspace) => (state.listbyidworkspace = workslistbyidworkspace),
    addNewWorkspace: (state, workspace) => state.respon = workspace,

    // removeWorkspace: (state, id_workspace) => {
    //     state.workspace.filter(workspace => workspace.id_workspace == id_workspace),
    //         state.workspace.splice(workspace => workspace.id_workspace, 1)

    // },
    // updateWorkspace: (state, workspace) => (state.workspace = (workspace => workspace))
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}