import axios from "axios";
const token = sessionStorage.getItem('token-user')
const state = {
    subinstitusi: [],
    listbysub: [],
    listbyidins: [],

}
const getters = {

    subinstitusilist: state => state.subinstitusi,
    bysubinstitusi: state => state.listbysub,
    bysubidinstitusi: state => state.listbyidins
}
const actions = {
    async fetchSubInstitusi({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const myinstitusi = localStorage.getItem('id_user')
        const response = await axios.get(`/login-crm/AllSub?page=${data.page}&size=${data.size}&name=${data.name}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        // return response
        commit("setSubInstitusi", response.data)
        console.log("SUBInstitusi store", response.data);
    },
    async SubInstitusiById({ commit }) {
        const token = sessionStorage.getItem('token-user')
        const myworkspace = localStorage.getItem('id_user')
        const response = await axios.get(`/login-crm/SubById/${myworkspace}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("bySubinstitusi", response.data)
        console.log("get institusi by ID", response.data);
    },
    async SubByIdInstitusi({ commit }, idsub) {
        const token = sessionStorage.getItem('token-user')
        const myworkspace = localStorage.getItem('id_user')
        const response = await axios.get(`/login-crm/SubByIdInstitus/${idsub}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("byIdInstitusi", response.data)
            // console.log("get institusi by ID", response.data);
    },
    async addSubInstitusi({ commit }, subinstitusi) {
        const token = sessionStorage.getItem('token-user')

        const response = await axios.post("/login-crm/AddSub  ", subinstitusi, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        return response;
    },
    async updateSubInstitusi({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        return axios.put(`/login-crm/updateSub?id_subinstitusi=${data.id_subinstitusi}`, data, {
            headers: {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        // console.log("Update", response.data);
        // return response;
    },

    async deleteSubInstitusi({ commit }, id_subinstitusi) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.delete(`/login-crm/deleteSub?id_subinstitusi=${id_subinstitusi}`, {
                headers: {
                    Authorization: 'Bearer ' + token //the token is a variable which holds the token
                }
            })
            // commit("removeSubInstitusi", id_subinstitusi)
            // console.log("removeInstitusi", response.data);
        return response;
    },

};

const mutations = {
    setSubInstitusi: (state, subinstitusi) => (state.subinstitusi = subinstitusi),
    bySubinstitusi: (state, listbysub) => (state.listbysub = listbysub),
    byIdInstitusi: (state, listbyidins) => (state.listbyidins = listbyidins),

    // removeSubInstitusi: (state, id_subinstitusi) => {
    //     state.subinstitusi.filter(subinstitusi => subinstitusi.id_subinstitusi == id_subinstitusi),
    //         state.subinstitusi.splice(subinstitusi => subinstitusi.id_subinstitusi, 1)

    // },
    // updateWorkspace: (state, workspace) => (state.workspace = (workspace => workspace))
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}