import axios from "axios";
const token = sessionStorage.getItem('token-user')

const state = {
    customer: [],
    respon: {},
    customerlistbyidworkspace: [],
    listbyidins: [],
};

const getters = {
    customerlist: state => state.customer,
    bysubidinstitusi: state => state.listbyidins,
    listbyidworkspace: state => state.customerlistbyidworkspace
};
const actions = {
    async fetchCustomers({ commit }) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get("/login-crm/customers", {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("setCustomers", response.data)

    },
    async customerByIdInstitusi({ commit }, idins) {


        const token = sessionStorage.getItem('token-user')
        const myworkspace = localStorage.getItem('id_user')
        const response = await axios.get(`/login-crm/customerByIdInstitusi/${idins}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("byIdInstitusi", response.data)
            // console.log("get institusi by ID", response.data);
    },
    async CustomersByidWorkspace({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const myworkspace = localStorage.getItem('myworkspace')
        const response = await axios.get(`/login-crm/CustomerByIdWorkspace?id_workspace=${data.id_workspace}&name=${data.name}&page=${data.page}&size=${data.size}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("customerlistbyidworkspaces", response.data)
        console.log("get tabel customer by ID workspace ", response.data);
    },
    async addCustomer({ commit }, customer) {
        const token = sessionStorage.getItem('token-user')
            // alert('create');
            // console.log('addUsers', customer)
        const response = await axios.post("/login-crm/addCustomers", customer, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("addNewCustomer", response)
            // console.log('data tambah', response)

    },

    async deleteCustomerbyID({ commit }, id_customer) {
        const token = sessionStorage.getItem('token-user')
        console.log('delete', id_customer)
        const respon = await axios.delete(`/login-crm/deleteCustomer/${id_customer}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("removeCustomer", id_customer)
        return respon;
    },
    async updateCustomer({ commit }, data) {

        const token = sessionStorage.getItem('token-user')
        console.log('customer', data)
        const response = await axios.put(`/login-crm/updateCustomer?id_customer=${data.id_customer}`, data, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });

        return response;
    }

};

const mutations = {
    setCustomers: (state, customer) => (
        state.customer = customer
    ),
    byIdInstitusi: (state, listbyid) => (state.listbyidins = listbyid),
    customerlistbyidworkspaces: (state, customerbyid) => (
        state.customerlistbyidworkspace = customerbyid
    ),
    addNewCustomer: (state, customer) => state.respon = customer,
    removeCustomer: (state, id_customer) => {
        state.customer.filter(customer => customer.id_customer == id_customer),
            state.customer.splice(customer => customer.id_customer, 1)
    },
    // updateCustomer: (state, customer) => (
    //     state.customer = (customer => customer)
    // )
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}