<template>
  <v-dialog persistent v-model="dialog" content-class="alert-success">
    <v-card>
      <v-card-text>
        <v-container>
          <v-row align="center" justify="center">
            <v-col cols="12 pa-12 d-flex justify-center text-center ">
              <div>
                <v-img width="80px" src="@/static/images/excla.png" />
              </div>
            </v-col>
            <v-spacer />
          </v-row>
        </v-container>
        <v-container>
          <v-row class="d-flex align-center text-center">
            <v-col>
              <div>
                <div>
                  <h1 class="h6 text--white" color="text-grey darken-4">
                    Are You Sure ?
                  </h1>
                </div>
              </div>
            </v-col>
          </v-row>
          <v-row align="center" justify="center">
            <v-col cols="12 pa-1 d-flex justify-center text-center ">
              <div>
                <div>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                      color="blue darken-1"
                      text
                      @click="closeDelete"
                      depressed
                      >Cancel</v-btn
                    >
                    <v-btn
                      :loder="loder"
                      @click="deleteItemConfirm"
                      depressed
                      color="primary"
                      >OK</v-btn
                    >
                  </v-card-actions>
                  <v-spacer></v-spacer>
                </div>
              </div>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>
<script>
export default {
  props: [
    "id_customer",
    "id_user",
    "id_workspace",
    "id_keluhan",
    "id_workorder",
    "id_institusi",
    "id_subinstitusi",
    "id_period",
    "id_subperiod",
    "id_reporting",
    "id_survey",
  ],
  data() {
    return {
      loder: false,
      dialog: true,
      resolve: null,
      reject: null,
    };
  },
  methods: {
    deleteItemConfirm() {
      this.loder = true;
      // console.log("id customer", this.id_customer);
      // console.log("id user", this.id_user);
      // console.log("id workspace", this.id_workspace);
      this.$emit("userdelete", this.id_user);
      this.$emit("workspacedelete", this.id_workspace);
      this.$emit("keluhandelete", this.id_keluhan);
      this.$emit("institusidelete", this.id_institusi);
      this.$emit("subinstitusidelete", this.id_subinstitusi);
      this.$emit("periodidelete", this.id_period);
      this.$emit("subperiodidelete", this.id_subperiod);
      this.$emit("reportingidelete", this.id_reporting);
      const data = {
        id: this.id_customer,
        id_keluhan: this.id_keluhan,
        id_user: this.id_user,
        id_workspace: this.id_workspace,
        id_workorder: this.id_workorder,
        id_institusi: this.id_institusi,
        id_subinstitusi: this.id_subinstitusi,
        id_period: this.id_period,
        id_subperiod: this.id_subperiod,
        id_reporting: this.id_reporting,
        id_survey: this.id_survey,
      };
      this.$emit("actionconfirm", data);
      this.dialog = false;
      this.loder = false;
    },
    closeDelete() {
      const data = {
        id: "",
        id_workspace: "",
        id_keluhan: "",
        id_user: "",
        id_workorder: "",
        id_institusi: "",
        id_subinstitusi: "",
        id_period: "",
        id_subperiod: "",
        id_reporting: "",
        id_survey: "",
        status: false,
      };
      this.$emit("actionconfirm", data);
      this.dialog = false;
    },
  },
};
</script>
<style  scoped>
.rounded-card {
  border-radius: 16px;
}
</style>