<template>
  <v-dialog
    v-model="dialog"
    persistent
    content-class="detail-dialog"
    scrollable
  >
    <v-card class="rounded-card">
      <v-card-title class="mx-6 toolbar-form">
        <v-spacer></v-spacer>
        Detail Survey
        <v-spacer></v-spacer>
        <v-btn icon @click="close()">
          <v-icon> mdi-close </v-icon>
        </v-btn>
      </v-card-title>

      <v-spacer />
      <v-divider></v-divider>
      <!-- </v-row> -->
      <!-- <v-subheader class="font-weight-bold">DETAIL</v-subheader> -->
      <v-card-text>
        <div>
          <v-row class="pt-6">
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">Customer Name</h3>
              <p class="font-italic">Nama Pelanggan</p>
            </v-col>
            <v-col cols="12" md="6" sm="12" class="">
              <h3 class="h3-props">
                {{ propsdetailsurvey.custname_filled }}
              </h3>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">Company / Institution</h3>
              <p class="font-italic">Perusahaan / Institusi</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">
                {{ propsdetailsurvey.id_institusi.name }}
              </h3>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">Address</h3>
              <p class="font-italic">Alamat</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">
                {{ propsdetailsurvey.address_filled }}
              </h3>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">Date</h3>
              <p class="font-italic">Tanggal</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">
                {{ propsdetailsurvey.date_filled }}
              </h3>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">Contract</h3>
              <p class="font-italic">Kontrak</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">
                {{ propsdetailsurvey.contract }}
              </h3>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">Type</h3>
              <p class="font-italic">Tipe</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props" v-if="type != 'SATISFACTION'">
                <v-chip :color="getColorstype(propsdetailsurvey.type)" dark>
                  {{ propsdetailsurvey.type }}
                </v-chip>
                <!-- {{ propsdetailsurvey.type }} -->
              </h3>
            </v-col>
          </v-row>
          <v-row class="">
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">Email</h3>
              <p class="font-italic">Email</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">
                {{ propsdetailsurvey.email_filled }}
              </h3>
            </v-col>
          </v-row>
          <v-row class="">
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">No Telp</h3>
              <p class="font-italic">No Telp</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">
                {{ propsdetailsurvey.phone_filled }}
              </h3>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">How Product Functions ?</h3>
              <p class="font-italic">Bagaimana Fungsi Produk ?</p>
            </v-col>

            <v-col cols="12" md="6" sm="12"
              ><h3 class="h3-props">{{ propsdetailsurvey.score1 }}/10</h3>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">
                How is the timeliness of product completion ?
              </h3>

              <p class="font-italic">
                Bagaimana ketepatan waktu penyelesaian produk ?
              </p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">{{ propsdetailsurvey.score2 }}/10</h3></v-col
            >
          </v-row>
          <v-row>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">How is the smooth communication with us ?</h3>
              <p class="font-italic">
                Bagaimana kelancaran komunikasi dengan kami ?
              </p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">{{ propsdetailsurvey.score3 }}/10</h3></v-col
            >
          </v-row>
          <v-row>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">How do we charge the price?</h3>
              <p class="font-italic">Bagaimana harga yang kami tetapkan ?</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">{{ propsdetailsurvey.score4 }}/10</h3>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">do you want to continue cooperation?</h3>
              <p class="font-italic">
                Apakah anda ingin melanjutkan kerja sama ?
              </p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">
                {{ propsdetailsurvey.subscription }}
              </h3>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">Suggestions</h3>
              <p class="font-italic">Saran</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <v-textarea
                disabled
                outlined
                dense
                v-model="editedItem.keterangan"
              >
              </v-textarea>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">Link</h3>
              <p class="font-italic">Link</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <v-text-field
                outlined
                dense
                append-icon=" mdi-content-copy"
                v-model="editedItem.link"
                @click:append="doCopy()"
              >
              </v-text-field>
            </v-col>
          </v-row>
          <!-- 
          <v-chip color="indigo" text-color="white">
            <v-avatar small left> <v-icon>mdi-account-circle</v-icon> </v-avatar
            >Filled by :
          </v-chip>

          <v-row class="pt-4">
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">Name</h3>
              <p class="font-italic">Nama</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">
                {{ propsdetailsurvey.custname_filled }}
              </h3>
            </v-col>
          </v-row>
          <v-row class="">
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">Email</h3>
              <p class="font-italic">Email</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">
                {{ propsdetailsurvey.email_filled }}
              </h3>
            </v-col>
          </v-row>
          <v-row class="">
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-text">No Telp</h3>
              <p class="font-italic">No Telp</p>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <h3 class="h3-props">
                {{ propsdetailsurvey.phone_filled }}
              </h3>
            </v-col>
          </v-row> -->
        </div>

        <get-link
          v-if="showlink"
          :propslink="editedItem"
          @close="dialogGen = false"
          :dialog="dialogGen"
        />
      </v-card-text>
    </v-card>
  </v-dialog>
</template>
                
<script>
import { mapActions, mapGetters } from "vuex";
import GetLink from "./popup/GetLink.vue";
export default {
  props: ["propsdetailsurvey", "type"],
  components: { GetLink },
  data() {
    return {
      email_filled: "",
      phone_filled: "",
      custname_filled: "",
      showlink: false,
      selectedFiles: [],
      selectedImages: [],
      isSelecting: false,
      statusitem: ["ACCEPT", "DECLINE", "NEW"],
      marker: true,
      dialog: true,
      resolve: null,
      showlink: true,
      reject: null,
      link: "",
      data: "",
      date: "",
      editedItem: {
        // title: "",
        // status: "",
        // link: "",
        // perbaikan: "",
        // file_customer: "",
        // download: "",
        score1: "",
        score2: "",
        score3: "",
        contract: "",
        score4: "",
        keterangan: "",
        link: "",
        status: "",
        type: "",
        period: [],
        subperiod: [],
        customer: [],
        file_customer: "",
      },
      dialogGen: false,
    };
  },

  mounted() {
    this.editedItem.score1 = this.propsdetailsurvey.score1;
    this.editedItem.score2 = this.propsdetailsurvey.score2;
    this.editedItem.score3 = this.propsdetailsurvey.score3;
    this.editedItem.score4 = this.propsdetailsurvey.score4;
    this.editedItem.contract = this.propsdetailsurvey.contract;
    this.editedItem.keterangan = this.propsdetailsurvey.explanation;
    this.editedItem.type = this.propsdetailsurvey.type;
    this.email_filled = this.propsdetailsurvey.email_filled;
    this.phone_filled = this.propsdetailsurvey.phone_filled;
    this.custname_filled = this.propsdetailsurvey.custname_filled;
  },

  computed: {
    // ...mapGetters("keluhan", ["keluhanlist"]),
  },
  async created() {
    // alert("ff");
    // await this.fetchKeluhan();
    await this.SubByIdSurvey({
      id: this.propsdetailsurvey.id_survey,
      token: localStorage.getItem("token-user"),
    }).then((response) => {
      console.log("response", response);
      this.editedItem.link = `https://ebcrm.blackeye.id/#/confirm-survey/${response.data.link}`;
      // this.editedItem.link = `http://localhost:8080/#/konfirmasi/survey/${response.data.link}`;
    });
  },

  methods: {
    ...mapActions("survey", ["fetchSurvey", "SubByIdSurvey", "Generated"]),
    close() {
      // alert("close");
      // this.dialog = false;
      let data = {
        type: "close",
      };
      this.$emit("close", data);
    },
    getColorstype(type) {
      if (type == "SATISFACTION") return "green";
      // else if (type == "EXIT") return "orange";
      else return "red";
    },
    doCopy: function () {
      let link;
      this.$copyText(this.editedItem.link).then(
        function (e) {
          link = e.text;

          navigator.clipboard.writeText(e.text);
          alert("copied success !!");
        },
        function (e) {
          alert("copied failed !!");
        }
      );
    },

    async generate() {
      let res = await this.Generated({
        idsurvey: this.propsdetailsurvey.id_survey,
      });
      // .then((response) => {

      //   // this.$emit("linkgen", this.editedItem);
      // })
      // .catch((err) => {});

      // this.editedItem.link = `http:///192.168.71.249:8080/#/konfirmasi/keluhan/${res.data.Keluhan}/${res.data.Token}`;
      this.editedItem.link = `https://ebcrm.blackeye.id/#/confirm-survey/${res.data.link}`;
      this.showlink = true;
      this.dialogGen = true;
      // this.$emit("close", false);
    },
    getColor(propsdetail) {
      if (propsdetail.status == "Done") return "green";
      else if (propsdetail.status == "progress") return "orange";
      else return "red";
    },
  },
};
</script>
<style  scoped>
.rounded-card {
  border-radius: 30px;
}
.select {
  background: orange;
}
</style>