<template>
  <div>
    <v-alert
      v-model="warning"
      text
      dismissible
      prominent
      type="error"
      icon="mdi-message-alert"
    >
      {{ text_alert }}
    </v-alert>
    <v-row class="ml-4 my-4 pr-10">
      <!-- <v-breadcrumbs
        style="padding-left: unset;margin-left: 10px;}"
        divider=">"
        :items="items"
      ></v-breadcrumbs> -->
      <v-col cols="12">
        <div>
          <v-row class="align-bottom">
            <v-col cols="12" class="">
              <h1 class="Montserrat">
                {{ titles }}
              </h1>
            </v-col>
          </v-row>

          <v-row class="justify-end">
            <v-col class="justify-end" cols="12" md="3">
              <v-text-field
                v-model="search"
                prepen
                prepend-inner-icon="mdi-magnify"
                label="Search here"
                filled
                rounded
                dense
                single-line
                v-on:change="changeType()"
                @click:prepend-inner="changeType()"
                hide-details
              ></v-text-field>
            </v-col>
            <v-col cols="12 " md="2">
              <v-btn
                class="text-capitalize"
                rounded
                depressed
                color="primary "
                @click="addinstitusi()"
                
              >
                Create Institusi</v-btn
              >
            </v-col>
          </v-row>
        </div>
      </v-col>

      <v-row>
        <v-col cols="12" class="mx-3 my-2">
          <v-card class="shadow rounded-card pr-1" :loading="loading">
            <v-card-text>
              <v-data-table
                style="cursor: pointer"
                :headers="headers"
                :items="institusilist.Institution"
                hide-default-footer
                :page.sync="page"
                :items-per-page="size"
                @page-count="pageCount = $event"
                class="elevation-0"
              >
                <template v-slot:[`item.actions`]="{ item }">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        v-bind="attrs"
                        v-on="on"
                        fab
                        dense
                        class="mx-1 my-2"
                        dark
                        x-small
                        color="primary"
                        @click="addinstitusi(item)"
                      >
                        <v-icon x-small> mdi-pencil </v-icon>
                      </v-btn>
                    </template>
                    <span>Edit</span>
                  </v-tooltip>
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        v-bind="attrs"
                        v-on="on"
                        fab
                        dense
                        dark
                        x-small
                        color="red"
                        @click="deleteItem(item)"
                      >
                        <v-icon fab dark x-small> mdi-delete </v-icon>
                      </v-btn>
                    </template>
                    <span>Remove </span>
                  </v-tooltip>
                </template>
              </v-data-table>

              <v-row class="justify-end">
                <v-col cols="12" md="4" class="justify-center">
                  <v-pagination
                    v-model="page"
                    class="my-4"
                    v-on:change="changeType()"
                    :length="totaldata"
                    :total-visible="7"
                  >
                  </v-pagination>
                </v-col>
                <v-col cols="12" md="1" class="select">
                  <v-select
                    label="Row per page"
                    :value="size"
                    type="number"
                    :items="row"
                    dense
                    outlined
                    v-on:change="changeType()"
                    @input="size = parseInt($event, 10)"
                  ></v-select>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-row>
    <form-institusi
      @success="actionInstitusi"
      :propsinstitusi="institusi"
      :type="type"
      v-if="isShowModal"
    />
    <confirm
      @actionconfirm="deleteinstitut"
      :id_institusi="iddelete"
      v-if="confirmshow"
    />
    <sukses
      :alert="dialogsukses"
      :type="typealert"
      @close="dialogsukses = false"
    />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
import Sukses from "../components/alert/Sukses.vue";
import FormInstitusi from "../components/form/FormInstitusi.vue";
// import TambahEdit from "../components/form/TambahEdit.vue";
// import FormWorkspace from "../components/form/FormWorkspace.vue";
import Confirm from "../components/popup/Confirm.vue";
// import TambahEdit from "../components/form/TambahEdit.vue";
export default {
  components: { Confirm, FormInstitusi, Sukses },
  data: () => ({
    text_alert: "",
    loading: true,
    warning: false,
    search: "",
    row: [5, 10, 15, 20, 25],
    page: 1,
    size: 5,
    pageCount: 0,
    totaldata: 0,
    dialogsukses: false,
    typealert: "Congratulations",
    isShowModal: false,
    confirmshow: false,
    content: false,
    card1: 0,
    workspace: "",
    titles: "Institusi",
    iddelete: "",

    items: ["institusi"],
    headers: [
      // { text: "id_workspace", value: "id_workspace" },
      { text: "NAME", value: "name" },
      { text: "DESCRIPTIONS", value: "keterangan" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    type: "",
    institusi: {},
  }),
  computed: {
    ...mapGetters("institusi", ["institusilist"]),
    // ...mapState("institusi", ["institusi"]),
    formTitle() {
      return this.type === "Create Institution"
        ? "Create Institution"
        : "Edit Institution";
    },
  },
  watch: {
    page() {
      this.datainst();
    },
    search() {
      this.datainst();
    },
  },
  async created() {
    await this.datainst();
  },

  methods: {
    ...mapActions("institusi", [
      "addInstitusi",
      "deleteInstitusi",
      "fetchInstitusi",
    ]),

    addinstitusi(item) {
      if (item != undefined) {
        console.log(item, "item");
        this.institusi = item;
        this.type = "Edit Institution";
        this.isShowModal = !this.isShowModal;
      } else {
        this.isShowModal = !this.isShowModal;
        this.type = "Create Institution";
      }
    },

    async datainst() {
      const prm = {
        name: this.search,
        size: this.size,
        page: this.page,
      };
      await this.fetchInstitusi(prm);
      this.workspace = localStorage.getItem("myworkspace");

      this.totaldata = this.institusilist.TotalPages;
      this.loading = false;
    },
    changeType() {
      this.page = 1;
      this.datainst();
    },
    deleteItem(item) {
      this.iddelete = item.id_institusi;

      this.confirmshow = true;
    },
    deleteinstitut(data) {
      if (data.id_institusi !== "") {
        this.loading = true;
        this.deleteInstitusi(data.id_institusi)
          .then((response) => {
            if (response.status == 200) {
              this.$emit("success");
              this.typealert = "Congratulations";
              this.dialogsukses = true;
              this.confirmshow = false;
              this.datainst();
              this.loading = false;
            } else {
              this.typealert = "Failed !";
              this.dialogsukses = true;
              this.confirmshow = false;
            }
          })

          .catch((err) => {
            this.text_alert = err.response.data;
            this.warning = true;
            this.loading = false;
          });
        this.confirmshow = false;
      } else {
        this.confirmshow = false;
        this.loading = false;
      }
    },
    async actionInstitusi(data) {
      this.isShowModal = false;
      this.loading = true;
      if (data.type == "create") {
        if (data.status == true) {
          this.dialogsukses = true;
          this.typealert = "Congratulations";
          await this.datainst();
          this.loading = false;
        } else {
          this.dialogsukses = true;
          this.typealert = "Failed !";
        }
      } else {
        this.loading = false;
        setTimeout(() => {
          this.datainst();
        }, 1000);
      }
    },
  },
};
</script>

<style scoped>
.rounded-card {
  border-radius: 30px;
}
.select {
  padding-top: 30px;
}
</style>
