// import Confirm from '../components/popup/Confirm.vue'
// import TambahEdit from '../components/form/TambahEdit.vue'
import Konfirmasikeluhan from '../components/form/Konfirmasikeluhan.vue'
import KonfirmasiReporting from '../components/form/KonfirmasiReporting.vue'
import Konfirmasisurvey from '../components/form/Konfirmasisurvey.vue'
import Konfirmasiworkorder from '../components/form/Konfirmasiworkorder.vue'
import RateComment from '../components/popup/RateComment.vue'
import Detailkeluhan from '../components/Detailkeluhan.vue'

import Vue from 'vue'
import VueRouter from 'vue-router'
import dashboard from '../components/Dashboard.vue'
import UserManagement from '../components/UserManagement.vue'
import Workspace from '../components/Workspace.vue'
import Customer from '../components/Customer.vue'
import Institusi from '../components/Institusi.vue'
import SubsInstitusi from '../components/SubsInstitusi.vue'
import Keluhan from '../components/Keluhan.vue'
import Workorder from '../components/Workorder.vue'
import PageNotFound from "../components/PageNotFound.vue";
import Accepted from "../components/page/Accepted.vue";
import Survey from "../components/Survey.vue";
import Reporting from "../components/Reporting.vue";
import Period from "../components/Period.vue";
import SubPeriod from "../components/SubPeriod.vue";
import ComponentThanks from "../views/Thanks.vue";
import Login from "../views/Login.vue";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/login",
    name: "Login",
    component: Login,
  },
  { path: "*", component: PageNotFound },
  { path: "/Accepted", component: Accepted },
  {
    path: "/confirm-page",
    name: "confirmpage",
    component: ComponentThanks,
  },

  {
    path: "/konfirmasi/:link",
    name: "Konfirmasikeluhan",
    component: Konfirmasikeluhan,
    props: true,
  },
  {
    path: "/confirm-report/:link",
    name: "Konfirmasireporting",
    component: KonfirmasiReporting,
    props: true,
  },
  {
    path: "/confirm-survey/:link",
    name: "Konfirmasisurvey",
    component: Konfirmasisurvey,
    props: true,
  },
  {
    path: "/",
    name: "home",
    component: Home,
    meta: {
      requiresAuth: true,
    },
    beforeEnter(to, from, next) {
      let token = sessionStorage.getItem("token-user");
      if (to.name !== "Login" && token == null) next({ name: "Login" });
      else next();
    },
    children: [
      {
        path: "/dashboard",
        name: "dashboard",
        component: dashboard,
      },
      {
        path: "/workspace",
        name: "Workspace",
        component: Workspace,
      },
      {
        path: "/usermanagement",
        name: "UserManagement",
        component: UserManagement,
      },
      {
        path: "/workorder",
        name: "Workorder",
        component: Workorder,
      },
      {
        path: "/keluhan",
        name: "Keluhan",
        component: Keluhan,
      },

      {
        path: "/customer",
        name: "Customer",
        component: Customer,
      },

      {
        path: "/survey",
        name: "Survey",
        component: Survey,
      },
      {
        path: "/institusi",
        name: "Institusi",
        component: Institusi,
      },
      {
        path: "/subinstitusi",
        name: "SubInstitusi",
        component: SubsInstitusi,
      },
      {
        path: "/period",
        name: "Period",
        component: Period,
      },
      {
        path: "/subperiod",
        name: "SubPeriod",
        component: SubPeriod,
      },
      {
        path: "/reporting",
        name: "Reporting",
        component: Reporting,
      },

      {
        path: "/ratecomment",
        name: "ratecomment",
        component: RateComment,
      },
      {
        path: "/konfirmasiworkorder",
        name: "Konfirmasiworkorder",
        component: Konfirmasiworkorder,
      },
      {
        path: "/detailkeluhan",
        name: "Detailkeluhan",
        component: Detailkeluhan,
      },
    ],
  },
];

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes
})

export default router