<template>
  <v-dialog v-model="dialog" content-class="alert-success">
    <v-card>
      <v-card-text>
        <v-container>
          <v-row align="center" justify="center">
            <v-col cols="12 pa-12 d-flex justify-center text-center ">
              <div>
                <v-img
                  width="80px"
                  :src="
                    require(type == 'Congratulations'
                      ? '@/static/images/check.png'
                      : '@/static/images/close.png')
                  "
                />
              </div>
            </v-col>
            <v-spacer />
          </v-row>
        </v-container>
        <v-container>
          <v-row class="d-flex align-center text-center">
            <v-col>
              <div>
                <div>
                  <h1 class="h6 text--white" color="text-grey darken-4">
                    {{
                      type == "Congratulations"
                        ? "Congratulations "
                        : "Failed !"
                    }}
                  </h1>
                </div>
              </div>
            </v-col>
          </v-row>
          <v-row align="center" justify="center">
            <v-col cols="12 pa-1 d-flex justify-center text-center ">
              <div>
                <div>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn @click="action" depressed color="primary">OK</v-btn>
                  </v-card-actions>
                  <v-spacer></v-spacer>
                </div>
              </div>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>
<script>
export default {
  props: ["alert", "type"],
  data() {
    return {
      dialog: false,
      resolve: null,
      reject: null,
    };
  },

  watch: {
    alert(val) {
      if (this.alert == true) {
        this.dialog = val;
      }
    },
  },
  methods: {
    action() {
      this.dialog = false;
      this.$emit("close", this.type);
    },
  },
};
</script>
<style  scoped>
/* .rounded-card {
  border-radius: 16px;
} */
</style>