<template>
  <div>
    <v-row class="justify-center text-center">
      <v-col cols="7">
        <v-dialog
          v-model="dialog"
          persistent
          content-class="detail-dialog"
          scrollable
        >
          <v-card class="rounded-card">
            <v-card-title class="toolbar-form">
              <v-spacer></v-spacer>
              Detail Complaint
              <v-spacer></v-spacer>

              <v-btn icon @click="close()">
                <v-icon> mdi-close </v-icon>
              </v-btn>
            </v-card-title>

            <v-spacer />
            <v-divider></v-divider>
            <!-- </v-row> -->
            <v-subheader class="font-weight-bold">DETAIL</v-subheader>
            <v-card-text>
              <div>
                <v-row>
                  <v-col cols="12" md="4" sm="12">
                    <h3 class="subtitle-1">Title</h3>
                    <p class="font-italic">Judul</p>
                  </v-col>
                  <v-col cols="12" md="8" sm="12">
                    <h3 class="h3-props">
                      {{ propsdetail.title }}
                    </h3>
                  </v-col>
                </v-row>

                <v-row>
                  <v-col cols="12" md="4" sm="12">
                    <h3 class="subtitle-1">Customer Name</h3>
                    <p class="font-italic">Nama Pelanggan</p>
                  </v-col>
                  <v-col cols="12" md="8" sm="12">
                    <h3 class="h3-props">
                      {{ propsdetail.customer.name }}
                    </h3>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" md="4" sm="12">
                    <h3 class="subtitle-1">Date</h3>
                    <p class="font-italic">Tanggal</p>
                  </v-col>
                  <v-col cols="12" md="8" sm="12">
                    <h3 class="h3-props">{{ propsdetail.date }}</h3>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" md="4" sm="12">
                    <h3 class="subtitle-1">Complaint Detail</h3>
                    <p class="font-italic">Detail Keluhan</p>
                  </v-col>
                  <v-col cols="12" md="8" sm="12">
                    <h3 class="h3-props">
                      {{ propsdetail.perbaikan }}
                    </h3>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" md="4" sm="12">
                    <h3 class="subtitle-1">Problem Analysis</h3>
                    <p class="font-italic">Analisa Masalah</p>
                  </v-col>
                  <v-col cols="12" md="8" sm="12">
                    <h3 class="h3-props">
                      {{ propsdetail.analisa_masalah }}
                    </h3>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" md="4" sm="12">
                    <h3 class="subtitle-1">Correction</h3>
                    <p class="font-italic">Perbaikan</p>
                  </v-col>
                  <v-col cols="12" md="8" sm="12">
                    <h3 class="h3-props">
                      {{ propsdetail.keluhan }}
                    </h3>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" md="4" sm="12">
                    <h3 class="subtitle-1">Root Cause</h3>
                    <p class="font-italic">Akar Masalah</p>
                  </v-col>
                  <v-col cols="12" md="8" sm="12">
                    <h3 class="h3-props">
                      {{ propsdetail.root_cause }}
                    </h3>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" md="4" sm="12">
                    <h3 class="subtitle-1">Corrective Action</h3>
                    <p class="font-italic">Tindakan Korektif</p>
                  </v-col>
                  <v-col cols="12" md="8" sm="12">
                    <h3 class="h3-props">
                      {{ propsdetail.corrective_action }}
                    </h3>
                  </v-col>
                </v-row>
                <v-row v-if="propsdetail.status == 'DECLINE'">
                  <v-col cols="12" md="4" sm="12">
                    <h3 class="subtitle-1">Decline Description</h3>
                    <p class="font-italic">Keterangan Penolakan</p>
                  </v-col>
                  <v-col cols="12" md="8" sm="12">
                    <h3 class="h3-props">
                      {{ propsdetail.ket_decline }}
                    </h3>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" md="4" sm="12" class="">
                    <h3 class="subtitle-1">Status</h3>
                    <p class="font-italic">Status</p>
                  </v-col>
                  <v-col cols="12" md="5" sm="12">
                    <h3 class="subtitle-1">
                      <v-select
                        dense
                        v-model="editedItem.status"
                        :items="statusitem"
                        item-value="abbr"
                        persistent-hint
                        outlined
                        rounded
                      >
                      </v-select>
                    </h3>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" md="4" sm="12" class="align-center">
                    <h3 class="subtitle-1">ATTACHMENT</h3>
                    <p class="font-italic">Lampiran</p>
                  </v-col>

                  <v-row v-if="editedItem.file_customer.length > 0">
                    <v-col
                      cols="12"
                      md="5"
                      sm="12"
                      class="pl-0"
                      v-for="(image, i) in editedItem.file_customer"
                      :key="i"
                      :style="i < 2 ? '' : 'display:none !important'"
                    >
                      <v-hover v-slot="{ hover }">
                        <v-card :elevation="hover ? 12 : 2">
                          <v-img
                            :src="'/images/fileKeluhan/' + image"
                            max-height="200"
                            max-width="200"
                            contain
                          >
                            <v-row class="flex-column" justify="space-between">
                              <div class="align-self-center mt-6">
                                <v-btn
                                  :class="{ 'show-btns': hover }"
                                  :color="transparent"
                                  icon
                                  @click="ShowImage(editedItem.file_customer)"
                                >
                                  <v-icon :class="{ 'white--text': hover }">
                                    mdi-image-search
                                  </v-icon>
                                </v-btn>
                              </div>
                            </v-row>
                          </v-img>
                        </v-card>
                      </v-hover>
                    </v-col>
                    <v-col
                      cols="4"
                      class="pl-0 pr-1 col col-2"
                      v-if="editedItem.file_customer.length > 2"
                    >
                      <v-card>
                        <v-img
                          :src="'/na-image/' + editedItem.file_customer[2]"
                          max-height="200"
                          max-width="200"
                          contain
                        >
                          <v-row class="flex-column" justify="space-between">
                            <div class="align-self-center mt-6">
                              <v-btn
                                class="show-img red--text"
                                :color="transparent"
                                small
                                @click="ShowImage(editedItem.file_customer)"
                              >
                                <span>+</span
                                >{{ editedItem.file_customer.length - 2 }}
                              </v-btn>
                            </div>
                          </v-row>
                        </v-img>
                      </v-card>
                    </v-col>
                  </v-row>
                </v-row>
              </div>

              <get-link
                v-if="showlink"
                :propslink="editedItem"
                @close="dialogGen = false"
                :dialog="dialogGen"
              />
            </v-card-text>
            <v-divider></v-divider>
            <div class="pt-4"></div>
            <v-card-actions class="justify-center">
              <v-btn
                ref="modal-save"
                id="modal-save"
                color="primary"
                rounded
                outlined
                @click="generate"
              >
                Generate Link
              </v-btn>

              <v-btn
                class="btn-update"
                ref="modal-save"
                id="modal-save"
                color="primary"
                rounded
                @click="ubahstatus"
              >
                Update
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <DialogOpenImage
          :dialog="showImage"
          :data="dataImage"
          @close-dialog="showImage = false"
        />
      </v-col>
    </v-row>
  </div>
</template>
                
<script>
import { mapActions, mapGetters } from "vuex";
import GetLink from "./popup/GetLink.vue";
import DialogOpenImage from "./DialogOpenImage.vue";
export default {
  props: ["propsdetail"],
  components: { GetLink, DialogOpenImage },
  data() {
    return {
      transparent: "",
      showlink: false,
      showImage: false,
      dataImage: [],
      selectedFiles: [],
      selectedImages: [],
      isSelecting: false,
      statusitem: [
        { text: "NEW", value: "NEW" },
        { text: "CONFIRMED", value: "CONFIRMED", disabled: true },
        { text: "In PROGRESS", value: "In PROGRESS" },
        { text: "DONE", value: "DONE" },
        { text: "CLOSED", value: "CLOSED" },
        { text: "DECLINE", value: "DECLINE" },
      ],

      marker: true,
      dialog: true,
      resolve: null,
      showlink: true,
      reject: null,
      link: "",
      data: "",
      date: "",
      editedItem: {
        title: "",
        status: "",
        link: "",
        perbaikan: "",
        file_customer: "",
        download: "",
        ket_decline: "",
      },
      dialogGen: false,
    };
  },

  mounted() {
    this.editedItem.status = this.propsdetail.status;
    this.editedItem.perbaikan = this.propsdetail.perbaikan;
    this.editedItem.ket_decline = this.propsdetail.ket_decline;
  },

  computed: {
    // buttonText() {
    //   return this.selectedFile
    //     ? this.selectedFile.name
    //     : this.defaultButtonText;
    // },
  },
  async created() {
    await this.KeluhanById({
      id: this.propsdetail.id_keluhan,
      token: localStorage.getItem("token-user"),
    }).then((response) => {
      this.editedItem.file_customer = response.data.File_Customer;
    });
  },
  methods: {
    ...mapActions("keluhan", [
      "fetchKeluhan",
      "KeluhanById",
      "updateStatus",
      "Generated",
    ]),
    close() {
      // alert("close");
      let data = {
        type: "close",
      };
      this.$emit("close", data);
    },

    onButtonClick() {
      this.isSelecting = true;
      window.addEventListener(
        "focus",
        () => {
          this.isSelecting = false;
        },
        { once: true }
      );

      this.$refs.uploader.click();
    },

    onFileChanged(e) {
      this.selectedFiles = e.target.files;

      for (let index = 0; index < this.selectedFiles.length; index++) {
        const element = this.selectedFiles[index];
        this.selectedImages.push(URL.createObjectURL(element));
      }
    },

    ubahstatus() {
      //inisiasi formdata
      var formData = new FormData();
      formData.append(`id_keluhan`, this.propsdetail.id_keluhan);
      formData.append(`status`, this.editedItem.status);
      formData.append(`perbaikan`, this.propsdetail.perbaikan);
      formData.append(`ket_decline`, this.propsdetail.ket_decline);
      formData.append(`files`, this.selectedImages);

      this.updateStatus({
        formData: formData,
      })

        .then((response) => {
          if (response.status == 200) {
            let data = {
              type: "save",
              status: true,
            };
            this.$emit("close", data);
          } else {
            let data = {
              type: "save",
              status: false,
            };
            this.$emit("close", data);
          }
        })
        .catch((err) => {
          let data = {
            type: "save",
            status: false,
          };
          this.$emit("close", data);
        });
    },

    async generate() {
      const iduser = localStorage.getItem("id_user");
      let res = await this.Generated({
        idcus: this.propsdetail.customer.id_customer,
        idkeluhan: this.propsdetail.id_keluhan,
        iduser: iduser,
      });
      this.editedItem.link = `https://ebcrm.blackeye.id/#/konfirmasi/${res.data.Link}`;
      this.showlink = true;
      this.dialogGen = true;
    },
    getColor(propsdetail) {
      if (propsdetail.status == "Done") return "green";
      else if (propsdetail.status == "progress") return "orange";
      else return "red";
    },
    ShowImage(e) {
      this.showImage = true;
      this.dataImage = e;
    },
  },
};
</script>
<style  scoped>
.rounded-card {
  border-radius: 30px;
}
.select {
  background: orange;
}
</style>