import axios from "axios";

const token = sessionStorage.getItem('token-user')

const state = {

    subperiod: []

}

const getters = {

    subperiodlist: state => state.subperiod,
}
const actions = {

    // allSubPeriod
    async fetchSubPeriod({ commit }) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get("/login-crm/AllSubPeriod", {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("setAllSubPeriod", response.data)
    },


    //getSubPeriodByID
    async SubPeriodById({ commit }, id_subperiod) {
        const response = await axios.get(`/login-crm/SubPeriodById/${id_subperiod}`, {
            headers: {
                Authorization: 'Bearer ' + token
            }
        });
        return response;

    },

    // addsubperiod
    async addSubPeriod({ commit }, subperiod) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.post("/login-crm/addSubPeriod  ", subperiod, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });

        commit("addSubPeriod", response)
        return response;
    },

    // updatesubperiod
    async updateSubPeriod({ commit }, data) {
        return axios.put(`/login-crm/updateSubPeriod?id_subperiod=${data.id_subperiod}`, data, {
            headers: {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
    },

    // DeleteIdPeriode
    async deleteSubPeriod({ commit }, id_subperiod) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.delete(
          `/login-crm/deleteSubPeriod?id_subperiod=${id_subperiod}`,
          {
            headers: {
              Authorization: "Bearer " + token, //the token is a variable which holds the token
            },
          }
        );
        commit("deleteSubPeriodd", id_subperiod)
        return response;
    },



};

const mutations = {
    //muttations add data

    addSubPeriod: (state, subperiod) => state.subperiod = subperiod,


    //muttations all data

    setAllSubPeriod: (state, subperiod) => (state.subperiod = subperiod),


    // deleteSubPeriodd: (state, id_subperiod) => {
    //     state.subperiod.filter(subperiod => subperiod.id_subperiod == id_subperiod),
    //         state.subperiod.splice(subperiod => subperiod.id_subperiod, 1)

    // },




}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}