<template>
  <div>
    <v-row class="ml-4 my-4 pr-10">
      <v-col cols="12">
        <v-breadcrumbs
          style="padding-left: unset;margin-left: 10px;}"
          divider=">"
        ></v-breadcrumbs>
      </v-col>
      <v-row class="justify-center">
        <h4>NO DATA</h4>
      </v-row> 
      <v-card  elevation="2">
        <!-- <highcharts v-if="!loading" :options="dataChart"></highcharts> -->
      </v-card>
    </v-row>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
import { Chart } from "highcharts-vue";
import Highcharts from "highcharts";
export default {
  components: {
    highcharts: Chart,
  },
  data: () => ({
    content: false,
    card1: 0,
    titles: "Dashboard",
    chart: "",
    loading: true,
    datarespon: [],
    dataChart: null,
  }),

  computed: {
    ...mapGetters("survey", ["chartsurvey"]),
  },

  async created() {
    this.chartpie();
  },
  methods: {
    ...mapActions("survey", ["surveyDashboardS"]),

    async chartpie() {
      this.loading = true;
      await this.surveyDashboardS();
      let a = this.chartsurvey;
      this.datarespon = [
        {
          name: "Filled",
          y: a.surveyFilled,
        
        },
        {
          name: "UnFilled",
          y: a.surveyUnfilled,
         
        },
       
      ];

      this.setChart();
      this.loading = false;
    },

    setChart() {
      this.dataChart = {
        chart: {
          plotBackgroundColor: null,
          plotBorderWidth: null,
          plotShadow: false,
          type: "pie",
        },
        title: {
          text: "SURVEY",
        },
        tooltip: {
          pointFormat: "{series.name}: <b>{point.percentage:.1f}%</b>",
        },
        accessibility: {
          point: {
            valueSuffix: "%",
          },
        },
        plotOptions: {
          pie: {
            allowPointSelect: true,
            cursor: "pointer",
            dataLabels: {
              enabled: true,
              format: "<b>{point.name}</b>: {point.percentage:.1f} %",
            },
          },
        },
        series: [
          {
            name: "Survey",
            colorByPoint: true,
            data: this.datarespon,
          },
        ],
      };
    },
  },
};
</script>
<style>
.rounded-card {
  border-radius: 30px;
}

.shadow {
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 8px !important;
}
</style>
