                      @click="closeDelete"
                      depressed
                      >No</v-btn
                    >
                    <v-btn @click="logout()" depressed color="primary"
                      >Yes</v-btn
                    >
                  </v-card-actions>
                  <v-spacer></v-spacer>
                </div>
