    </v-card>
  </v-dialog>
</template>
<script>
export default {
  props: ["data", "dialog"],
  data: () => ({}),

  mounted() {},
  methods: {
    logout() {
       sessionStorage.clear();
   
      localStorage.clear();
      this.$router.push("Login");
      //   this.$emit("clear", data);
    },
    closeDelete() {
      this.$emit("dialogs", false);
    },
  },
};
</script>
