<template>
  <div>
    <v-row class="ml-4 my-4 pr-10">
      <v-breadcrumbs
        style="padding-left: unset;margin-left: 10px;}"
        divider=">"
        :items="items"
      ></v-breadcrumbs>
      <v-col cols="12">
        <div>
          <v-row class="align-bottom">
            <v-col cols="12" class="">
              <h1 class="Montserrat">
                {{ titles }}
              </h1>
            </v-col>
          </v-row>
          <v-row>
            <v-col class="pe-4 justify-end" cols="12" md="4" offset="6">
              <v-text-field
                prepend-inner-icon="mdi-magnify"
                label="Search here"
                filled
                rounded
                dense
                single-line
                hide-details
              ></v-text-field>
            </v-col>
            <v-col cols="12 pa-0 " md="2">
              <v-btn class="ma-2" rounded color="primary " @click="addPeriod()">
                Create SubPeriod</v-btn
              >
            </v-col>
          </v-row>
        </div>
      </v-col>

      <v-row>
        <v-col cols="12" class="mx-3 my-2">
          <v-card elevation="2" class="rounded-card pr-1" :loading="loading">
            <v-card-text>
              <v-data-table
                style="cursor: pointer"
                :headers="headers"
                :items="subperiodlist"
                :items-per-page="10"
                class="elevation-0"
              >
                <template v-slot:[`item.actions`]="{ item }">
                  <v-btn
                    fab
                    dense
                    class="mx-1"
                    dark
                    x-small
                    color="primary"
                    @click="addPeriod(item)"
                  >
                    <v-icon x-small> mdi-pencil </v-icon>
                  </v-btn>
                  <v-btn
                    fab
                    dense
                    dark
                    x-small
                    color="red"
                    @click="deleteItem(item)"
                  >
                    <v-icon fab dark x-small> mdi-delete </v-icon>
                  </v-btn>
                </template>
              </v-data-table>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-row>

    <form-sub-period
      @success="actionSubPeriod"
      :propssubperiod="subperiod"
      :type="type"
      v-if="isShowModal"
    />
    <confirm
      @actionconfirm="deletesubPeriod"
      :id_subperiod="iddelete"
      v-if="confirmshow"
    />
    <sukses
      :alert="dialogsukses"
      :type="typealert"
      @close="dialogsukses = false"
    />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
import Sukses from "../components/alert/Sukses.vue";

import FormSubPeriod from "../components/form/FormSubPeriod.vue";
import FormPeriod from "../components/form/FormPeriod.vue";
// import TambahEdit from "../components/form/TambahEdit.vue";
// import FormWorkspace from "../components/form/FormWorkspace.vue";
import Confirm from "../components/popup/Confirm.vue";
// import TambahEdit from "../components/form/TambahEdit.vue";
export default {
  components: { Confirm, FormPeriod, FormSubPeriod, Sukses },
  data: () => ({
    loading: true,
    dialogsukses: false,
    typealert: "Congratulations",
    isShowModal: false,
    confirmshow: false,
    content: false,
    card1: 0,
    titles: "SubPeriod",
    iddelete: "",

    items: ["period"],
    headers: [
      // { text: "id_workspace", value: "id_workspace" },
      { text: "nama", value: "name_subperiod" },

      { text: "Actions", value: "actions", sortable: false },
    ],
    type: "",
    subperiod: {},
  }),
  computed: {
    ...mapGetters("subperiod", ["subperiodlist"]),
    formTitle() {
      return this.type === "Create Data" ? "Create Data" : "Edit Data";
    },
  },
  // watch: {
  //   workspacelist(val) {
  //     this.listworkspace = val;
  //   },
  // },
  async created() {
    // alert("ff");
    await this.fetchSubPeriod();
    this.loading = false;
    // this.listworkspace = this.workspacelist;
  },

  methods: {
    ...mapActions("subperiod", [
      "addSubPeriod",
      "deleteSubPeriod",
      "fetchSubPeriod",
    ]),

    addPeriod(item) {
      console.log("item addPeriod bro", item);
      if (item != undefined) {
        this.subperiod = item;
        this.type = "Edit Data";
        this.isShowModal = !this.isShowModal;
      } else {
        this.isShowModal = !this.isShowModal;
        this.type = "Create Data";
      }
    },
    deleteItem(item) {
      console.log("item", item);
      this.iddelete = item.id_subperiod;
      console.log("iddelete", this.iddelete);
      this.confirmshow = true;
    },
    deletesubPeriod(data) {
      console.log("confim", data);
      if (data.id_subperiod !== "") {
        this.deleteSubPeriod(data.id_subperiod)
          .then((response) => {
            console.log("response keluhan", response);
            if (response.status == 200) {
              this.$emit("success");
              this.typealert = "Congratulations";
              this.dialogsukses = true;
              this.confirmshow = false;
              this.fetchSubPeriod();
            } else {
              this.typealert = "Failed !";
              this.dialogsukses = true;
              this.confirmshow = false;
            }
          })
          .catch((err) => {
            this.typealert = "Failed !";
            this.dialogsukses = true;
          });
      } else {
        this.confirmshow = false;
      }
    },
    async actionSubPeriod(data) {
      this.isShowModal = false;
      if (data.type == "create") {
        if (data.status == true) {
          this.dialogsukses = true;
          this.typealert = "Congratulations";
          await this.fetchSubPeriod();
        } else {
          this.dialogsukses = true;
          this.typealert = "Failed !";
        }
      } else {
        this.loading = false;
        setTimeout(() => {
          this.onfilter();
        }, 1000);
      }
    },
  },
};
</script>

<style  scoped>
.rounded-card {
  border-radius: 30px;
}
</style>