import axios from "axios";

const state = {
    reporting: [],
    reportingType: [],
    allbytype: [],
    logreport: []

}

const getters = {
    reportinglist: state => state.reporting,
    typereportlist: state => state.reportingType,
    bytypelist: state => state.allbytype,
    logIdReport: state => state.logreport

}
const actions = {

    // allReport
    async fetchreporting({ commit }) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get("/login-crm/AllReporting", {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("setAllreporting", response.data)
    },
    // allReportByType
    async fetchType({ commit }) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get("/login-crm/AllTypeReporting", {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("setAllType", response.data)
    },
    async ReportingActive({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = axios.get(`/login-crm//reportingActive/${data.link}`, {
            // headers: {
            //     Authorization: 'Bearer ' + token 
            // }
        })
        return response
            // commit("updateStatus", id_keluhan, perbaikan, status);
    },

    //getReportingByID
    async reportingById({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get(`/login-crm/reportingById/${data.id }`, {
            headers: {
                Authorization: 'Bearer ' + token
            }
        });
        return response;

    },
    //getReportingByType
    async reportingByType({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get(
          `/login-crm/ReportingByType?type=${data.type}&id_workspace=${data.id_workspace}&name=${data.name}&page=${data.page}&size=${data.size}&status=${data.status}`,
          {
            headers: {
              Authorization: "Bearer " + token,
            },
          }
        );
        // return response;
        commit("setAllReportByType", response.data)

    },
    // addreport
    async addReport({ commit }, reporting) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.post("/login-crm/addReporting", reporting, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            },

        });

        commit("addNewReport", response)
        return response
    },


    // updatereporting
    async updateReporting({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.put(`/login-crm/updateReporting`, data, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token

            }
        });
        return response;
    },
    // updateStatusReporting
    async updateStatusReporting({}, data) {
        const token = sessionStorage.getItem('token-user')
            // return axios.put(`/login-crm/updateStatus/${data.id_keluhan}?status=${data.status}&perbaikan=${data.perbaikan}`, data.formData, {
        return axios.put(`/login-crm/updateStatusReporting`, data.formData, {

            headers: {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        })


    },
    //updatekonfirmasi
    async updateKonfirmasi({}, data) {
        const token = sessionStorage.getItem('token-user')
        return axios.put(`/login-crm/updateStatusReporting`, data.formData, {
                headers: {
                    'Content-Type': 'application/json',

                }
            })
            // commit("updateStatus", id_keluhan, perbaikan, status);
    },
    async Generated({ commit }, data) {
        const token = sessionStorage.getItem('token-user');
        return new Promise((resolve, reject) => {
            axios.post(`/login-crm/generateLinkReporting?id_reporting=${data.idreporting}`, data, {
                headers: {
                    Authorization: 'Bearer ' + token //the token is a variable which holds the token
                }
            }).then((res) => {
                resolve(res)
            });
        })
    },

    // DeleteReporting
    async deleteReportingID({ commit }, id_reporting) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.delete(`/login-crm/deleteReporting/${id_reporting}`, {
                headers: {
                    Authorization: 'Bearer ' + token //the token is a variable which holds the token
                }
            })
            // commit("removeReporting", id_reporting)
        return response
    },
    async logByIdReport({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get(`/login-crm/findLogByIdReporting/${data.id}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        // commit("logIdReport", response.data)
        return response
            // console.log("get institusi by ID", response.data);
    },

};

const mutations = {
    //muttations add data

    addNewReport: (state, reporting) => state.reporting = reporting,

    //muttations all data

    setAllReportByType: (state, allbytype) => (state.allbytype = allbytype),
    setAllreporting: (state, reporting) => (state.reporting = reporting),
    setAllType: (state, reportingType) => (state.reportingType = reportingType),
    logIdReport: (state, logreport) => (state.logreport = logreport)

    // removeReporting: (state, id_reporting) => {
    //     state.keluhan.filter(reporting => reporting.id_reporting == id_reporting),
    //         state.keluhan.splice(reporting => reporting.id_reporting, 1)

    // },




}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}