import axios from "axios";

const token = sessionStorage.getItem('token-user')

const state = {
    period: [],

}

const getters = {

    periodlist: state => state.period,

}
const actions = {
    // allPeriod
    async fetchPeriod({ commit }) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get("/login-crm/AllPeriod", {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("setAllPeriod", response.data)
    },

    //getPeriodByID
    async PeriodById({ commit }, id_period) {
        const response = await axios.get(`/login-crm/PeriodById/${id_period }`, {
            headers: {
                Authorization: 'Bearer ' + token
            }
        });
        return response;

    },

    // addperiod
    async addPeriod({ commit }, period) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.post("/login-crm/addPeriod  ", period, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });

        commit("addPeriod", response)
        return response;
    },


    // updateperiod
    async updatePeriod({ commit }, data) {
        return axios.put(`/login-crm/updatePeriod?id_period=${data.id_period}`, data, {
            headers: {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
    },

    // DeleteIdPeriode
    async deletePeriodID({ commit }, id_period) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.delete(
          `/login-crm/deletePeriod?id_period=${id_period}`,
          {
            headers: {
              Authorization: "Bearer " + token, //the token is a variable which holds the token
            },
          }
        );
        commit("deletePeriodd", id_period)
        return response;
    },



};

const mutations = {
    //muttations add data

    addPeriod: (state, period) => state.period = period,

    //muttations all data
    setAllPeriod: (state, period) => (state.period = period),



    deletePeriodd: (state, id_period) => {
        state.period.filter(period => period.id_period == id_period),
            state.period.splice(period => period.id_period, 1)

    },




}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}