<template>
  <div>
    <v-alert
      v-model="warning"
      text
      dismissible
      prominent
      type="error"
      icon="mdi-message-alert"
    >
      {{ text_alert }}
    </v-alert>
    <v-row class="mx-5 mt-4">
      <!-- <v-breadcrumbs :items="items" divider=">"></v-breadcrumbs> -->
      <v-col cols="12">
        <div>
          <v-row class=" justify-end">
            <v-col cols="12" md="7" class="">
              <h1 class="Montserrat">
                {{ titles }}
              </h1>
              <span>{{ kode_fr_keluhan }}</span>
            </v-col>

            <v-col class="pt-0 d-flex justify-end " cols="4" md="3">
            </v-col>
            <v-col
              v-model="logo"
              class="d-flex justify-space-around "
              cols="9"
              md="2"
              sm="5"
            >
              <v-img
                src="../static/images/iso-9001-01.svg"
                max-height="200"
                max-width="70"
                contain
              ></v-img>
              <v-img
                :src="'/images/fileKeluhan/' + logo"
                 max-height="200"
                max-width="120"
                contain
              ></v-img>
            </v-col>
          </v-row>

          <v-row class=" justify-end">
            <v-col class="pe-4 justify-end" cols="12" md="3">
              <v-text-field
                v-model="search"
                prepend-inner-icon="mdi-magnify"
                label="Customer"
                filled
                rounded
                dense
                single-line
                v-on:change="changeType()"
                @click:prepend-inner="changeType()"
                hide-details
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="2" class="btn-update d-flex justify-end">
              <v-btn rounded color="primary " @click="addkeluhan()" depressed   class="text-capitalize">
                <!-- <v-icon color="white" left> mdi-account-plus </v-icon> -->
                Create Complaint</v-btn
              >
            </v-col>
          </v-row>
        </div>
      </v-col>

      <v-row>
        <v-col cols="12" class="mx-3 my-2">
          <v-card class="shadow rounded-card pr-1" :loading="loading">
            <v-card-text>
              <v-data-table
                :headers="headers"
                :items="keluhanbyidworkspace.Keluhan"
                sort-by="customer.name"
                class="elevation-0"
                hide-default-footer
                :page.sync="page"
                :items-per-page="size"
                @page-count="pageCount = $event"
              >
                <template v-slot:[`item.status`]="{ item }">
                  <span :style="`color:${getColor(item.status)}`">
                    {{ item.status }}
                  </span>
                </template>
                <template v-slot:[`item.title`]="{ item }">
                  <a
                    :style="
                      $vuetify.theme.dark
                        ? 'cursor: pointer; color: #0072B6'
                        : 'cursor: pointer; color: blue'
                    "
                    class="Subtitle 2 font-weight-medium"
                    @click="ClickDetail(item)"
                    >{{ item.title }}</a
                  >
                  <!-- class="text-decoration-underline" -->
                </template>

                <template v-slot:[`item.actions`]="{ item }">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        fab
                        dense
                        dark
                        class="mx-1 my-2"
                        x-small
                        color="primary"
                        @click="addkeluhan(item)"
                        v-bind="attrs"
                        v-on="on"
                      >
                        <v-icon x-small> mdi-pencil </v-icon>
                      </v-btn>
                    </template>
                    <span>Edit</span>
                  </v-tooltip>
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        depressed
                        fab
                        dark
                        x-small
                        color="red"
                        @click="deleteItem(item)"
                        v-bind="attrs"
                        v-on="on"
                      >
                        <v-icon fab dark x-small> mdi-delete </v-icon>
                      </v-btn>
                    </template>
                    <span>Remove</span>
                  </v-tooltip>
                </template>
              </v-data-table>

              <v-row class="justify-end">
                <v-col cols="12" md="4" class="justify-center">
                  <v-pagination
                    v-model="page"
                    class="my-4"
                    :total-visible="13"
                    v-on:change="changeType()"
                    :length="totaldata"
                  >
                  </v-pagination>
                </v-col>
                <v-col cols="12" md="1" class="select">
                  <v-select
                    label="Row per page"
                    :value="size"
                    type="number"
                    :items="row"
                    dense
                    outlined
                    v-on:change="changeType()"
                    @input="size = parseInt($event, 10)"
                  ></v-select>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-row>
    <form-keluhan
      :type="type"
      :dialog="isShowModal"
      :item="keluhan"
      @success="actionfrom"
    />
    <confirm
      @actionconfirm="deletekeluhan"
      :id_keluhan="iddelete"
      v-if="confirmshow"
    />

    <sukses
      :alert="dialogsukses"
      :type="typealert"
      @close="dialogsukses = false"
    />
    <Detailkeluhan
      v-if="detailshow"
      :propsdetail="rowClick"
      @close="actionKeluhan"
    />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
// import TambahEdit from "../components/form/TambahEdit.vue";
import Detailkeluhan from "./Detailkeluhan.vue";
import FormKeluhan from "../components/form/FormKeluhan.vue";
import Sukses from "./alert/Sukses.vue";
import Confirm from "../components/popup/Confirm.vue";
// import ConfirmKeluhan from "../components/popup/ConfirmKeluhan.vue";
// import TambahEdit from "../components/form/TambahEdit.vue";
export default {
  components: { FormKeluhan, Sukses, Detailkeluhan, Confirm },
  data: () => ({
    text_alert: "",
    logo: "",
    loading: true,
    warning: false,
    row: [5, 10, 15, 20, 25],
    page: 1,
    size: 5,
    pageCount: 0,
    totaldata: 0,
    search: "",
    detailshow: false,
    dialogsukses: false,
    typealert: "Congratulations",
    isShowModal: false,
    confirmshow: false,
    content: false,
    card1: 0,
    titles: "Complaint",
    iddelete: "",
    iddelwork: "",
    id_workspace: [],

    workspace: "",
    kode_fr_keluhan: "",
    items: ["customer"],
    headers: [
      { text: "TITTLE", value: "title" },
      { text: "CUSTOMER", value: "customer.name" },

      { text: "COMPLAINT", value: "perbaikan" },

      { text: "DATE", value: "date" },
      { text: "STATUS", value: "status" },
      { text: "ACTION", value: "actions", sortable: false, width: "10%" },
    ],
    items: [
      {
        text: "Complaint",
        disabled: false,
        href: "breadcrumbs_dashboard",
      },
    ],
    type: "",
    keluhan: {},
  }),

  computed: {
    ...mapGetters("keluhan", ["keluhanbyidworkspace"]),
    ...mapState("keluhan", ["keluhanlistbyidkeluhan"]),

    formTitle() {
      return this.type === "Create Complaint"
        ? "Create Complaint"
        : "Edit Complaint";
    },
  },
  watch: {
    page() {
      this.datakeluhan();
    },
    search() {
      this.datakeluhan();
    },
  },
  mounted() {},

  async created() {
    await this.datakeluhan();
    this.workspace = localStorage.getItem("myworkspace");
    this.kode_fr_keluhan = localStorage.getItem("kode_fr_keluhan");
  },
  methods: {
    ...mapActions("keluhan", [
      "deleteKeluhanID",
      "fetchKeluhan",
      "getKeluhanById",
    ]),
    changeType() {
      this.page = 1;
      this.datakeluhan();
      // this.SurveyFilter(this.typesurvey);
    },

    ClickDetail(item) {
      this.detailshow = true;
      this.rowClick = item;
    },
    // rowClickss(item, row) {

    //   this.detailshow = true;
    //   this.rowClick = item;
    // },
    getColor(status) {
      if (status == "NEW") return "Primary";
      else if (status == "In PROGRESS") return "orange";
      else if (status == "DONE") return "green";
      else if (status == "CONFIRMED") return "cyan";
      else if (status == "CLOSED") return "purple";
      else if (status == "DECLINE") return "red";
    },

    addkeluhan(item) {
      if (item != undefined) {
        this.keluhan = item;
        this.type = "Edit Complaint";
        this.isShowModal = !this.isShowModal;
      } else {
        this.isShowModal = true;
        this.type = "Create Complaint";
      }
    },
    deleteItem(item) {
      this.iddelete = item.id_keluhan;

      this.confirmshow = true;
    },
    async datakeluhan() {
      this.loading = true;
      const prm = {
        name: this.search,
        size: this.size,
        page: this.page,
        id_workspace: localStorage.getItem("myworkspace"),
      };
      await this.getKeluhanById(prm);
      this.loading = false;

      this.logo = this.keluhanlistbyidkeluhan.Logo;
      this.kode_fr_keluhan = this.keluhanlistbyidkeluhan.Keluhan.kode_fr_keluhan;
      this.totaldata = this.keluhanlistbyidkeluhan.TotalPages;
    },

    deletekeluhan(data) {
      if (data.id_keluhan !== "") {
        this.deleteKeluhanID(data.id_keluhan)
          .then((response) => {
            if (response.status == 200) {
              this.$emit("success");
              this.typealert = "Congratulations";
              this.dialogsukses = true;
              this.confirmshow = false;
              this.datakeluhan();
            } else {
              this.typealert = "Failed !";
              this.dialogsukses = true;
              this.confirmshow = false;
            }
          })
          .catch((err) => {
            this.text_alert = err.response.data;
            this.warning = true;
          });
        this.confirmshow = false;
      } else {
        this.confirmshow = false;
      }
    },
    async actionfrom(data) {
      this.isShowModal = false;
      if (data.type == "create") {
        if (data.status == true) {
          this.dialogsukses = true;
          this.typealert = "Congratulations";
          await this.datakeluhan();
        } else {
          this.dialogsukses = true;
          this.typealert = "Failed !";
        }
      } else {
        setTimeout(() => {
          this.datakeluhan();
        }, 1000);
      }
    },

    actionKeluhan(data) {
      // alert("aaaa");
      this.detailshow = false;
      this.rowClick = [];
      if (data.type == "save") {
        if (data.status == true) {
          this.typealert = "Congratulations";
          this.dialogsukses = true;
          this.datakeluhan();
        } else {
          this.typealert = "Failed !";
          this.dialogsukses = true;
        }
      } else {
        setTimeout(() => {
          this.datakeluhan();
        }, 1000);
      }
    },
  },
};
</script>
<style scoped>
.rounded-card {
  border-radius: 16px !important;
}
.select {
  padding-top: 30px;
}
</style>
