import axios from "axios";
const token = sessionStorage.getItem('token-user')
const state = {
    workorder: [],
    respon: {},
    byid_keluhan: [],
    listbyidworkorder: [],
    listworkbyworkpace: []
}
const getters = {
    workorderlist: state => state.workorder,
    workorderbyid: state => state.listbyidworkorder,
    bytypeidkeluhan: state => state.byid_keluhan,
    workorderbyidworkspace: state => state.listworkbyworkpace
}

const actions = {
    async fetchWorkorder({ commit }) {
        const token = sessionStorage.getItem('token-user')
        const myworkspace = localStorage.getItem('id_user')
        const response = await axios.get("/login-crm/workorder", {
          headers: {
            Authorization: "Bearer " + token, //the token is a variable which holds the token
          },
        });
        commit("setWorkorder", response.data)
            // console.log("get workorder", response.data);s
    },
    async WorkorderById({ commit }, id_workorder) {
        const token = sessionStorage.getItem('token-user')
            // const myworkspace = localStorage.getItem('id_user')
        const response = await axios.get(`/login-crm/WorkorderById/${id_workorder}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        // commit("workslistbyidworkorder", response.data)
        return response;
        console.log("get workorder by ID", response.data);
    },
    //WorderByIdKeluhan
    async WorderByIdKeluhan({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.get(
          `/login-crm/WorkorderByIdkeluhan?id_keluhan=${data.id_keluhan}&id_workspace=${data.id_workspace}&category=${data.category}&page=${data.page}&size=${data.size}`,
          {
            headers: {
              Authorization: "Bearer " + token,
            },
          }
        );
        // return response;

        commit("setAllWOByType", response.data)
        console.log("get workorder by ID keluhan", response);

    },

    async WorkorderByIdWorkspace({ commit }) {
        const token = sessionStorage.getItem('token-user')
        const myworkspace = localStorage.getItem('myworkspace')
        const response = await axios.get(`/login-crm/WorkorderByIdWorkspace/${myworkspace}`, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        commit("workslistbyidworkspace", response.data)
            // console.log("get workorder by ID", response.data);
    },
    //addworkorder
    async addWorkorder({ commit }, workorder) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.post("/login-crm/addWorkorder", workorder, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        // commit("addNewWorkorder", response.data)
        console.log("add workorder", response.data)
        return response;
    },
    //deleteworkorder
    async deleteWorkorder({ commit }, id_workorder) {
        const response = await axios.delete(`/login-crm/deleteWorkorder/${id_workorder}`, {
                headers: {
                    Authorization: 'Bearer ' + token //the token is a variable which holds the token
                }
            })
            // commit("removeWorkorder", id_workorder)
        console.log("workorder delete", response.data);
        return response;
    },
    //updateworkorderbyidworkorder
    async updateWorkorderByID({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.put(`/login-crm/updateWorkorder`, data, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        return response;
    },
    //updatestatusworkorder
    async updateStatusWorkorderByID({ commit }, data) {
        const token = sessionStorage.getItem('token-user')
        const response = await axios.put(`/login-crm/updateStatusWorkorder/${data.id_workorder}/{keterangan}?keterangan=${data.id_workorder}&status=${data.id_workorder}`, data, {
            headers: {
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        });
        return response;
    },
    async updateStatusWorkorder({}, data) {
        const token = sessionStorage.getItem('token-user')
            // return axios.put(`/login-crm/updateStatusWorkorder/${data.id_workorder}?status=${data.status}`, data, {
            //     headers: {
            //         'Content-Type': 'application/json',
            //         Authorization: 'Bearer ' + token //the token is a variable which holds the token
            //     }
            // })
        return axios.put(`/login-crm/updateStatusWorkorder`, data.formData, {
            headers: {
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + token //the token is a variable which holds the token
            }
        })

        // commit("updateStatus", id_keluhan, perbaikan, status);
    }


};

const mutations = {

    setWorkorder: (state, workorder) => (state.workorder = workorder),
    // workslistbyidworkspace: (state, workslistbyidworkspace) => (state.listbyidworkspace = workslistbyidworkspace),
    workslistbyidworkorder: (state, listbyidworkorder) => (state.listbyidworkorder = listbyidworkorder),
    workslistbyidworkspace: (state, listworkbyworkpace) => state.listworkbyworkpace = listworkbyworkpace,
    addNewWorkorder: (state, workorder) => state.workorder = workorder,
    // removeWorkorder: (state, id_workorder) => {
    //     state.workorder.filter(workorder => workorder.id_workorder == id_workorder),
    //         state.workorder.splice(workorder => workorder.id_workorder, 1)

    // },
    setAllWOByType: (state, all) => (state.byid_keluhan = all),
    updateKeluhan: (state, workorder) => (state.workorder = (workorder => workorder)),
    updateStatus: (state, workorder) => (state.workorder = (workorder => workorder))

}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}